import { currencyFormat } from '@repo/utils/common/currency'
import { memo } from 'react'

interface ResultProps extends React.HTMLAttributes<HTMLDivElement> {
  result: number
  currency: string | undefined
  status: string // TODO: This type will be improved in the future, currently it is "finished" or "cancelled"
}

function GameHistoryResult({ result, currency, status, ...restDivProps }: ResultProps) {
  const color = status === 'finished' && result > 0 ? '#4EB472' : '#b0b0b0'

  return (
    <div
      style={{ color }}
      {...restDivProps}
    >
      {currencyFormat(result, currency ?? 'USD')}
    </div>
  )
}

export default memo(GameHistoryResult)
