import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

interface DataStoreState {
  payoutLimits: {
    betType: string
    betLimits: string
    payouts: string
  }[]
  titleList: string[]
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useDataStore`,
  store: 'DataStore',
  serialize: {
    options: true,
  },
}

export const useDataStore = create<DataStoreState>()(
  devtools(
    (_) => ({
      payoutLimits: [],
      titleList: [],
    }),
    DEVTOOLS_CONFIG
  )
)
