import { GameResult, Payload } from '@ikigaians/house'
import { create, StoreApi, UseBoundStore } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'

// Type for the state
// * T: roulette=string, sicBo=number[], baccarat=string[]
// * Roulette the result is a string because US Roulette will have "00"
export type IWinNumber<T> = {
  history: T[]
  recentHistoryDetails: { result: T; roundId: string }[]
  lastWinCache: number
  showWinnerList: boolean
  winnerList: { nickname?: string; amount: number }[]
  initPrevRounds: (payload: Payload) => void
  add: (result: T, roundId: string) => void
  getDescHistory: () => T[]
  clearHistory: () => void
}

const historyCapacity = 500
const recentHistoryCapacity = 20

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useWinNumberStore`,
  store: 'WinNumberStore',
  serialize: {
    options: true,
  },
}

// Generic store creator function
function createWinNumberStore<T>() {
  return create(
    devtools(
      subscribeWithSelector<IWinNumber<T>>((set, get) => ({
        userWin: 0,
        history: [],
        recentHistoryDetails: [], // for locating wins in bet result
        lastWinCache: 0,
        showWinnerList: false,
        winnerList: [],
        initPrevRounds: (data: Payload) => {
          const { tableHistory } = data
          if (!tableHistory) return

          const recentHistoryDetails = tableHistory
            .filter((round) => round.result)
            .map((round) => ({
              result: Object.values(round.result as GameResult)[0] as T,
              roundId: round.roundId ?? '',
            }))
            .reverse()
            .slice(-historyCapacity)
          const history = recentHistoryDetails.map((round) => round.result)
          set({ history, recentHistoryDetails })
        },
        add: (result: T, roundId: string) => {
          set((s) => {
            const history = s.history.concat([result]).slice(-historyCapacity)
            const recentHistoryDetails = s.recentHistoryDetails.concat({ result, roundId }).slice(-recentHistoryCapacity)
            return { history, recentHistoryDetails }
          })
        },
        getDescHistory: () => get().history.slice().reverse(),
        clearHistory: () => {
          console.debug('history cleared')
          set({ history: [] })
        },
      })),
      DEVTOOLS_CONFIG
    )
  )
}

export const useWinNumberStore = createWinNumberStore<unknown>()

export const getWinNumberStore = <T>() => useWinNumberStore as UseBoundStore<StoreApi<IWinNumber<T>>>
