import { bezier } from './cubicBezier'

type KeyFrame = {
  sec: number
  value: number
}

type InterpolationFunc = (elapsed: number, keyFrames: KeyFrame[]) => number

export enum InterpolationMode {
  LINEAR = 'LINEAR',
  EASE_IN_OUT_CUBIC = 'EASE_IN_OUT_CUBIC',
  CAP_MORPH = 'CAP_MORPH',
  NUM_DESCEND = 'NUM_DESCEND',
  NUM_LANDING = 'NUM_LANDING',

  WHEEL_ROTATION = 'WHEEL_ROTATION',
  WHEEL_Y_POSITION = 'WHEEL_Y_POSITION',
  WIN_NUM_SCALE = 'WIN_NUM_SCALE',
  WIN_TEXT_SCALE = 'WIN_TEXT_SCALE',
  ROULETTE_RAISE_BALL_SCALE = 'ROULETTE_RAISE_BALL_SCALE',
  TITLE_EARN_POSITION_Y = 'TITLE_EARN_POSITION_Y',
}

const interpolatorMap: Record<InterpolationMode, (percent: number) => number> = {
  [InterpolationMode.LINEAR]: (percent) => percent,
  [InterpolationMode.EASE_IN_OUT_CUBIC]: bezier(0.94, 0, 0.68, 1),
  [InterpolationMode.CAP_MORPH]: bezier(0.6, 0, 0.16, 1),
  [InterpolationMode.NUM_DESCEND]: bezier(0.94, 0, 0.68, 1),
  [InterpolationMode.NUM_LANDING]: bezier(0.67, 0, 0.1, 1),

  [InterpolationMode.WHEEL_Y_POSITION]: bezier(0.3, 0, 0.09, 1),
  [InterpolationMode.WHEEL_ROTATION]: bezier(0.3, 0, 0.26, 1),
  [InterpolationMode.WIN_TEXT_SCALE]: bezier(0.35, 0, 0.13, 0.85),
  [InterpolationMode.WIN_NUM_SCALE]: bezier(0.54, 0.19, 0.17, 1),
  [InterpolationMode.ROULETTE_RAISE_BALL_SCALE]: bezier(0.5, 0, 0.05, 1),
  [InterpolationMode.TITLE_EARN_POSITION_Y]: bezier(0.88, 0, 0.31, 1),
}

/*
 * Opt for the mode for the transitional animation between key frames.
 */
const initInterpolationFunc =
  (mode: InterpolationMode): InterpolationFunc =>
  (elapsed: number, keyFrames: KeyFrame[]) => {
    if (!keyFrames.length) return NaN
    if (elapsed < keyFrames[0].sec) return keyFrames[0].value

    let start = 0
    let end = 0
    let prev = 0
    let percent = 0

    for (const keyFrame of keyFrames) {
      const { sec, value } = keyFrame
      if (elapsed < sec) {
        percent = (elapsed - prev) / (sec - prev)
        end = value
        break
      }
      prev = sec
      start = value
    }

    const interpolator = interpolatorMap[mode]
    percent = interpolator(percent)

    return start + (end - start) * percent
  }

export default initInterpolationFunc
