import { sleep } from '@repo/utils/common/common'
import { enableMapSet } from 'immer'
import { create } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'
import { immer } from 'zustand/middleware/immer'

enableMapSet()

export enum LoadingResourceEnum {
  PixiCore = 'Pixi Core',
  ConnectToRGS = 'Connect To RGS',
  SyncWallet = 'Sync Wallet',
  AppPromise = 'Lazy App Promise',
  i18n = 'i18n',
}

export enum LoadingStageEnum {
  Init = 0, // Loading minimal resources
  Idle = 1, // Start animating spinner
  Loading = 2, // Resources loading
  Loaded = 3, // Fade out loading spinner
  PostLoaded1 = 4, // Stretching the board
  PostLoaded2 = 5, // (Internal) Element swapping
  PostLoaded3 = 6, // Dissolving text
  PostLoaded4 = 7, // Finishing and display all game components
}

export interface ILoadingStore {
  resources: Set<string>
  totalResources: number
  isWebP: boolean
  loadingStage: LoadingStageEnum
  isUpdatingStage: boolean
  isLoadingFinished: boolean

  addToResources: (key: string) => void
  decrementResources: (key: string) => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useLoadingStore`,
  store: 'LoadingStore',
  serialize: {
    options: true,
  },
}

export const useLoadingStore = create<ILoadingStore>()(
  devtools(
    subscribeWithSelector(
      immer((set, _get) => ({
        resources: new Set([LoadingResourceEnum.i18n]),
        totalResources: 0,
        isWebP: false,
        loadingStage: LoadingStageEnum.Init,
        isUpdatingStage: false,
        isLoadingFinished: false,

        addToResources: (key) => {
          set(
            (state) => {
              state.resources.add(key)
              state.totalResources = Math.max(state.totalResources, state.resources.size)
            },
            undefined,
            'addToResources'
          )
        },

        decrementResources: (key) => {
          set(
            (state) => {
              state.resources.delete(key)
            },
            undefined,
            'decrementResources'
          )
        },
      }))
    ),
    DEVTOOLS_CONFIG
  )
)

useLoadingStore.subscribe(
  (state) => state.loadingStage,
  async (loadingStage) => {
    if (loadingStage !== LoadingStageEnum.Loaded) return

    await sleep(1000)
    useLoadingStore.setState({ loadingStage: LoadingStageEnum.PostLoaded1 })

    await sleep(500)
    useLoadingStore.setState({ loadingStage: LoadingStageEnum.PostLoaded2 })

    await sleep(1500)
    useLoadingStore.setState({ loadingStage: LoadingStageEnum.PostLoaded3 })

    await sleep(3500)
    useLoadingStore.setState({
      loadingStage: LoadingStageEnum.PostLoaded4,
      isLoadingFinished: true,
    })
  }
)

// Set up subscription to watch for resource changes
useLoadingStore.subscribe(
  (state) => ({
    resources: state.resources,
    totalResources: state.totalResources,
    loadingStage: state.loadingStage,
    isUpdatingStage: state.isUpdatingStage,
  }),
  ({ resources, totalResources, loadingStage, isUpdatingStage }) => {
    // Stop progress monitoring once we reach Loaded stage - let the subscription handle stages 4-7
    if (loadingStage >= LoadingStageEnum.Loaded) return

    const progress = (totalResources - resources.size) / totalResources || 0

    if (loadingStage !== LoadingStageEnum.Idle && progress === 0) return

    // Find the next sequential stage based on progress
    let targetStage = LoadingStageEnum.Idle
    if (progress > 0) targetStage = LoadingStageEnum.Loading
    if (progress >= 1) targetStage = LoadingStageEnum.Loaded

    // Only proceed to the next stage sequentially (one step at a time)
    const nextStage = loadingStage + 1

    // Only update if we need to move to the next sequential stage and we're not already updating
    if (nextStage <= targetStage && nextStage !== loadingStage && !isUpdatingStage) {
      useLoadingStore.setState({ isUpdatingStage: true })

      const updateStage = async () => {
        // Wait 1000ms before updating to the next stage
        await new Promise((resolve) => setTimeout(resolve, 1000))
        // console.log('Updating loading stage:', nextStage, 'Progress:', progress)

        useLoadingStore.setState({
          loadingStage: nextStage,
          isUpdatingStage: false,
        })
      }

      updateStage()
    }
  }
)
