import { cn, isMobileDevice } from '@repo/utils/common/common'
import { HTMLAttributes, memo } from 'react'

interface IPopUpButton extends HTMLAttributes<HTMLDivElement> {
  disabled?: boolean
  cancel?: boolean
  onClick: () => void
}

function PopUpButton(props: IPopUpButton) {
  const { onClick, cancel, disabled } = props

  return (
    <button
      id="PopUpButton"
      onClick={onClick}
      className={cn(
        'px-10 py-4 text-[1rem] leading-[1.5rem] tracking-[-0.03rem]',
        { 'px-5 py-[0.625rem] text-[0.875rem] leading-[1.25rem] tracking-[-0.02625rem]': isMobileDevice, cancel, disabled },
        props.className
      )}
    >
      <div className="inner">{props.children}</div>
    </button>
  )
}

export default memo(PopUpButton)
