import { GameEvent } from '@ikigaians/connect'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { SESSION_ERROR_CODES } from '../constants'
import { SessionKickedEvent } from '../types'

export class SessionEventHandlers {
  static onSessionKicked(msg: SessionKickedEvent) {
    const ev = msg
    console.debug(GameEvent.SESSION_KICKED, ev)
    const code = ev.data?.kicked?.code

    useDebugStore
      .getState()
      .setMsg(
        `${GameEvent.SESSION_KICKED}: ${code === SESSION_ERROR_CODES.EXPIRED ? 'expired' : code === SESSION_ERROR_CODES.MULTI_SESSION ? 'multiSession' : ''} ${JSON.stringify(ev.data?.kicked, null, '\t')}`
      )

    if (code === SESSION_ERROR_CODES.EXPIRED) {
      useNetworkStore.setState({ isExpired: true })
    }

    if (code === SESSION_ERROR_CODES.MULTI_SESSION) {
      useNetworkStore.setState({ isMultiSession: true })
    }
  }
}
