type KeyFrame = {
  sec: number
  value: number
}

export function initParticleAlphaKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 20 * fps, value: 1 },
    { sec: 40 * fps, value: 1 },
    { sec: 60 * fps, value: 1 },
    { sec: 80 * fps, value: 0 },
  ]
}

export function initWheelYPositKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 30 * fps, value: 0 },
  ]
}

export function initWheelRotationKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: -0.8 },
    { sec: 30 * fps, value: 0 },
    { sec: 60 * fps, value: 0 },
    { sec: 90 * fps, value: 0.8 },
  ]
}

export function initTextWrapperScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 30 * fps, value: 1 },
  ]
}

export function initTitleCtnScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 18 * fps, value: 1.1 },
    { sec: 28 * fps, value: 0.95 },
  ]
}

export function initWinNumCtnScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 20 * fps, value: 1 },
  ]
}

export function initRouletteBallScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0.8 },
    { sec: 5 * fps, value: 0.8 },
    { sec: 20 * fps, value: 1 },
    { sec: 40 * fps, value: 0.8 },
  ]
}

export function initRaiseCtnScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0.95 },
    { sec: 12 * fps, value: 0.95 },
    { sec: 42 * fps, value: 1.02 },
  ]
}

export function initWinNumScaleKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0.85 },
    { sec: 102 * fps, value: 0.95 },
  ]
}

export function initFadeKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 20 * fps, value: 1 },
    { sec: 30 * fps, value: 1 },
    { sec: 60 * fps, value: 1 },
    { sec: 72 * fps, value: 0 },
  ]
}

export function initWheelYPositionKeyFrame(fps: number, factor: number, top: number, isMobile: boolean): KeyFrame[] {
  return [
    { sec: 0, value: isMobile ? 150 * factor + top : 207 * factor + top },
    { sec: 30 * fps, value: isMobile ? 34 * factor + top : 52 * factor + top },
    { sec: 60 * fps, value: isMobile ? 34 * factor + top : 52 * factor + top },
    { sec: 90 * fps, value: isMobile ? 150 * factor + top : 207 * factor + top },
  ]
}

export function initTitlePositionYKeyFrame(fps: number, factor: number, top: number, isMobile: boolean): KeyFrame[] {
  return [
    { sec: 0, value: isMobile ? 190 * factor + top : 376 * factor + top },
    { sec: 30 * fps, value: isMobile ? 190 * factor + top : 376 * factor + top },
    { sec: 57 * fps, value: isMobile ? 190 * factor + top : 376 * factor + top },
    { sec: 87 * fps, value: isMobile ? 418 * factor + top : 469 * factor + top },
  ]
}

export function initEarnPositionYKeyFrame(fps: number, factor: number, top: number, isMobile: boolean): KeyFrame[] {
  return [
    { sec: 0, value: isMobile ? 218 * factor + top : 416 * factor + top },
    { sec: 30 * fps, value: isMobile ? 218 * factor + top : 416 * factor + top },
    { sec: 57 * fps, value: isMobile ? 218 * factor + top : 416 * factor + top },
    { sec: 87 * fps, value: isMobile ? 476 * factor + top : 550 * factor + top },
  ]
}

export function initBallScaleKeyFrame(fps: number, isMobile: boolean): KeyFrame[] {
  return [
    { sec: 0, value: isMobile ? 1.6 : 1 },
    { sec: 5 * fps, value: isMobile ? 1.6 : 1 },
    { sec: 20 * fps, value: isMobile ? 1.8 : 1.2 },
    { sec: 40 * fps, value: isMobile ? 1.6 : 1 },
  ]
}
