import { ComponentType, JSX, useEffect, useMemo, useRef } from 'react'

import useLoadingLightEffect from '@repo/ui/pixi-react/effects/loadingLight/useLoadingLightEffect'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { LoadingResourceEnum, LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { cn, isMobileDevice, sleep } from '@repo/utils/common/common'
import { useTransitionMount } from '@repo/utils/hook/useTransitionMount'
import { connectWS } from '@repo/utils/socket'
import { type PreloadResource } from '@repo/utils/types/misc'

import LoadingScreen, { LoadingResources } from './loadingPage/LoadingScreen'
import './LoadingProviderNew.scss'
// import { loadResource } from './loadResource'
import { loadResource, pruneCache } from './loadResource-cache'

type AppPromise = Promise<{ default: ComponentType<unknown> }>

const useAppLoader = (preloadResources: PreloadResource, AppPromise: AppPromise) => {
  const addToResources = useLoadingStore((state) => state.addToResources)
  const decrementResources = useLoadingStore((state) => state.decrementResources)
  const IS_CLOSE_SERVER = useConfigStore((state) => state.IS_CLOSE_SERVER)
  const loadingStage = useLoadingStore((state) => state.loadingStage)
  const progress = useLoadingStore((state) => {
    const { totalResources, resources } = state
    if (totalResources === 0) return 0
    return (totalResources - resources.size) / totalResources
  })

  const isUpdatingStageRef = useRef(false)
  const isPreloadStartedRef = useRef(false)

  const resourcesArray = useMemo(() => Object.values(preloadResources), [preloadResources])

  useEffect(() => {
    resourcesArray.forEach(({ url }) => {
      if (url) addToResources(url)
    })

    if (!IS_CLOSE_SERVER) {
      addToResources(LoadingResourceEnum.ConnectToRGS)
      addToResources(LoadingResourceEnum.SyncWallet)
    }

    connectWS()
  }, [IS_CLOSE_SERVER, addToResources, resourcesArray])

  useEffect(() => {
    if (loadingStage !== LoadingStageEnum.Idle || isPreloadStartedRef.current) return

    isPreloadStartedRef.current = true

    pruneCache().then(() => {
      resourcesArray.forEach(({ type, url }) => loadResource(type, url))
    })

    AppPromise.then(() => {
      decrementResources(LoadingResourceEnum.AppPromise)
    }).catch((err) => {
      console.error('Failed to load lazy app promise', err)
    })
  }, [AppPromise, decrementResources, loadingStage, resourcesArray])

  useEffect(() => {
    // Find the next sequential stage based on progress
    if (loadingStage === LoadingStageEnum.Init || loadingStage >= LoadingStageEnum.Loaded || isUpdatingStageRef.current) return

    let targetStage = LoadingStageEnum.Idle
    if (progress >= 0.1) targetStage = LoadingStageEnum.Loading
    if (progress >= 1) targetStage = LoadingStageEnum.Loaded

    // Only proceed to the next stage sequentially (one step at a time)
    const nextStage = loadingStage + 1

    if (nextStage <= targetStage && nextStage !== loadingStage) {
      isUpdatingStageRef.current = true

      const updateStage = async () => {
        // Wait 1000ms before updating to the next stage
        await sleep(1000)

        useLoadingStore.setState({ loadingStage: nextStage })
        isUpdatingStageRef.current = false
      }

      updateStage()
    }
  }, [progress, loadingStage])

  // Use the loading spinning light
  useLoadingLightEffect()
}

interface LoadingProviderProps {
  children: JSX.Element
  AppPromise: AppPromise
  preloadResources: PreloadResource
  loadingResources: LoadingResources
}

export default function LoadingProvider(props: LoadingProviderProps) {
  const { children, AppPromise, preloadResources, loadingResources } = props
  const { desktopBG, mobileBG } = loadingResources

  useAppLoader(preloadResources, AppPromise)

  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const isLoadingFinished = useLoadingStore((s) => s.isLoadingFinished)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const shouldRenderApp = useTransitionMount(loadingStage >= LoadingStageEnum.Loaded)

  const interactionBlockOverlay = !isLoadingFinished // Block interactions until fully loaded
  const showMainBG = loadingStage >= LoadingStageEnum.Idle

  return (
    <div
      id="LoadingProvider"
      className={cn({ isSmallDevice })}
    >
      {interactionBlockOverlay && <div className="interactionBlockOverlay" />}
      <div
        className={showMainBG ? 'mainBG' : ''}
        style={{ backgroundImage: showMainBG ? `url(${isMobileDevice ? mobileBG : desktopBG})` : 'black' }}
      />
      <LoadingScreen resources={loadingResources} />
      {shouldRenderApp && children}
    </div>
  )
}
