import { GameEvent } from '@ikigaians/connect'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { BetHistoryListEvent, BetHistorySubtotalEvent, BetResultEvent, BetStatsEvent } from '../types'

export class BetEventHandlers {
  static onBetResult(msg: BetResultEvent, currentRoundId: string) {
    const ev = msg
    console.debug('BET_RESULT', ev)

    if (ev.error) {
      const { error } = ev
      console.error(`error code: ${error.code}, ${error.message}`)
      useWinAnimStore.getState().onBetResultError(currentRoundId)
      useToastStore.getState().addToast('error', error.message)
      useDebugStore.getState().setMsg(`${GameEvent.BET_RESULT}: ${JSON.stringify(ev.error)}`)
      return false
    }

    return true
  }

  static onBetHistoryList(msg: BetHistoryListEvent) {
    const ev = msg
    console.debug(GameEvent.BET_HISTORY_LIST, ev)
    const { betHistory, next } = ev.data ?? {}
    if (betHistory) useHistoryStore.getState().addHistories(betHistory)
    useHistoryStore.setState({ next })
  }

  static onBetHistorySubtotal(msg: BetHistorySubtotalEvent) {
    const ev = msg
    console.debug(GameEvent.BET_HISTORY_SUBTOTAL, ev)
    const { betHistorySubtotals } = ev.data ?? {}
    if (betHistorySubtotals) useHistoryStore.getState().addDaySubtotal(betHistorySubtotals)
  }

  static onBetStats(msg: BetStatsEvent) {
    const ev = msg
    const { betStats } = ev.data ?? {}
    if (betStats) useCartStore.setState({ betStats })
  }
}
