import { Event, GameEvent, WsEvent } from '@ikigaians/connect'
import { DealerPayload, GameType } from '@ikigaians/house'

export interface RoundInfo {
  roundId: string
  gameType: GameType
  gameCode: string
  betResult: boolean
  dealer: DealerPayload
}

export const createEmptyRoundInfo = (): RoundInfo => ({
  gameCode: '',
  gameType: GameType.UNKNOWN,
  roundId: '',
  betResult: false,
  dealer: { name: '' },
})

// WebSocket message type aliases for better readability

// WebSocket lifecycle events
export type WsOpenedEvent = Event<typeof WsEvent.WS_OPENED>
export type WsClosedEvent = Event<typeof WsEvent.WS_CLOSED>
export type WsReconnectingEvent = Event<typeof WsEvent.WS_RECONNECTING>
export type WsReconnectedEvent = Event<typeof WsEvent.WS_RECONNECTED>

// Table events
export type TableStateEvent = Event<typeof GameEvent.TABLE_STATE>
export type TableStartEvent = Event<typeof GameEvent.TABLE_START>
export type TableBetStopEvent = Event<typeof GameEvent.TABLE_BET_STOP>
export type TableDealEvent = Event<typeof GameEvent.TABLE_DEAL>
export type TableWinnersEvent = Event<typeof GameEvent.TABLE_WINNERS>
export type TableFinishEvent = Event<typeof GameEvent.TABLE_FINISH>
export type TablePauseEvent = Event<typeof GameEvent.TABLE_PAUSE>
export type TableResumeEvent = Event<typeof GameEvent.TABLE_RESUME>
export type TableMaintenanceEvent = Event<typeof GameEvent.TABLE_MAINTENANCE>

// Player events
export type PlayerStateEvent = Event<typeof GameEvent.PLAYER_STATE>
export type WalletStateEvent = Event<typeof GameEvent.WALLET_STATE>

// Session events
export type SessionKickedEvent = Event<typeof GameEvent.SESSION_KICKED>

// Bet events
export type BetResultEvent = Event<typeof GameEvent.BET_RESULT>
export type BetHistoryListEvent = Event<typeof GameEvent.BET_HISTORY_LIST>
export type BetHistorySubtotalEvent = Event<typeof GameEvent.BET_HISTORY_SUBTOTAL>
export type BetStatsEvent = Event<typeof GameEvent.BET_STATS>

// Message events
export type GameMessageEvent = Event<typeof GameEvent.GAME_MESSAGE>
export type SupportMessageEvent = Event<typeof GameEvent.SUPPORT_MESSAGE>
export type SystemMessageEvent = Event<typeof GameEvent.SYSTEM_MESSAGE>
