import crystalLogo from '@repo/ui/assets/loading/crystalLogo.webp'
import goldenLiveLogo from '@repo/ui/assets/loading/goldenLiveLogo.webp'
import desktopBGDefault from '@repo/ui/assets/loading/rouLoadingBG.webp'
import desktopBG001 from '@repo/ui/assets/loading/rouLoadingBG001.webp'
import desktopBG002 from '@repo/ui/assets/loading/rouLoadingBG002.webp'
import mobileBGDefault from '@repo/ui/assets/loading/rouMobileLoadingBG.webp'
import mobileBG001 from '@repo/ui/assets/loading/rouMobileLoadingBG001.webp'
import mobileBG002 from '@repo/ui/assets/loading/rouMobileLoadingBG002.webp'
import Image from '@repo/ui/components/common/Image'
import { lazy } from 'react'

import LandscapeProvider from '@repo/ui/components/layout/LandscapeProvider'
import LoadingProviderNew from '@repo/ui/components/layout/LoadingProviderNew'

import { Icon } from '@repo/ui/icon'
import VisualEffect from '@repo/ui/pixi-react/VisualEffect'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { isMobileDevice } from '@repo/utils/common/common'
import '@repo/utils/locales/index'

import { useWinEffect } from './pixi-react/winning/useWinEffect'
import preloadResources from './utils/resources/preloadResources'

const App = lazy(() => import('./App'))
const AppPromise = import('./App')

// ARO-259
const getBackgroundForTable = (tableId: string) => {
  switch (tableId) {
    case 'ARO-001':
    case 'ARO-005':
    case 'ARO-006':
    case 'ARO-007':
      return isMobileDevice ? mobileBG001 : desktopBG001
    case 'ARO-002':
    case 'ARO-003':
    case 'ARO-004':
      return isMobileDevice ? mobileBG002 : desktopBG002
    default:
      return isMobileDevice ? mobileBGDefault : desktopBGDefault
  }
}

const GAME_TABLE = useConfigStore.getState().GAME_TABLE
const backgroundImage = getBackgroundForTable(GAME_TABLE)

// Loading text options for random selection (shared between percent10 and percent80)
const LOADING_TEXT_OPTIONS = [
  'ROULETTE.LOADING_WAXING_WHEEL',
  'ROULETTE.LOADING_STARTING_ENGINES',
  'ROULETTE.LOADING_LIGHTING_LIGHT',
  'ROULETTE.LOADING_POLISHING_BALL',
  'ROULETTE.LOADING_POWERING_CAMERA',
  'ROULETTE.LOADING_SPIN_TO_WIN',
  'ROULETTE.LOADING_YOUR_TURN',
  'ROULETTE.LOADING_CHECKING_HORIZON',
  'ROULETTE.LOADING_SLAM_DUNKING',
] as const

const loadingResources: React.ComponentProps<typeof LoadingProviderNew>['loadingResources'] = {
  headerWithoutWinHistory: true,
  desktopBG: backgroundImage,
  mobileBG: backgroundImage,
  topIcon: (
    <Image
      src={goldenLiveLogo}
      alt="Golden Live Logo"
      className="h-auto max-w-full"
    />
  ),
  gameIcon: <Icon.Roulette />,
  bottomIcon: (
    <Image
      src={crystalLogo}
      alt="Crystal Logo"
      className="h-auto max-w-full"
    />
  ),
  gameId: 'roulette',
  gameIconDissolve: preloadResources.dissolveLogo.url,
  loadingTextOptions: LOADING_TEXT_OPTIONS,
}

export default function Root() {
  return (
    <>
      <LoadingProviderNew
        preloadResources={preloadResources}
        loadingResources={loadingResources}
        AppPromise={AppPromise}
      >
        <App />
      </LoadingProviderNew>

      <LandscapeProvider />
      <VisualEffect hooks={[useWinEffect]} />
    </>
  )
}
