import { CardSuit } from '@ikigaians/house'

import ClubsIcon from '@repo/ui/assets/baccarat/mini-card/clubs.svg'
import DiamondsIcon from '@repo/ui/assets/baccarat/mini-card/diamonds.svg'
import HeartsIcon from '@repo/ui/assets/baccarat/mini-card/hearts.svg'
import SpadesIcon from '@repo/ui/assets/baccarat/mini-card/spades.svg'
import { cn } from '@repo/utils/common/common'
import { type FC, type SVGProps } from 'react'

interface SuitIconProps {
  suit: CardSuit
  scaleFactor?: number
}

interface SuitConfig {
  Component: FC<SVGProps<SVGSVGElement>>
  width: number
  height: number
  colorClass: string
}

const SUIT_CONFIG: Partial<Record<CardSuit, SuitConfig>> = {
  [CardSuit.HEART]: { Component: HeartsIcon, width: 1.4375, height: 1.3125, colorClass: 'text-[#CE2127]' },
  [CardSuit.DIAMOND]: { Component: DiamondsIcon, width: 1.4375, height: 1.5, colorClass: 'text-[#CE2127]' },
  [CardSuit.SPADE]: { Component: SpadesIcon, width: 1.375, height: 1.4375, colorClass: 'text-[#000]' },
  [CardSuit.CLUB]: { Component: ClubsIcon, width: 1.4375, height: 1.4375, colorClass: 'text-[#000]' },
} as const

export default function SuitIcon({ suit, scaleFactor = 1 }: SuitIconProps) {
  const config = SUIT_CONFIG[suit]

  if (!config) return null

  const { Component, width, height, colorClass } = config

  return (
    <Component
      style={
        {
          '--scale-factor': scaleFactor,
          height: `calc(${height}rem*var(--scale-factor))`,
          width: `calc(${width}rem*var(--scale-factor))`,
        } as React.CSSProperties
      }
      className={cn(colorClass)}
    />
  )
}
