import { useChatStore } from '@repo/ui/store/useChatStore'
import { playSound } from '@repo/utils/common/sound'
import { create } from 'zustand'
import { devtools, subscribeWithSelector, type DevtoolsOptions } from 'zustand/middleware'

export type MenuPageType =
  | 'index'
  | 'rules'
  | 'limits'
  | 'rules_inner'
  | 'history'
  | 'history_content'
  | 'chat'
  | 'chat_support'
  | 'setting'
  | 'setting_video'
  | 'language'
  | 'safari_hint'
  | 'betting_mode'
  | 'betting_mode_intro'
  | 'statistics'
  | 'autoplay'
  | 'help'
  | null

export type DesktopModalType =
  | 'settings'
  | 'language'
  | 'history'
  | 'chat'
  | 'support'
  | 'help'
  | 'rules'
  | 'rules_inner'
  | 'statistics'
  | 'history_content'
  | 'betting_mode'
  | 'payout'
  | 'autoplay'
  | 'sound'
  | null

export const skipScrollPages = ['rules_inner', 'support']
export type chipTextSizeType = {
  gridBoard: number
  racetrack: number
  sicBo: number
  baccarat: number
}

interface LayoutStoreState {
  isVirtualKeyboardOpened: boolean
  visualViewportMargin: number
  isPixiLoading: boolean
  isOpenSelector: boolean
  menuPage: MenuPageType
  menuPageHistory: MenuPageType[]
  isPayoutModalOpen: boolean
  isChatExpand: boolean
  desktopModal: DesktopModalType
  prevDesktopModal: DesktopModalType
  desktopModalStack: DesktopModalType[]
  rootEmFontSize: number
  mobileHeaderFontSize: number
  isUnmuteHint: boolean
  scrollToId: string
  isCanStream: boolean
  chipTextSize: chipTextSizeType
  modalOpenCount: number
}

interface LayoutStoreActions {
  toggleSelector: () => void
  toggleMenu: (page: MenuPageType) => void
  openDesktopModal: (modal: DesktopModalType) => void
  closeDesktopModal: (modal: DesktopModalType) => void
  closeNewestDesktopModal: () => void
  closeMenu: () => void
  goBackMenu: () => void
  getIsFullHeight: () => boolean
  incrementModalCount: () => void
  decrementModalCount: () => void
}

const DEVTOOLS_CONFIG: DevtoolsOptions = {
  name: `${document.title} - useLayoutStore`,
  store: 'LayoutStore',
  serialize: {
    options: true,
  },
}

const LEFT_SIDE_DESKTOP_MODALS: DesktopModalType[] = ['payout', 'chat']

export const useLayoutStore = create<LayoutStoreState & LayoutStoreActions>()(
  devtools(
    subscribeWithSelector((set, get) => ({
      isVirtualKeyboardOpened: false,
      visualViewportMargin: 0,
      isPixiLoading: false,
      isOpenSelector: false,
      isUnmuteHint: true,
      scrollToId: '',
      menuPage: null,
      menuPageHistory: [],
      rootEmFontSize: 16,
      mobileHeaderFontSize: 0.688,
      isPayoutModalOpen: false,
      isChatExpand: false,
      desktopModal: null,
      prevDesktopModal: null,
      desktopModalStack: [],
      isCanStream: true,
      chipTextSize: {
        gridBoard: 0,
        racetrack: 0,
        sicBo: 0,
        baccarat: 0,
      },
      modalOpenCount: 0,
      toggleSelector: () => {
        const { isOpenSelector } = get()
        set({
          menuPage: null,
          isOpenSelector: !isOpenSelector,
        })
        playSound(['chiptrayOpen'])
      },
      toggleMenu: (newPage: MenuPageType) => {
        const { menuPage } = get()
        if (menuPage === newPage) {
          set({ menuPage: null })
          return
        }
        set({
          menuPage: newPage,
          isOpenSelector: false,
        })
      },
      openDesktopModal: (modal) => {
        set((state) => {
          if (modal === 'chat') {
            return {
              isChatExpand: true,
              desktopModalStack: [...state.desktopModalStack, modal],
            }
          }

          if (modal === 'payout') {
            return {
              isPayoutModalOpen: true,
              desktopModalStack: [...state.desktopModalStack, modal],
            }
          }

          return {
            prevDesktopModal: state.desktopModal,
            desktopModal: modal,
            // When opening the modal (right side), we retain only left-side modals in the stack.
            // This ensures right-side modals do not overlap, but left-side modals can remain open.
            desktopModalStack: [...state.desktopModalStack.filter((item) => LEFT_SIDE_DESKTOP_MODALS.includes(item)), modal],
          }
        })
      },
      closeDesktopModal: (modal) => {
        set((state) => {
          if (modal === 'chat') {
            return {
              isChatExpand: false,
              desktopModalStack: state.desktopModalStack.filter((item) => item !== modal),
            }
          }

          if (modal === 'payout') {
            return {
              isPayoutModalOpen: false,
              desktopModalStack: state.desktopModalStack.filter((item) => item !== modal),
            }
          }

          return {
            prevDesktopModal: null,
            desktopModal: null,
            desktopModalStack: state.desktopModalStack.filter((item) => item !== modal),
          }
        })
      },
      closeNewestDesktopModal: () => {
        set((state) => {
          const topItem = state.desktopModalStack.at(-1)

          if (topItem === 'chat') {
            return {
              isChatExpand: false,
              desktopModalStack: state.desktopModalStack.filter((item) => item !== topItem),
            }
          }

          if (topItem === 'payout') {
            return {
              isPayoutModalOpen: false,
              desktopModalStack: state.desktopModalStack.filter((item) => item !== topItem),
            }
          }

          return {
            prevDesktopModal: null,
            desktopModal: null,
            desktopModalStack: state.desktopModalStack.filter((item) => item !== topItem),
          }
        })
      },
      goBackMenu: () => {
        const { menuPageHistory } = get()

        if (menuPageHistory.length <= 1) {
          set({ menuPage: null })
          return
        }

        set({
          menuPage: menuPageHistory[menuPageHistory.length - 2],
          menuPageHistory: menuPageHistory.slice(0, menuPageHistory.length - 1),
        })
      },
      closeMenu: () => set({ menuPage: null, desktopModal: null }),
      getIsFullHeight: () => {
        const notFullHeightPages = ['index', 'safari_hint', 'betting_mode', 'betting_mode_intro', 'autoplay']
        const { menuPage } = get()
        return menuPage === null || !notFullHeightPages.includes(menuPage)
      },
      incrementModalCount: () => {
        set((state) => ({ modalOpenCount: state.modalOpenCount + 1 }))
      },
      decrementModalCount: () => {
        set((state) => ({ modalOpenCount: Math.max(0, state.modalOpenCount - 1) }))
      },
    })),
    DEVTOOLS_CONFIG
  )
)

useLayoutStore.subscribe(
  (state) => state.menuPage,
  (newPage) => {
    const { menuPageHistory } = useLayoutStore.getState()
    if (newPage === null) {
      useLayoutStore.setState({ menuPageHistory: [] })
      return
    }
    if (newPage === 'chat_support') {
      useChatStore.setState({ supportChatUnread: 0 })
    }

    if (menuPageHistory[menuPageHistory.length - 1] !== newPage) {
      useLayoutStore.setState({ menuPageHistory: [...menuPageHistory, newPage] })
    }
  }
)
