import { isMobileDevice } from '@repo/utils/common/common'
import { Container, FillGradient, TextStyle, TextStyleFontWeight } from 'pixi.js'

export const createCtn = (ref: React.RefObject<Container | null>, alpha = 1) => {
  const container = new Container()
  container.interactive = false
  container.interactiveChildren = false
  container.cullable = true
  container.cullableChildren = true
  ref.current = container
  container.alpha = alpha
  return container
}

export const GOLDEN_GRADIENT = {
  type: 'linear' as const,
  start: { x: 0, y: 0 },
  end: { x: 0, y: 1 },
  colorStops: [
    { offset: 0, color: 0xfdf0df },
    { offset: 0.63, color: 0xaf8455 },
    { offset: 0.9, color: 0xfdf0df },
  ],
  textureSpace: 'local' as const,
}

export const SILVER_GRADIENT = {
  type: 'linear' as const,
  start: { x: 0, y: 0 },
  end: { x: 0, y: 1 },
  colorStops: [
    { offset: 0, color: 0xe2ded2 },
    { offset: 1, color: 0xafa482 },
  ],
  textureSpace: 'local' as const,
}

export const createBaseStyle = (
  factor: number,
  options: {
    fontFamily: string | string[]
    fontSize: number
    fontWeight: TextStyleFontWeight
    lineHeight: number
    letterSpacing: number
    gradient: typeof GOLDEN_GRADIENT | typeof SILVER_GRADIENT
  }
) =>
  new TextStyle({
    fontFamily: options.fontFamily,
    fontSize: `${isMobileDevice ? options.fontSize * factor : options.fontSize * 1.5 * factor}px`,
    fontWeight: options.fontWeight,
    fontVariant: 'normal',
    lineHeight: isMobileDevice ? options.lineHeight * factor : options.lineHeight * 1.5 * factor,
    letterSpacing: options.letterSpacing,
    align: 'center',
    fill: new FillGradient(options.gradient),
  })

export const createTitleStyle = (factor: number) =>
  createBaseStyle(factor, {
    fontFamily: ['Lufga', 'Arial'],
    fontSize: 16,
    fontWeight: '700',
    lineHeight: 32,
    letterSpacing: 1.1,
    gradient: GOLDEN_GRADIENT,
  })

export const createUserWinStyle = (factor: number) =>
  createBaseStyle(factor, {
    fontFamily: ['Lufga', 'Arial'],
    fontSize: 24,
    fontWeight: '600',
    lineHeight: 28,
    letterSpacing: 0.48,
    gradient: SILVER_GRADIENT,
  })

export const createNumStyle = (factor: number) =>
  createBaseStyle(factor, {
    fontFamily: ['Khand', 'Segoe UI', 'Helvetica Neue'],
    fontSize: 29,
    fontWeight: '600',
    lineHeight: 20,
    letterSpacing: 0,
    gradient: SILVER_GRADIENT,
  })

export const createNumHighlightStyle = (factor: number) =>
  createBaseStyle(factor, {
    fontFamily: ['Khand', 'Segoe UI', 'Helvetica Neue'],
    fontSize: 59,
    fontWeight: '600',
    lineHeight: 30,
    letterSpacing: 0,
    gradient: SILVER_GRADIENT,
  })
