import { playSound } from '@repo/utils/common/sound'
import { TooltipImperativeOpenOptions } from '../components/react-tooltip/TooltipList'

export type ToolTipType =
  | 'lowBalance'
  | 'betAutomatically'
  | 'empty'
  | 'autoplay'
  | 'close'
  | 'comingSoon'
  | 'overLimit'
  | 'noBet'
  | 'oppositeBet'

type propsType = {
  position?: { x: number; y: number }
  anchorSelect?: string
  mute?: boolean
}
const exceptionList = new Set(['betAutomatically'])

class TooltipEventBus {
  private listeners: Map<ToolTipType, (props?: propsType) => void>

  constructor() {
    this.listeners = new Map()
  }

  subscribe(type: ToolTipType, listener: (props?: TooltipImperativeOpenOptions) => void) {
    this.listeners.set(type, listener)

    return (type: ToolTipType) => {
      this.listeners.delete(type)
    }
  }

  emit(type: ToolTipType, props?: propsType) {
    if (!exceptionList.has(type) && !props?.mute) playSound(['disabledAction'])
    const listener = this.listeners.get(type)
    listener?.(props)
  }
}

export const tooltipEventBus = new TooltipEventBus()
