import type { Application } from 'pixi.js'
import { create } from 'zustand'
import { devtools, type DevtoolsOptions } from 'zustand/middleware'

interface StageStoreState {
  core: Application | null
}

interface StageStoreActions {
  setCore: (core: Application) => void
}

const DEVTOOLS_CONFIG: DevtoolsOptions = {
  name: `${document.title} - useStageStore`,
  store: 'StageStore',
  serialize: {
    replacer: (key: keyof StageStoreState, value: StageStoreState[keyof StageStoreState]) => {
      if (key === 'core' && value !== null) {
        return '[Pixi Application Instance]'
      }
      return value
    },
  },
}

export const useStageStore = create<StageStoreState & StageStoreActions>()(
  devtools(
    (set) => ({
      core: null,
      setCore: (core) => set({ core }),
    }),
    DEVTOOLS_CONFIG
  )
)
