export interface PreloadProgress {
  /** The URL of the video being preloaded */
  url: string
  /** Number of bytes loaded so far */
  loaded: number
  /** Total size of the video in bytes */
  total: number
}

export interface VideoData {
  /** The video blob data */
  blob: Blob
}

export interface PreloadStatus {
  /** Number of bytes loaded */
  loaded: number
  /** Total size in bytes */
  total: number
  /** Progress as a decimal between 0 and 1 */
  progress: number
}

type ProgressCallback = (progress: number) => void

const DEFAULT_PROGRESS = 1

/**
 * Manages video preloading and caching for optimized playback.
 * Implements a singleton pattern to ensure a single cache instance.
 * If without, browser will initiate multiple range requests, and under slow network, it will cause severe FPS drops.
 */
export class VideoPreloadManager {
  readonly cache = new Map<string, VideoData>()
  readonly progress = new Map<string, PreloadProgress>()

  static instance: VideoPreloadManager

  static getInstance(): VideoPreloadManager {
    if (!this.instance) this.instance = new VideoPreloadManager()

    return this.instance
  }

  async preloadVideo(url: string, onProgress?: ProgressCallback): Promise<void> {
    if (this.isVideoCached(url)) return onProgress?.(DEFAULT_PROGRESS)
    if (this.isDownloadComplete(url)) return onProgress?.(DEFAULT_PROGRESS)

    try {
      await this.downloadVideo(url, onProgress)
    } catch (error) {
      console.error('Failed to preload video:', error)
      throw error
    }
  }

  async getVideoForPlayback(url: string): Promise<Blob | null> {
    return this.cache.get(url)?.blob ?? null
  }

  getPreloadStatus(url: string): PreloadStatus | null {
    const progress = this.progress.get(url)
    if (!progress) return null

    return {
      loaded: progress.loaded,
      total: progress.total,
      progress: this.calculateProgress(progress.loaded, progress.total),
    }
  }

  getCacheSize(): number {
    return Array.from(this.cache.values()).reduce((total, { blob }) => total + blob.size, 0)
  }

  getCachedUrls(): string[] {
    return Array.from(this.cache.keys())
  }

  clearCache(url: string): void {
    this.cache.delete(url)
    this.progress.delete(url)
  }

  private isVideoCached(url: string): boolean {
    return this.cache.has(url)
  }

  private isDownloadComplete(url: string): boolean {
    const progress = this.progress.get(url)
    return progress ? progress.loaded === progress.total : false
  }

  private async downloadVideo(url: string, onProgress?: ProgressCallback): Promise<void> {
    const response = await this.fetchVideo(url)
    const contentLength = this.getContentLength(response.headers)

    this.initializeProgress(url, contentLength)

    if (!contentLength) return await this.handleEmptyContent(url, response, onProgress)

    await this.downloadWithProgress(url, response, contentLength, onProgress)
  }

  private async fetchVideo(url: string): Promise<Response> {
    const response = await fetch(url)

    if (!response.ok || !response.body) throw new Error(`Failed to fetch video: ${response.statusText}`)

    return response
  }

  private getContentLength(headers: Headers): number {
    return parseInt(headers.get('content-length') || '0')
  }

  private initializeProgress(url: string, total: number): void {
    this.progress.set(url, { url, loaded: 0, total })
  }

  private async handleEmptyContent(url: string, response: Response, onProgress?: ProgressCallback): Promise<void> {
    const blob = await response.blob()
    this.cacheVideo(url, blob)
    onProgress?.(DEFAULT_PROGRESS)
  }

  private async downloadWithProgress(url: string, response: Response, contentLength: number, onProgress?: ProgressCallback): Promise<void> {
    const reader = response.body!.getReader()
    const chunks: Uint8Array[] = []
    let receivedLength = 0

    while (true) {
      const { done, value } = await reader.read()
      if (done) break

      chunks.push(value)
      receivedLength += value.length

      this.updateProgress(url, receivedLength, contentLength)
      onProgress?.(this.calculateProgress(receivedLength, contentLength))
    }

    const blob = this.createBlob(chunks, response.headers)
    this.cacheVideo(url, blob)
    this.finalizeProgress(url, contentLength)
  }

  private updateProgress(url: string, loaded: number, total: number): void {
    this.progress.set(url, { url, loaded, total })
  }

  private calculateProgress(loaded: number, total: number): number {
    return total > 0 ? loaded / total : 0
  }

  private createBlob(chunks: Uint8Array[], headers: Headers): Blob {
    const contentType = headers.get('content-type') ?? undefined
    return new Blob(chunks, { type: contentType })
  }

  private cacheVideo(url: string, blob: Blob): void {
    this.cache.set(url, { blob })
  }

  private finalizeProgress(url: string, total: number): void {
    this.progress.set(url, { url, loaded: total, total })
  }
}

export const videoManager = VideoPreloadManager.getInstance()
