import { CardRank, CardSuit } from '@ikigaians/house'
import { cn } from '@repo/utils/common/common'
import { AnimatePresence, motion, type MotionProps } from 'framer-motion'

import './MiniCard.scss'
import RankIcon from './RankIcon'
import SuitIcon from './SuitIcon'

type FadeInOutDirection = 'up' | 'left' | 'right'
type Status = 'top-board' | 'history'
interface MiniCardProps {
  suit: CardSuit
  rank: CardRank
  status: Status
  fadeInOutDirection: FadeInOutDirection
  isVisible?: boolean
  orientation: 'vertical' | 'horizontal'
  className?: string
  scaleFactor?: number
}

const MOTION_CONFIG: Record<Status, Record<FadeInOutDirection, MotionProps>> = {
  'top-board': {
    up: {
      initial: { opacity: 0, y: '50%' },
      animate: { opacity: 1, y: 0 },
      transition: { duration: 0.4, ease: 'easeInOut' },
      exit: { opacity: 0 },
    },
    left: {
      initial: { opacity: 0, x: '-50%' },
      animate: { opacity: 1, x: 0 },
      transition: { duration: 0.4, ease: 'easeInOut' },
      exit: { opacity: 0 },
    },
    right: {
      initial: { opacity: 0, x: '50%' },
      animate: { opacity: 1, x: 0 },
      transition: { duration: 0.4, ease: 'easeInOut' },
      exit: { opacity: 0 },
    },
  },
  history: {
    up: {
      initial: false,
    },
    left: {
      initial: false,
    },
    right: {
      initial: false,
    },
  },
} as const

export default function MiniCard(props: MiniCardProps) {
  const { suit, rank, status, isVisible = true, fadeInOutDirection, orientation, className, scaleFactor = 1 } = props
  const isRed = suit === CardSuit.HEART || suit === CardSuit.DIAMOND

  return (
    <div
      id="MiniCard"
      className={className}
      style={{ '--scale-factor': scaleFactor } as React.CSSProperties}
    >
      <AnimatePresence>
        {isVisible ? (
          <motion.div {...MOTION_CONFIG[status][fadeInOutDirection]}>
            <div
              className={cn(
                'relative rounded-[calc(0.1875rem*var(--scale-factor))] bg-white',
                orientation === 'vertical' &&
                  'aspect-[45/62] h-[calc(3.875rem*var(--scale-factor))] w-[calc(2.8125rem*var(--scale-factor))]',
                orientation === 'horizontal' &&
                  'aspect-[62/45] h-[calc(2.8125rem*var(--scale-factor))] w-[calc(3.875rem*var(--scale-factor))]',
                status === 'history' && 'historyDropShadow',
                status === 'top-board' && 'topBoardDropShadow'
              )}
            >
              <div
                className={cn(
                  'absolute top-[calc(0.25rem*var(--scale-factor))]',
                  orientation === 'vertical' && 'left-[calc(0.25rem*var(--scale-factor))]',
                  orientation === 'horizontal' && 'right-[calc(0.25rem*var(--scale-factor))] origin-top-right translate-y-full rotate-90'
                )}
              >
                <div className="flex size-[calc(1.8125rem*var(--scale-factor))] flex-shrink-0 items-start justify-start">
                  <RankIcon
                    rank={rank}
                    isRed={isRed}
                    scaleFactor={scaleFactor}
                  />
                </div>
              </div>
              <div
                className={cn(
                  'absolute bottom-[calc(0.25rem*var(--scale-factor))]',
                  orientation === 'vertical' && 'right-[calc(0.25rem*var(--scale-factor))]',
                  orientation === 'horizontal' && 'left-[calc(0.25rem*var(--scale-factor))] origin-bottom-left -translate-y-full rotate-90'
                )}
              >
                <div className="flex size-[calc(1.5rem*var(--scale-factor))] flex-shrink-0 items-end justify-end">
                  <SuitIcon
                    suit={suit}
                    scaleFactor={scaleFactor}
                  />
                </div>
              </div>
            </div>
          </motion.div>
        ) : null}
      </AnimatePresence>
    </div>
  )
}
