export enum CallBetsEnum {
  TIER = 'TIER',
  ORPH = 'ORPH',
  VOISINS = 'VOISINS',
  ZERO = 'ZERO',
}

export const BetTypeEnum = {
  ...RouletteBetNo,
  ...CallBetsEnum,
}
export type BetTypeEnum = (typeof BetTypeEnum)[keyof typeof BetTypeEnum]

export const raceTrackGroup = Object.values(CallBetsEnum) as BetTypeEnum[]

