import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { motion } from 'framer-motion'
import { useMemo } from 'react'

interface LoadingBackgroundProps {
  desktopBG: string
  mobileBG: string
  gameId: 'roulette' | 'sicbo' | 'baccarat'
}

interface AnimationConfig {
  opacity: number
  filter?: string
  height?: string
  top?: string
  [key: `--${string}`]: string | number
}

export default function LoadingBackground({ desktopBG, mobileBG, gameId }: LoadingBackgroundProps) {
  const loadingStage = useLoadingStore((s) => s.loadingStage)

  const animProps = useMemo(() => {
    const base = {
      initial: {
        opacity: 0,
        filter: `blur(${isMobileDevice ? 4 : 12}px)`,
        height: '100%',
        top: isMobileDevice ? '-4.5rem' : '0',
      },
      transition: {
        height: { duration: 1.8, ease: [0.33, 1, 0.68, 1] },
        top: { duration: 1.8, ease: [0.33, 1, 0.68, 1] },
        default: { duration: 2, ease: [0.8, 0, 0.9, 1] },
      },
    }

    let animate: AnimationConfig

    /*
     * Animation sequence.
     * Desktop background's position temporarily remain unanimated.
     */
    if (loadingStage >= LoadingStageEnum.PostLoaded3) {
      animate = {
        opacity: 0.9,
        filter: 'blur(0px)',
        height: isMobileDevice && gameId === 'baccarat' ? 'calc(100% - 9.8rem)' : '100%',
        top: isMobileDevice ? '-7rem' : '0',
      }
      base.transition.default = { duration: 0.85, ease: [0.8, 0, 0.9, 1] }
    } else if (loadingStage >= LoadingStageEnum.PostLoaded1) {
      animate = {
        opacity: 0.6,
        height: isMobileDevice ? 'calc(100% - 9.8rem)' : '100%',
        top: isMobileDevice ? '-7rem' : '0',
      }
    } else if (loadingStage >= LoadingStageEnum.Idle) {
      animate = {
        opacity: 0.6,
      }
    } else {
      animate = {
        opacity: 0,
      }
    }

    return { ...base, animate }
  }, [gameId, loadingStage])

  return (
    <motion.div
      className={cn('loadingScreenBG', { isMobileDevice })}
      style={{ backgroundImage: `url(${isMobileDevice ? mobileBG : desktopBG})` }}
      {...animProps}
    >
      <div className="loadingFadeout absolute bottom-[-2rem] left-0 h-full w-full" />
    </motion.div>
  )
}
