import { CardRank } from '@ikigaians/house'
import { cn } from '@repo/utils/common/common'

import AIcon from '@repo/ui/assets/baccarat/mini-card/rank-a.svg'
import EightIcon from '@repo/ui/assets/baccarat/mini-card/rank-eight.svg'
import FiveIcon from '@repo/ui/assets/baccarat/mini-card/rank-five.svg'
import FourIcon from '@repo/ui/assets/baccarat/mini-card/rank-four.svg'
import JIcon from '@repo/ui/assets/baccarat/mini-card/rank-j.svg'
import KIcon from '@repo/ui/assets/baccarat/mini-card/rank-k.svg'
import NineIcon from '@repo/ui/assets/baccarat/mini-card/rank-nine.svg'
import QIcon from '@repo/ui/assets/baccarat/mini-card/rank-q.svg'
import SevenIcon from '@repo/ui/assets/baccarat/mini-card/rank-seven.svg'
import SixIcon from '@repo/ui/assets/baccarat/mini-card/rank-six.svg'
import TenIcon from '@repo/ui/assets/baccarat/mini-card/rank-ten.svg'
import ThreeIcon from '@repo/ui/assets/baccarat/mini-card/rank-three.svg'
import TwoIcon from '@repo/ui/assets/baccarat/mini-card/rank-two.svg'
import { type FC, type SVGProps } from 'react'

interface RankIconProps {
  rank: CardRank
  isRed?: boolean
  scaleFactor?: number
}

interface RankConfig {
  Component: FC<SVGProps<SVGSVGElement>>
  width: number
  height: number
}

const RANK_CONFIG: Partial<Record<CardRank, RankConfig>> = {
  [CardRank.A]: { Component: AIcon, width: 1.3125, height: 1.75 },
  [CardRank.K]: { Component: KIcon, width: 1.25, height: 1.8125 },
  [CardRank.Q]: { Component: QIcon, width: 1.3125, height: 1.9375 },
  [CardRank.J]: { Component: JIcon, width: 1.0625, height: 1.875 },
  [CardRank.R10]: { Component: TenIcon, width: 1.4375, height: 1.8125 },
  [CardRank.R9]: { Component: NineIcon, width: 1.0625, height: 1.8125 },
  [CardRank.R8]: { Component: EightIcon, width: 1.0625, height: 1.875 },
  [CardRank.R7]: { Component: SevenIcon, width: 0.9375, height: 1.75 },
  [CardRank.R6]: { Component: SixIcon, width: 1, height: 1.875 },
  [CardRank.R5]: { Component: FiveIcon, width: 1, height: 1.875 },
  [CardRank.R4]: { Component: FourIcon, width: 1.125, height: 1.8125 },
  [CardRank.R3]: { Component: ThreeIcon, width: 1.0625, height: 1.875 },
  [CardRank.R2]: { Component: TwoIcon, width: 1, height: 1.8125 },
} as const

export default function RankIcon({ rank, isRed = false, scaleFactor = 1 }: RankIconProps) {
  const currentColor = isRed ? 'text-[#CE2127]' : 'text-[#000]'

  const config = RANK_CONFIG[rank]

  if (!config) return null

  const { Component, width, height } = config

  return (
    <Component
      style={
        {
          '--scale-factor': scaleFactor,
          height: `calc(${height}rem*var(--scale-factor))`,
          width: `calc(${width}rem*var(--scale-factor))`,
        } as React.CSSProperties
      }
      className={cn(currentColor)}
    />
  )
}
