import * as Sentry from '@sentry/react'

import { WsEvent } from '@ikigaians/connect'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { isEnvDev, isLocalhost } from './common'

// Configuration interface for environment variables
interface PreinitConfig {
  VITE_WS_SERVER: string
  VITE_GAME_TABLE: string
  VITE_SESSION_ID: string
  VITE_ENABLE_SCALE: string
  VITE_VERSION: string
  VITE_DEPLOY_ENV: string
  VITE_CLOSE_SERVER: string
  MODE: string
}

// Initialize cache system with service worker
async function initializeCacheSystem(): Promise<void> {
  try {
    if (!('serviceWorker' in navigator)) return

    // Use URL constructor for safer path resolution that handles query params and hash fragments
    const baseUrl = new URL(window.location.origin + window.location.pathname)
    const swUrl = new URL('sw-cache-interceptor.js', baseUrl).href
    const scopeUrl = new URL('./', baseUrl).pathname

    await navigator.serviceWorker.register(swUrl, { scope: scopeUrl })
    await navigator.serviceWorker.ready
  } catch (error) {
    console.error('Cache initialization failed:', error)
  }
}

export async function initializeCommon(config: PreinitConfig) {
  checkRequiredEnv()
  console.debug(config)
  const searchParams = new URLSearchParams(document.location.search)

  const IS_CLOSE_SERVER = searchParams.get('VITE_CLOSE_SERVER') === '1' || config.VITE_CLOSE_SERVER === '1'
  const WS_SERVER = searchParams.get('cb') ?? config.VITE_WS_SERVER
  const GAME_TABLE = searchParams.get('game') ?? config.VITE_GAME_TABLE
  const SESSION_ID = searchParams.get('sid') ?? config.VITE_SESSION_ID

  if (!SESSION_ID) console.error('get your session id first')
  if (!WS_SERVER) console.error('WS_SERVER is not defined. Please check your configuration.')

  useConfigStore.setState({ WS_SERVER, GAME_TABLE, SESSION_ID, IS_CLOSE_SERVER })
  useBettingStore.setState({ isBet: IS_CLOSE_SERVER })

  // Initialize cache system with service worker
  await initializeCacheSystem()

  if (config.VITE_ENABLE_SCALE === '1') {
    const head = document.querySelector('head')

    const viewport = document.createElement('meta')
    viewport.setAttribute('name', 'viewport')
    viewport.setAttribute('content', 'width=device-width, initial-scale=1.0, maximum-scale=10.0, user-scalable=yes')

    head?.appendChild(viewport)
  }

  const initSentry = () => {
    Sentry.init({
      dsn: 'https://24a2c6f5f545222a036bba11f7b73534@o4509115379679232.ingest.us.sentry.io/4509173021999105',
      denyUrls: [/^chrome-extension:\/\//, /^moz-extension:\/\//, /^safari-extension:\/\//],
      beforeSend(event) {
        if (isLocalhost()) return null
        return event
      },
      integrations: [
        Sentry.browserTracingIntegration(),
        // Sentry.replayIntegration(),
        // Sentry.replayCanvasIntegration()
      ],
      initialScope: {
        tags: {
          game: useConfigStore.getState().GAME_TYPE,
        },
      },
      release: config.VITE_VERSION,
      environment: config.VITE_DEPLOY_ENV,
      tracesSampler: (samplingContext) => {
        if ([WsEvent.WS_RECONNECTING, WsEvent.WS_RECONNECTED].includes(samplingContext.name as WsEvent)) {
          return 1.0
        }
        return 0.1
      },
      tracePropagationTargets: config.VITE_WS_SERVER ? [config.VITE_WS_SERVER] : [],
      replaysSessionSampleRate: 0.1,
      replaysOnErrorSampleRate: 1,
      sendDefaultPii: true,
    })
  }

  if (!isEnvDev) initSentry()
}

function checkRequiredEnv() {
  const requiredEnvVars = ['VITE_DEPLOY_ENV', 'VITE_WS_SERVER', 'VITE_GAME_TABLE']

  const missingVars = requiredEnvVars.filter((varName) => !import.meta.env[varName])

  if (missingVars.length > 0) {
    throw new Error(`Missing required environment variables: ${missingVars.join(', ')}`)
  }
}

export const defaultTableLimit = { min: 0.1, max: 20000 }
