/**
 * Loading Light Effect Utilities
 *
 * Contains calculation and data processing functions for the loading light effect.
 * Separated from PIXI-specific rendering logic for better maintainability.
 */

import { MainBoardParams } from '@repo/ui/pixi-react/utils/mainBoardUtils'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'

import type { GlowFilter, KawaseBlurFilter } from 'pixi-filters'
import { Graphics, Point } from 'pixi.js'
import { CORNER_SEGMENTS, DESIGN_PERIMETER, DRAW_SEGMENT_EVERY_PIXEL, LINES_SPECS } from './constants'

export type LineSpec = ReturnType<typeof createLineSpec>[number]

export interface Path {
  p1: Point
  p2: Point
  length: number
  cumulativeLength: number
}

export interface LineGraphics extends Graphics {
  userData: {
    lineSpec: LineSpec
    baseDegree: number
  }
}

export interface SegmentSpec {
  widthMultiplier: number
  lengthMultiplier: number
}

export interface RenderCache {
  params: MainBoardParams | null
  path: Path[]
  totalPerimeter: number
  glow: GlowFilter | null
  kawase: KawaseBlurFilter[]
}

/**
 * Get the scaling factor based on the current root em font size
 * @returns Scaling factor relative to 16px base
 */
export const getFactor = () => {
  const rem = useLayoutStore.getState().rootEmFontSize
  return rem / 16 // Calculate the scaling factor based on rem units
}

/**
 * Create line specifications scaled to the current perimeter and factor
 * @param totalPerimeter - Total length in pixel of the border path
 * @returns Array of scaled line specifications with width, blur, length, color, and alpha
 */
export function createLineSpec(totalPerimeter: number) {
  const factor = getFactor()
  const lengthRatio = totalPerimeter / DESIGN_PERIMETER

  return LINES_SPECS.map(({ color, alpha, width, length, blur }) => ({
    width: width * factor,
    blur: blur * factor,
    length: length * lengthRatio,
    color,
    alpha,
  }))
}

/**
 * Calculate offset position along the border path based on degrees
 * @param degreeOffset - Offset in degrees (0-360), where 0 is the starting position
 * @param totalPerimeter - Total length in pixel of the border path
 * @returns Offset distance along the path (this will be the CENTER position of the line)
 */
export function calcDegreeOffsetDist(degreeOffset: number, totalPerimeter: number): number {
  // Normalize degrees to 0-360 range and handle negative values
  const normalizedDegrees = ((degreeOffset % 360) + 360) % 360

  // Convert degrees to distance along the perimeter
  // 360 degrees = full perimeter, so each degree = perimeter/360
  const baseOffset = (normalizedDegrees / 360) * totalPerimeter

  return baseOffset
}

/**
 * Get a point at a specific distance along the border path using binary search
 * @param path - Array of path segments
 * @param distance - Distance along the path
 * @param totalPerimeter - Total length in pixel of the border path
 * @returns Point at the specified distance, or null if path is invalid
 */
export const getPointAtDistance = (path: Path[], distance: number, totalPerimeter: number): Point | null => {
  const normalizedDistance = ((distance % totalPerimeter) + totalPerimeter) % totalPerimeter

  if (!path || path.length === 0) return null

  let low = 0
  let high = path.length - 1
  let segmentIndex = 0

  while (low <= high) {
    const mid = Math.floor((low + high) / 2)
    const segment = path[mid]

    if (normalizedDistance >= segment.cumulativeLength) {
      segmentIndex = mid
      low = mid + 1
    } else {
      high = mid - 1
    }
  }

  const segment = path[segmentIndex]
  const distanceIntoSegment = normalizedDistance - segment.cumulativeLength

  if (segment.length === 0) return new Point(segment.p1.x, segment.p1.y)

  const ratio = distanceIntoSegment / segment.length

  // Linear interpolation
  const { p1, p2 } = segment
  const xDist = p2.x - p1.x
  const yDist = p2.y - p1.y
  const x = p1.x + xDist * ratio
  const y = p1.y + yDist * ratio

  return new Point(x, y)
}

/**
 * Draw a line segment along the border path at a specific offset
 * @param graphics - Graphics object to draw on
 * @param path - Array of path segments
 * @param offset - Center offset position along the path
 * @param totalPerimeter - Total length in pixel of the border path
 */
export const drawSegment = (graphics: LineGraphics, path: Path[], offset: number, totalPerimeter: number): void => {
  graphics.clear()

  const { width, length, color, alpha } = graphics.userData.lineSpec
  const lineStartOffset = offset - length / 2
  const startPoint = getPointAtDistance(path, lineStartOffset, totalPerimeter)

  if (!startPoint) return

  graphics.moveTo(startPoint.x, startPoint.y)

  /**
   * Total length in pixel => length
   * Draw a line every "DRAW_SEGMENT_EVERY_PIXEL" pixel
   * This results in how many lines (segments) to draw
   */
  const segments = Math.ceil(length / DRAW_SEGMENT_EVERY_PIXEL)

  for (let i = 1; i <= segments; i++) {
    /**
     * Every loop means the distance will be increased by "DRAW_SEGMENT_EVERY_PIXEL" * i,
     * since we draw a line of "DRAW_SEGMENT_EVERY_PIXEL" pixel in each loop
     */
    const distance = lineStartOffset + DRAW_SEGMENT_EVERY_PIXEL * i
    const point = getPointAtDistance(path, distance, totalPerimeter)
    if (!point) break
    graphics.lineTo(point.x, point.y)
  }

  graphics.stroke({ width, alpha, color })
}

/**
 * Add corner arc points to a points array
 * @param points - Array to add points to
 * @param centerX - X coordinate of arc center
 * @param centerY - Y coordinate of arc center
 * @param radius - Radius of the arc
 * @param startAngle - Starting angle in radians
 * @param endAngle - Ending angle in radians
 */
export const addCornerPoints = (
  points: Point[],
  centerX: number,
  centerY: number,
  radius: number,
  startAngle: number,
  endAngle: number
): void => {
  for (let i = 0; i <= CORNER_SEGMENTS; i++) {
    const angle = startAngle + (endAngle - startAngle) * (i / CORNER_SEGMENTS)
    const x = centerX + Math.cos(angle) * radius
    const y = centerY + Math.sin(angle) * radius
    points.push(new Point(x, y))
  }
}

/**
 * Create points for a rounded rectangle border path
 * @param width - Width of the rectangle
 * @param height - Height of the rectangle
 * @param radius - Corner radius
 * @returns Array of points representing the rounded rectangle border
 */
export const createPoints = (width: number, height: number, radius: number): Point[] => {
  const points: Point[] = []
  const left = 0
  const top = 0
  const right = width
  const bottom = height

  // The following four corners' points will be interconnected to form a rounded rectangle

  // Top right corner
  addCornerPoints(points, right - radius, top + radius, radius, -Math.PI / 2, 0)

  // Bottom right corner
  addCornerPoints(points, right - radius, bottom - radius, radius, 0, Math.PI / 2)

  // Bottom left corner
  addCornerPoints(points, left + radius, bottom - radius, radius, Math.PI / 2, Math.PI)

  // Top left corner
  addCornerPoints(points, left + radius, top + radius, radius, Math.PI, (Math.PI * 3) / 2)

  return points
}

/**
 * Create a mask graphics object for the main board with rounded corners
 * @param params - Main board parameters including width, height, and round
 * @returns Graphics object configured as a mask
 */
export const createMainBoardMask = (params: MainBoardParams): Graphics => {
  const { width, height, round } = params
  const mask = new Graphics()
  const factor = getFactor()
  mask.roundRect(0, 0, width, height, round * factor)
  mask.fill({ color: 0xffffff })
  return mask
}

/**
 * Create a graphics object for a line with specified specifications
 * @param lineSpec - Line specification including width, length, color, and alpha
 * @param segmentSpec - Segment multipliers for width and length
 * @param offset - Offset position along the path
 * @param totalPerimeter - Total length in pixel of the border path
 * @param path - Array of path segments
 * @returns LineGraphics object with drawn segment
 */
export const createSegmentGraphics = (
  lineSpec: LineSpec,
  segmentSpec: SegmentSpec,
  offset: number,
  totalPerimeter: number,
  path: Path[]
): LineGraphics => {
  const graphics = new Graphics() as LineGraphics
  const segmentWidth = lineSpec.width * segmentSpec.widthMultiplier
  const segmentLength = lineSpec.length * segmentSpec.lengthMultiplier
  graphics.userData = { lineSpec: { ...lineSpec, width: segmentWidth, length: segmentLength }, baseDegree: 0 }
  drawSegment(graphics, path, offset, totalPerimeter)
  return graphics
}

/**
 * Create a path from an array of points with cumulative length calculations
 * @param points - Array of points representing the path
 * @returns Object containing the path segments and total length in pixel of the border path
 */
export const createPath = (points: Point[]): { path: Path[]; totalPerimeter: number } => {
  const path: Path[] = []
  let calculatedPerimeter = 0
  const closedPath = [...points, points[0]]

  for (let i = 0; i < closedPath.length - 1; i++) {
    const p1 = closedPath[i]
    const p2 = closedPath[i + 1]
    const length = Math.sqrt(Math.pow(p2.x - p1.x, 2) + Math.pow(p2.y - p1.y, 2))
    path.push({ p1, p2, length, cumulativeLength: calculatedPerimeter })
    calculatedPerimeter += length
  }
  return { path, totalPerimeter: calculatedPerimeter }
}
