import { cn, isDesktop } from '@repo/utils/common/common'
import { memo } from 'react'

import Stage from './core/Stage'

type HookFunction = () => void | (() => void)

interface VisualEffectProps {
  hooks?: HookFunction[]
}

function VisualEffect({ hooks = [] }: VisualEffectProps) {
  // Execute any additional hooks passed as props
  hooks.forEach((hook) => hook())

  // Desktop's header component has an index of 10, traffic light has to be above it
  return <Stage className={cn('pointer-events-none absolute left-0 top-0', isDesktop ? 'z-[11]' : 'z-[2]')} />
}

export default memo(VisualEffect)
