import { BetOptions, BetRange, DealerPayload, GameType, TableStreams } from '@ikigaians/house'
import * as workers from 'worker-timers'
import { create } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'
import { shallow } from 'zustand/shallow'

import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { useTimerStore } from '@repo/ui/store/useTimerStore'
import { handleGamePauseToast, isInIframe } from '@repo/ui/utils/common'
import { isEnvDev } from '@repo/utils/common/common'
import { connectIns } from '@repo/utils/socket'

import { useBettingStore } from './useBettingStore'
// Enable all UI in development mode
const defaultJurisdictionValue = isEnvDev ? true : false

/**
 * Current exit/lobby requirements:
 *  In iframe:
      Exit: close iframe
      Return to lobby: close iframe
    
    In independent tab:
      Exit: Redirect to lobby URL
      Return to lobby: Redirect to lobby URL
 * So we just use the same function for both
 */
export const exitGame = () => {
  if (isInIframe()) {
    window.parent.postMessage({ type: 'REDIRECT', targetUrl: '/' }, '*')
  } else {
    const { LOBBY_URL } = useConfigStore.getState()
    if (LOBBY_URL) window.location.href = LOBBY_URL
  }
}

export type MaintenanceState = 'stopped' | 'maintenance' | ''

interface ConfigStoreState {
  GAME: string
  WS_SERVER: string
  GAME_TABLE: string
  SESSION_ID: string
  GAME_TYPE: GameType

  // Exit/Lobby ctrl
  LOBBY_URL: string | undefined

  // Jurisdiction
  JURISDICTION: {
    AUTOPLAY_ALLOWED: boolean | undefined
    FULLSCREEN_ALLOWED: boolean | undefined
    SHOW_LOBBY_ALLOWED: boolean | undefined
    SHOW_ACTIVE_SESSION_ALLOWED: boolean | undefined
    SHOW_SUPPORT_CHAT: boolean | undefined
    BET_OPTIONS: BetOptions
  }

  roundInfo: {
    roundId: string
    gameType: GameType
    gameCode: string
    betResult: boolean
    dealer: DealerPayload
  }

  betTypesLimit: Record<string, BetRange>
  tableLimit: BetRange
  tableMaxPayout: number
  isPaused: boolean
  tableFinished: boolean
  maintenanceState: MaintenanceState
  maintenanceStartTime: number
  maintenanceEndTime: number

  streams: TableStreams | null

  IS_CLOSE_SERVER: boolean
  CHAT_API_KEY: string
  IS_SMALL_DEVICE: boolean
}

interface ConfigStoreActions {
  exitGame: () => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useConfigStore`,
  store: 'ConfigStore',
  serialize: {
    options: true,
  },
}

export const useConfigStore = create<ConfigStoreState & ConfigStoreActions>()(
  devtools(
    subscribeWithSelector((_set, _get) => ({
      GAME: '',
      WS_SERVER: '',
      GAME_TABLE: '',
      SESSION_ID: '',
      GAME_TYPE: GameType.UNKNOWN,

      // Exit/Lobby ctrl
      LOBBY_URL: undefined,
      exitGame: exitGame,

      // Jurisdiction
      JURISDICTION: {
        AUTOPLAY_ALLOWED: defaultJurisdictionValue,
        FULLSCREEN_ALLOWED: defaultJurisdictionValue,
        SHOW_LOBBY_ALLOWED: defaultJurisdictionValue,
        SHOW_ACTIVE_SESSION_ALLOWED: defaultJurisdictionValue,
        SHOW_SUPPORT_CHAT: defaultJurisdictionValue,
        BET_OPTIONS: { forbidOppositeBets: true },
      },
      roundInfo: {
        gameCode: '',
        gameType: GameType.UNKNOWN,
        roundId: '',
        betResult: false,
        dealer: { name: '' },
      },
      tableLimit: { min: 0.1, max: 20000 },
      tableMaxPayout: 100000,
      betTypesLimit: {},

      isPaused: false,
      tableFinished: false,
      maintenanceState: '',
      maintenanceStartTime: 0,
      maintenanceEndTime: Infinity,
      streams: null,
      // TODO: testing
      // streams: {
      //   primary: {
      //     hd: 'https://pull-ws-test.stream.iki-utl.cc/live/sr_hd.flv',
      //     hi: 'https://pull-ws-test.stream.iki-utl.cc/live/sr_hd.flv',
      //     me: 'https://pull-ws-test.stream.iki-utl.cc/live/sr_hd.flv',
      //     lo: 'https://pull-ws-test.stream.iki-utl.cc/live/sr_hd.flv',
      //   },
      // },

      IS_SMALL_DEVICE: false,
      IS_CLOSE_SERVER: false,
      CHAT_API_KEY: '',
    })),
    DEVTOOLS_CONFIG
  )
)

useConfigStore.subscribe(
  (state) => state.isPaused,
  (isPaused) => {
    /*
     * FND-1244
     * Why here needs to checks isBet
     * 1. First enter the game => if game is paused and is not betting => show toast
     * 2. First enter the game => if game is paused and is betting => wont show toast
     * 3. If game is paused during betting => wont show toast
     */
    const { isBet } = useBettingStore.getState()
    handleGamePauseToast(isBet, isPaused)
  }
)

useConfigStore.subscribe(
  (state) => [state.maintenanceState, state.tableFinished],
  ([maintenanceState, tableFinished]) => {
    if (maintenanceState === 'maintenance' && tableFinished) {
      useNetworkStore.setState({ isMaintenance: true })
      connectIns.close()
      const timerId = useTimerStore.getState().timerId
      if (timerId !== null) {
        workers.clearInterval(timerId)
      }
    }
  },
  { equalityFn: shallow }
)
