import { cn, isMobileDevice } from '@repo/utils/common/common'

import useGlobalModalOverlay from '@repo/utils/hook/useGlobalModalOverlay'

import modalHeader from '@repo/ui/assets/modal/modalHeader.webp'
import modalHeaderAutoPlayBetChanged from '@repo/ui/assets/modal/modalHeaderAutoPlayBetChanged.webp'
import modalHeaderAutoPlayFinished from '@repo/ui/assets/modal/modalHeaderAutoPlayFinished.webp'
import modalHeaderExpiredChip from '@repo/ui/assets/modal/modalHeaderExpired.webp'
import modalHeaderIdle from '@repo/ui/assets/modal/modalHeaderIdle.webp'
import modalHeaderMultiSession from '@repo/ui/assets/modal/modalHeaderMultiSession.webp'
import PopUpButton from '../button/PopUpButton'
import './CenterModal.scss'

export type ModalType = 'autoplayFinished' | 'autoplayBetChanged' | 'expired' | 'maintenance' | 'idle' | 'multiSession'

interface CenterModalProps {
  open: boolean
  icon: ModalType
  iconDescription?: string
  title?: string
  titleClassName?: string
  text: string
  textClassName?: string
  cancelText?: string
  confirmText?: string
  onConfirm?: () => void
  onCancel?: () => void
}

const modalHeaderSrcMap: Record<ModalType, string> = {
  autoplayFinished: modalHeaderAutoPlayFinished,
  autoplayBetChanged: modalHeaderAutoPlayBetChanged,
  expired: modalHeaderExpiredChip,
  maintenance: modalHeaderMultiSession,
  idle: modalHeaderIdle,
  multiSession: modalHeaderMultiSession,
}

const getModalHeaderImage = (type: ModalType) => {
  return (
    <img
      src={modalHeaderSrcMap[type]}
      alt={`Modal header for ${type}`}
    />
  )
}

export default function CenterModal({
  open,
  icon,
  title,
  titleClassName,
  text,
  textClassName,
  cancelText,
  confirmText,
  onConfirm,
  onCancel,
}: CenterModalProps) {
  useGlobalModalOverlay(open)

  const handleConfirm = () => {
    onConfirm?.()
  }
  const handleCancel = () => {
    onCancel?.()
  }

  if (!open) return null

  return (
    <div
      id="CenterModal"
      className="absolute inset-0 z-[100]"
    >
      <div className="flex size-full flex-col items-center justify-center">
        <div
          className={cn('content w-[23.8125rem] !overflow-visible rounded-3xl px-4 pb-8 pt-[3.25rem]', {
            'w-[21rem] rounded-[2rem]': isMobileDevice,
          })}
        >
          <img
            src={modalHeader}
            alt="Modal header"
            className="absolute -top-[1.6rem] left-1/2 z-[100] h-[1.6875rem] w-[14rem] -translate-x-1/2"
          />
          <div
            className="absolute -top-7 left-1/2 z-[100] size-[5rem] -translate-x-1/2"
            role="img"
            aria-label={`${icon} icon`}
          >
            {getModalHeaderImage(icon)}
          </div>
          <div className="flex flex-col items-center gap-4">
            {title && <div className={cn('title', titleClassName)}>{title}</div>}
            <div className={cn('text', textClassName)}>{text}</div>
            <div className="flex items-center gap-3">
              {cancelText && (
                <PopUpButton
                  cancel
                  onClick={handleCancel}
                >
                  {cancelText}
                </PopUpButton>
              )}

              {confirmText && <PopUpButton onClick={handleConfirm}>{confirmText}</PopUpButton>}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
