export const currencyFormat = (val: number, currency: string = 'USD'): string => {
  // 1. Create a formatter for the requested currency
  const fmt = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency,
    currencyDisplay: 'symbol', // ensure were asking for the symbol
  })

  // 2. Format into "parts" so we can detect what was used for the currency
  const parts = fmt.formatToParts(val)
  const currencyPart = parts.find((p) => p.type === 'currency')?.value

  // 3. If the currency part equals the code (i.e. no real symbol), fallback to USD
  if (currencyPart === currency) {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(val)
  }

  // 4. Otherwise return the original formatted string
  return fmt.format(val)
}
