import { GameType } from '@ikigaians/house'

import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useDataStore } from '@repo/ui/store/useDataStore'
import { setAppComponent } from '@repo/ui/utils/componentResolver'
import '@repo/utils/common/scaleLayout'

import { defaultTableLimit, initializeCommon } from '@repo/utils/common/preinit.common'
import { titleList } from '@roulette/data/gameRules'
import payoutLimits from '@roulette/data/payoutLimits'
import GameRules from './components/layout/menu/GameRules'
import initCartStore from './initCartStore'

const searchParams = new URLSearchParams(document.location.search)

setAppComponent({ GameRulesComponent: GameRules })

useDataStore.setState({ payoutLimits, titleList })

const defaultBetTypesLimit = {
  'ro.column': { min: 0.1, max: 6000 },
  'ro.corner': { min: 0.1, max: 2000 },
  'ro.dozen': { min: 0.1, max: 6000 },
  'ro.line': { min: 0.1, max: 3000 },
  'ro.lowHigh': { min: 0.1, max: 10000 },
  'ro.oddEven': { min: 0.1, max: 10000 },
  'ro.redBlack': { min: 0.1, max: 10000 },
  'ro.split': { min: 0.1, max: 1000 },
  'ro.straight': { min: 0.1, max: 500 },
  'ro.street': { min: 0.1, max: 1500 },
}

useConfigStore.setState({
  GAME_TYPE: GameType.AUTO_ROULETTE,
  betTypesLimit: defaultBetTypesLimit,
  tableLimit: defaultTableLimit,
})

// FND-910
const TITLE_MAP = { 'ARO-001': 'CRYSTAL Speed Auto Roulette', 'ARO-002': 'CRYSTAL VIP Auto Roulette' }
const titleKey = (searchParams.get('game') as keyof typeof TITLE_MAP) ?? import.meta.env.VITE_GAME_TABLE
document.title = TITLE_MAP[titleKey] ?? 'CRYSTAL Auto Roulette'

initializeCommon({
  VITE_WS_SERVER: import.meta.env.VITE_WS_SERVER,
  VITE_GAME_TABLE: import.meta.env.VITE_GAME_TABLE,
  VITE_SESSION_ID: import.meta.env.VITE_SESSION_ID,
  VITE_ENABLE_SCALE: import.meta.env.VITE_ENABLE_SCALE,
  VITE_VERSION: import.meta.env.VITE_VERSION,
  VITE_DEPLOY_ENV: import.meta.env.VITE_DEPLOY_ENV,
  VITE_CLOSE_SERVER: import.meta.env.VITE_CLOSE_SERVER,
  MODE: import.meta.env.MODE,
})

initCartStore(useCartStore)
