import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

export interface INetworkStore {
  isConnected: boolean
  isExpired: boolean
  isMultiSession: boolean
  isIdleTimeout: boolean
  isMaintenance: boolean
  forceShowIdleModal: boolean // for QA testing purpose, can be removed in the future
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useNetworkStore`,
  store: 'NetworkStore',
  serialize: {
    options: true,
  },
}

export const useNetworkStore = create<INetworkStore>()(
  devtools(
    (_) => ({
      isConnected: false,
      isExpired: false,
      isMultiSession: false, // repeat login
      isIdleTimeout: false,
      isMaintenance: false,
      forceShowIdleModal: false,
    }),
    DEVTOOLS_CONFIG
  )
)
