import { AnimatePresence, AnimationDefinition, motion } from 'framer-motion'
import { useMemo, useState } from 'react'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { cn } from '@repo/utils/common/common'

interface MainBoardProps {
  mainBoardBound: {
    initial: {
      height: string
      width: string
      top: string
    }
    animate: {
      height: string
      width: string
      top: string
      marginLeft?: string
    }
  }
}

export default function MainBoard({ mainBoardBound }: MainBoardProps) {
  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const isCompact = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const shouldFadeOut = loadingStage < LoadingStageEnum.PostLoaded2
  const [animComplete, setAnimComplete] = useState(false)

  const onAnimationComplete = (def: AnimationDefinition) => {
    if (def !== 'animate') return
    setAnimComplete(true)
  }

  // MainBoard animation: Complex transform with variants for positioning + fade out when Loaded
  const createMainBoardAnimation = useMemo(() => {
    const baseAnimation = { initial: 'initial', variants: mainBoardBound }
    const ease = [0.1, 0, 0.2, 1]

    // Handle different loading stages
    if (loadingStage >= LoadingStageEnum.Idle) {
      // Normal animation from Idle onwards
      return {
        ...baseAnimation,
        animate: 'animate',
        transition: { duration: 1.5, ease },
      }
    } else {
      // Initial state (Init stage)
      return {
        ...baseAnimation,
        animate: 'initial',
      }
    }
  }, [loadingStage, mainBoardBound])

  return (
    <AnimatePresence>
      {shouldFadeOut && (
        <motion.div
          id="MainBoard"
          className={cn('fixed z-10', { isCompact, animComplete })}
          exit={{ opacity: 0 }}
          onAnimationComplete={onAnimationComplete}
          {...createMainBoardAnimation}
        />
      )}
    </AnimatePresence>
  )
}
