import { Connect, GameAction, GameEvent, WsEvent } from '@ikigaians/connect'
import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { getBetDetail } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { MenuPageType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useTimerStore } from '@repo/ui/store/useTimerStore'
import { DEFAULT_HISTORY_LIMIT } from './constants'
import {
  BetEventHandlers,
  ConnectionEventHandlers,
  MessageEventHandlers,
  PlayerEventHandlers,
  SessionEventHandlers,
  TableEventHandlers,
} from './handlers'

export class SocketConnectionManager {
  private connection: Connect
  private unsubscribers: (() => void)[] = []
  private gameTable: string
  private sessionId: string

  constructor(domainUrl: string, launch: string, gameTable: string, sessionId: string) {
    this.gameTable = gameTable
    this.sessionId = sessionId
    this.connection = new Connect(domainUrl, launch)
    this.setupEventHandlers()
    this.setupSubscriptions()
    this.setupLogger()
  }

  private setupEventHandlers() {
    this.connection
      .on(WsEvent.WS_OPENED, (msg) => ConnectionEventHandlers.onOpened(msg, this.connection, this.gameTable))
      .on(WsEvent.WS_CLOSED, (msg) => ConnectionEventHandlers.onClosed(msg))
      .on(WsEvent.WS_RECONNECTING, (msg) => ConnectionEventHandlers.onReconnecting(msg, this.sessionId))
      .on(WsEvent.WS_RECONNECTED, (msg) => ConnectionEventHandlers.onReconnected(msg, this.connection, this.gameTable, this.sessionId))
      .on(GameEvent.TABLE_PAUSE, (msg) => TableEventHandlers.onTablePause(msg))
      .on(GameEvent.TABLE_RESUME, (msg) => TableEventHandlers.onTableResume(msg))
      .on(GameEvent.TABLE_STATE, (msg) => TableEventHandlers.onTableState(msg, this.connection, () => this.getHistory()))
      .on(GameEvent.TABLE_START, (msg) => TableEventHandlers.onTableStart(msg, this.connection, getBetDetail))
      .on(GameEvent.TABLE_BET_STOP, (msg) => TableEventHandlers.onTableBetStop(msg))
      .on(GameEvent.TABLE_DEAL, (msg) => TableEventHandlers.onTableDeal(msg))
      .on(GameEvent.TABLE_WINNERS, (msg) => TableEventHandlers.onTableWinners(msg))
      .on(GameEvent.TABLE_FINISH, (msg) => TableEventHandlers.onTableFinish(msg, this.connection))
      .on(GameEvent.TABLE_MAINTENANCE, (msg) => TableEventHandlers.onTableMaintenance(msg))
      .on(GameEvent.SESSION_KICKED, (msg) => SessionEventHandlers.onSessionKicked(msg))
      .on(GameEvent.PLAYER_STATE, (msg) => PlayerEventHandlers.onPlayerState(msg))
      .on(GameEvent.WALLET_STATE, (msg) => PlayerEventHandlers.onWalletState(msg, () => this.getHistory()))
      .on(GameEvent.GAME_MESSAGE, (msg) => MessageEventHandlers.onGameMessage(msg))
      .on(GameEvent.SUPPORT_MESSAGE, (msg) => MessageEventHandlers.onSupportMessage(msg))
      .on(GameEvent.BET_RESULT, (msg) => {
        const currentRoundId = useConfigStore.getState().roundInfo.roundId
        BetEventHandlers.onBetResult(msg, currentRoundId)
      })
      .on(GameEvent.BET_HISTORY_LIST, (msg) => BetEventHandlers.onBetHistoryList(msg))
      .on(GameEvent.BET_HISTORY_SUBTOTAL, (msg) => BetEventHandlers.onBetHistorySubtotal(msg))
      .on(GameEvent.BET_STATS, (msg) => BetEventHandlers.onBetStats(msg))
      .on(GameEvent.SYSTEM_MESSAGE, (msg) => MessageEventHandlers.onSystemMessage(msg))
      .onElse((msg) => {
        const ev = msg
        console.debug('onElse event', JSON.stringify(ev))
      })
  }

  private setupSubscriptions() {
    // Timer subscription for betting period
    this.unsubscribers.push(
      useTimerStore.subscribe(
        (state) => state.currentMs,
        (current) => this.handleTimerUpdate(current)
      )
    )

    // Layout subscription for history
    this.unsubscribers.push(
      useLayoutStore.subscribe(
        (state) => state.menuPage,
        (type: MenuPageType) => {
          if (type === 'history') this.getHistory()
        }
      )
    )
  }

  private setupLogger() {
    this.connection.setLogger({
      info: (...args) => {
        console.log('[Connect]', ...args)
      },
      error: (...args) => {
        console.error('[Connect]', ...args)
      },
    })
  }

  private handleTimerUpdate(current: number) {
    const { isBet, betEnd } = useBettingStore.getState()
    if (isBet !== current < betEnd) {
      useBettingStore.setState({ isBet: false })
      const { handleAutoPlay } = useAutoPlayStore.getState()
      handleAutoPlay()
    }

    const { maintenanceStartTime, maintenanceEndTime } = useConfigStore.getState()
    if (maintenanceStartTime === 0) return
    if (current >= maintenanceStartTime && current < maintenanceEndTime) {
      useConfigStore.setState({ maintenanceState: 'maintenance' })
    }
  }

  private getHistory(offset?: number) {
    this.connection?.listBetHistory(DEFAULT_HISTORY_LIMIT, offset)
  }

  connect() {
    this.connection.connect()
  }

  close() {
    this.unsubscribers.forEach((unsub) => unsub())
    this.connection.send(GameAction.UNSUBSCRIBE_TABLE)
    this.connection.close()
  }

  getInstance(): Connect {
    return this.connection
  }
}
