import { BaccaratCards, Card, CardRank, CardSuit, type CardType } from '@ikigaians/house'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'

import bankerResult from '@repo/ui/assets/baccarat/result/baccaratBankerResult.webp'
import playerResult from '@repo/ui/assets/baccarat/result/baccaratPlayerResult.webp'
import tieResult from '@repo/ui/assets/baccarat/result/baccaratTieResult.webp'
import './BaccaratWinNumberResult.scss'
import MiniCard from './mini-card/MiniCard'

type CardSet = [CardType, CardType, CardType, CardType, CardType, CardType]

const createBaccaratHand = (cards: string[]): BaccaratCards => {
  const result = cards.map((card) => Card(card)) as CardSet
  return new BaccaratCards(result)
}

interface WinNumberAreaTitleProps {
  role: 'player' | 'banker'
  status: 'win' | 'lose' | 'tie'
}
function WinNumberAreaTitle({ role, status }: WinNumberAreaTitleProps) {
  const title = role === 'player' ? 'Player' : 'Banker'

  return (
    <div
      className={cn(
        'winNumberArea__title',
        'text-[0.75rem] font-medium uppercase leading-4 tracking-[0.03rem] text-[#eadfd2]',
        status === 'win' && 'text-white'
      )}
    >
      {title}
    </div>
  )
}

interface WinNumberAreaDigitProps {
  role: 'player' | 'banker'
  status: 'win' | 'lose' | 'tie'
  digit: number
}
function WinNumberAreaDigit({ status, digit, role }: WinNumberAreaDigitProps) {
  const digitContainerClasses = cn(
    'winNumberArea__digitContainer',
    role === 'player' && status === 'win' && 'winNumberArea__digitContainer--playerWin',
    role === 'player' && status === 'lose' && 'winNumberArea__digitContainer--playerLose',
    role === 'banker' && status === 'win' && 'winNumberArea__digitContainer--bankerWin',
    role === 'banker' && status === 'lose' && 'winNumberArea__digitContainer--bankerLose',
    status === 'tie' && 'winNumberArea__digitContainer--tie'
  )

  const digitTextClasses = cn(
    'winNumberArea__digitText',
    role === 'player' && status === 'win' && 'winNumberArea__digitText--playerWin',
    role === 'player' && status === 'lose' && 'winNumberArea__digitText--playerLose',
    role === 'banker' && status === 'win' && 'winNumberArea__digitText--bankerWin',
    role === 'banker' && status === 'lose' && 'winNumberArea__digitText--bankerLose',
    status === 'tie' && 'winNumberArea__digitText--tie'
  )

  return (
    <div className={digitContainerClasses}>
      <div className={digitTextClasses}>{digit}</div>
    </div>
  )
}

interface WinNumberAreaProps {
  role: 'player' | 'banker'
  status: 'win' | 'lose' | 'tie'
  digit: number
  cards: CardType[]
}
function WinNumberArea({ role, status, digit, cards }: WinNumberAreaProps) {
  const winNumberAreaClasses = cn(
    'winNumberArea',
    role === 'player' && status === 'win' && 'winNumberArea--playerWin',
    role === 'player' && status === 'lose' && 'winNumberArea--playerLose',
    role === 'banker' && status === 'win' && 'winNumberArea--bankerWin',
    role === 'banker' && status === 'lose' && 'winNumberArea--bankerLose',
    role === 'player' && status === 'tie' && 'winNumberArea--playerTie',
    role === 'banker' && status === 'tie' && 'winNumberArea--bankerTie'
  )
  return (
    <div className={winNumberAreaClasses}>
      <div className={cn('flex h-[1.5rem] items-start justify-between', role === 'banker' && 'flex-row-reverse')}>
        <WinNumberAreaTitle
          role={role}
          status={status}
        />
        <WinNumberAreaDigit
          role={role}
          status={status}
          digit={digit}
        />
      </div>
      <HistoryCardGroup
        role={role}
        cards={cards}
      />
    </div>
  )
}

interface HistoryCardGroupProps {
  role: 'player' | 'banker'
  cards: CardType[]
}
function HistoryCardGroup({ role, cards }: HistoryCardGroupProps) {
  const isThirdCard = (index: number) => index === cards.length - 1
  const isVisibleCard = (card: CardType) => card.rank !== CardRank.UNKNOWN && card.suit !== CardSuit.UNKNOWN

  return (
    <div className={cn('flex h-[2.5625rem] flex-grow items-end justify-end gap-x-[0.30356rem]', role === 'player' && 'flex-row-reverse')}>
      {cards.map((card, index) => {
        return (
          <MiniCard
            className={cn(!isVisibleCard(card) && 'opacity-0')}
            key={index}
            status="history"
            suit={card.suit}
            rank={card.rank}
            fadeInOutDirection={'up'}
            orientation={isThirdCard(index) ? 'horizontal' : 'vertical'}
            scaleFactor={29.1 / 45}
          />
        )
      })}
    </div>
  )
}

interface ResultBadgeProps {
  result: 'P' | 'B' | 'T'
}

const RESULT_BADGE_CONFIG: Record<ResultBadgeProps['result'], { icon: React.ReactNode }> = {
  P: {
    icon: (
      <img
        src={playerResult}
        alt="Player Result"
      />
    ),
  },
  B: {
    icon: (
      <img
        src={bankerResult}
        alt="Banker Result"
      />
    ),
  },
  T: {
    icon: (
      <img
        src={tieResult}
        alt="Tie Result"
      />
    ),
  },
} as const

function ResultBadge({ result }: ResultBadgeProps) {
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const config = RESULT_BADGE_CONFIG[result]
  if (!config) return null

  const { icon } = config
  const resultBadgeClasses = cn(
    'flex items-center justify-center size-[1.40625rem] shrink-0 aspect-square',
    isMobileDevice && isSmallDevice && 'size-[1.17188rem]'
  )
  return <div className={resultBadgeClasses}>{icon}</div>
}

interface BaccaratWinNumberResultProps {
  winNumber: string[]
}
export default function BaccaratWinNumberResult({ winNumber }: BaccaratWinNumberResultProps) {
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const baccaratHand = createBaccaratHand(winNumber)
  const isPlayerWin = baccaratHand.isPlayerWin()
  const isBankerWin = baccaratHand.isBankerWin()
  const isTie = baccaratHand.isTie()

  const resultIcon = isTie ? <ResultBadge result="T" /> : isPlayerWin ? <ResultBadge result="P" /> : <ResultBadge result="B" />
  const resultText = isTie ? 'tie' : isPlayerWin ? 'player win' : 'banker win'

  const playerStatus = isTie ? 'tie' : isPlayerWin ? 'win' : 'lose'
  const playerDigit = baccaratHand.sumPlayer()
  const bankerStatus = isTie ? 'tie' : isBankerWin ? 'win' : 'lose'
  const bankerDigit = baccaratHand.sumBanker()

  const playerCards = baccaratHand.playerCards
  const bankerCards = baccaratHand.bankerCards

  return (
    <div id="BaccaratWinNumberResult">
      <div className={cn('my-[2rem] h-[8.13rem] w-full', isMobileDevice && isSmallDevice && 'my-4 h-[7.625rem]')}>
        <div
          className={cn(
            'h-[2.9375rem] rounded-tl-[0.75rem] rounded-tr-[0.75rem] border border-solid border-[#bf9e77] p-[0.75rem_1rem]',
            'winNumberResultHeader',
            isPlayerWin && 'winNumberResultHeader--playerWin',
            isBankerWin && 'winNumberResultHeader--bankerWin',
            isTie && 'winNumberResultHeader--tie',
            isMobileDevice && isSmallDevice && 'h-[2.4375rem] p-0'
          )}
        >
          <div className={cn('flex h-[1.5rem] items-center justify-center gap-x-[0.56rem]', isMobileDevice && isSmallDevice && 'h-full')}>
            {resultIcon}
            <div
              className={cn(
                'winNumberResultHeader__resultTitle',
                isMobileDevice && isSmallDevice && 'winNumberResultHeader__resultTitle--sm'
              )}
            >
              {resultText}
            </div>
          </div>
        </div>
        <div className={cn('flex shrink-0 items-start self-stretch')}>
          <WinNumberArea
            role="player"
            status={playerStatus}
            digit={playerDigit}
            cards={playerCards}
          />
          <WinNumberArea
            role="banker"
            status={bankerStatus}
            digit={bankerDigit}
            cards={bankerCards}
          />
        </div>
      </div>
    </div>
  )
}
