import { useCartStore } from '@repo/ui/store/useCartStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

interface IAutoPlayStore {
  isAutoPlay: boolean
  boardSnapshot: Record<string, number> | null
  autoPlayResumed: boolean

  roundLastChosen: number | null
  roundSelected: number | null
  roundRemaining: number

  confirmModalType: 'BetChanged' | 'AutoPlayFinished' | null
  isHaveBets: () => boolean
  checkAutoPlay: () => boolean
  cancelAutoPlay: () => void
  handleAutoPlay: () => void
  decrementAutoPlayRound: () => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useAutoPlayStore`,
  store: 'AutoPlayStore',
  serialize: {
    options: true,
  },
}

export const useAutoPlayStore = create<IAutoPlayStore>()(
  devtools(
    (set, get) => ({
      isAutoPlay: false,
      boardSnapshot: null,
      autoPlayResumed: true,

      roundLastChosen: null,
      roundSelected: null,
      roundRemaining: 0,

      confirmModalType: null,
      isHaveBets: () => useCartStore.getState().boardHistory.length > 0,
      checkAutoPlay: () => {
        const { isAutoPlay, cancelAutoPlay } = get()
        if (!isAutoPlay) return false

        const { totalBet, remainedBalance } = useCartStore.getState()
        const { addToast } = useToastStore.getState()
        const isBalanceEnough = remainedBalance - totalBet >= 0
        if (!isBalanceEnough) {
          cancelAutoPlay()
          addToast('info', 'Autoplay has been deactivated, because of low balance')
          return false
        }

        return true
      },
      decrementAutoPlayRound: () => {
        const { isAutoPlay, roundRemaining } = get()
        if (!isAutoPlay) return
        set({ roundRemaining: roundRemaining - 1 })
      },
      handleAutoPlay: () => {
        const { boardSnapshot, isAutoPlay, roundRemaining, cancelAutoPlay } = get()
        if (!isAutoPlay) return

        if (roundRemaining === 0) {
          cancelAutoPlay()
          set({ confirmModalType: 'AutoPlayFinished' })
          return
        } else {
          // if chipList changed, confirm to continue
          const { boardHistory } = useCartStore.getState()
          const isSame = JSON.stringify(boardHistory[boardHistory.length - 1]) === JSON.stringify(boardSnapshot)
          if (!isSame) set({ confirmModalType: 'BetChanged' })
        }
      },
      cancelAutoPlay: () => {
        set({
          confirmModalType: null,
          isAutoPlay: false,
          roundSelected: null,
          roundRemaining: 0,
        })
      },
    }),
    DEVTOOLS_CONFIG
  )
)
