import { tooltipEventBus } from '@repo/ui/store/tooltipEvent'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { isMobileDevice } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import { BetState } from '@repo/utils/types/betstate'

import { getBetDiff } from '@repo/ui/utils/bet'
import { getBetLimit } from './service/bet'
import { BetTypeEnum, raceTrackGroup } from './types/betType'

export default function initCartStore(cartStore: typeof useCartStore) {
  if (useConfigStore.getState().IS_CLOSE_SERVER) {
    useBettingStore.setState({ isBet: true, gameState: BetState.Bet })
    useCartStore.setState({ walletBalance: 10000 })
  }

  cartStore.setState({
    handleDouble: (e: React.MouseEvent) => {
      const pointerPosition = { x: e.pageX, y: e.pageY }
      const isBet = useBettingStore.getState().isBet
      if (!isBet) {
        tooltipEventBus.emit('close', { anchorSelect: '#doubleRepeatBtn' })
        return
      }
      const { walletBalance, boardHistory } = useCartStore.getState()
      const tableLimit = useConfigStore.getState().tableLimit
      const latest = boardHistory.at(-1)
      if (!latest) return
      // An array of bet types that exceed the limit for emitting the tooltip
      const overLimitBets: string[] = []
      const latestEntries = Object.entries(latest) as [BetTypeEnum, number][]
      const doubled = latestEntries.reduce(
        (acc, pair) => {
          const [key, value] = pair
          const doubledValue = value * 2
          const limitedValue = getBetLimit([key, doubledValue] as [BetTypeEnum, number])
          if (typeof limitedValue === 'number') {
            overLimitBets.push(key)
            acc[key] = limitedValue
          } else {
            acc[key] = doubledValue
          }
          return acc
        },
        {} as Record<string, number>
      )

      const { getTotalBet } = useCartStore.getState()
      const doubledBets = getTotalBet([doubled])
      if (doubledBets > walletBalance) {
        tooltipEventBus.emit('lowBalance', { position: isMobileDevice ? undefined : pointerPosition })
        return
      }
      if (doubledBets > (tableLimit?.max ?? 0)) {
        tooltipEventBus.emit('overLimit', { anchorSelect: '#doubleRepeatBtn' })
        return
      }
      if (overLimitBets.length === latestEntries.length) {
        tooltipEventBus.emit('overLimit', { anchorSelect: '#doubleRepeatBtn' })
      }
      playSound(['double'])
      const diff = getBetDiff(latest, doubled)
      if (Object.keys(diff).length < 1) return
      useCartStore.setState({
        latestPlaceBet: diff,
        boardHistory: [...boardHistory, doubled],
        playChipSound: false,
      })
    },
    package: () => {
      const { boardHistory } = useCartStore.getState()
      const latest = boardHistory[boardHistory.length - 1]
      if (!latest) return new Map()
      const filtered = Object.entries(latest).filter(([k]) => !raceTrackGroup.includes(k as BetTypeEnum))
      return new Map(filtered)
    },
    canDouble: (curBets: Record<BetTypeEnum, number>): boolean => {
      if (!curBets) return false

      const curEntries = Object.entries(curBets) as [BetTypeEnum, number][]
      const { walletBalance } = useCartStore.getState()
      const tableLimit = useConfigStore.getState().tableLimit

      // Check if the doubled all bets is under the wallet balance
      const doubled = curEntries.reduce(
        (acc, pair) => {
          const [key, value] = pair
          const doubledValue = value * 2
          const limitedValue = getBetLimit([key, doubledValue] as [BetTypeEnum, number])
          if (typeof limitedValue === 'number') {
            acc[key] = limitedValue
          } else {
            acc[key] = doubledValue
          }
          return acc
        },
        {} as Record<string, number>
      )

      const { getTotalBet } = useCartStore.getState()
      const doubledBets = getTotalBet([doubled])
      if (doubledBets > walletBalance) return false
      if (doubledBets > (tableLimit?.max ?? 0)) return false

      // Check if the bet is under the limit (limit is undefined or amount is under the limit)
      for (const pair of curEntries) {
        const [, amount] = pair
        const limit = getBetLimit(pair, 2)
        if (typeof limit !== 'number' || amount < limit) {
          return true
        }
      }

      return false
    },
  })
}
