import { cn } from '@repo/utils/common/common'
import React from 'react'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { isMobileDevice } from '@repo/utils/common/common'

import { useLoadingCalculations } from './hooks/useLoadingCalculations'
import LoadingOverlay from './LoadingOverlay'
import './LoadingScreen.scss'
import MainBoard from './MainBoard'

export type LoadingResources = {
  headerWithoutWinHistory?: boolean
  desktopBG: string
  mobileBG: string
  topIcon: React.ReactNode
  gameIcon: React.ReactNode
  bottomIcon: React.ReactNode
  gameIconDissolve?: string
  translationKeys?: {
    percent10: string
    percent80: string
  }
  loadingTextOptions?: readonly string[] // Prop for random text selection
  gameId: 'roulette' | 'sicbo' | 'baccarat'
}

export default function LoadingScreen({ resources }: { resources: LoadingResources }) {
  const {
    headerWithoutWinHistory = false,
    loadingTextOptions,
    desktopBG,
    mobileBG,
    topIcon,
    gameIcon,
    bottomIcon,
    gameIconDissolve,
    gameId,
  } = resources

  const isSmallDevice = useConfigStore.getState().IS_SMALL_DEVICE
  const loadingStage = useLoadingStore((s) => s.loadingStage)

  // Use the custom hook for all layout calculations
  const { mainBoardBound, loadingOverlayAnimation, boundedStyle } = useLoadingCalculations(headerWithoutWinHistory)
  const shouldShowMainBoard = loadingStage < LoadingStageEnum.PostLoaded3

  return (
    <div
      id="LoadingScreen"
      className={cn({ mb: isMobileDevice, sd: isSmallDevice, 'z-[1]': shouldShowMainBoard })}
    >
      <LoadingOverlay
        loadingOverlayAnimation={loadingOverlayAnimation}
        boundedStyle={boundedStyle}
        desktopBG={desktopBG}
        mobileBG={mobileBG}
        topIcon={topIcon}
        gameIcon={gameIcon}
        bottomIcon={bottomIcon}
        gameIconDissolve={gameIconDissolve}
        gameId={gameId}
        loadingTextOptions={loadingTextOptions}
      />

      {shouldShowMainBoard && <MainBoard mainBoardBound={mainBoardBound} />}
    </div>
  )
}
