import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useEffect } from 'react'

export default function useModalOverlay(isOpen: boolean) {
  const increment = useLayoutStore((s) => s.incrementModalCount)
  const decrement = useLayoutStore((s) => s.decrementModalCount)

  useEffect(() => {
    if (isOpen) {
      increment()

      return () => {
        decrement()
      }
    }
  }, [isOpen, increment, decrement])
}
