import { GameEvent, WsEvent, WsMessage } from '@ikigaians/connect'
import * as Sentry from '@sentry/react'

type User = {
  sid?: string
}

export const traceWsMessageEvent = <T extends GameEvent | WsEvent>(name: GameEvent | WsEvent, msg?: WsMessage<T>, user?: User) =>
  Sentry.startSpan({ name, op: 'websocket.event' }, (span) => {
    try {
      span.setAttribute('sid', user?.sid || 'no sid provided')
      span.setAttribute('msg', JSON.stringify(msg || 'no message provided'))
    } catch (error) {
      console.warn('Failed to serialize WebSocket message for tracing:', error)
      span.setAttribute('serialization_error', true)
    }
    return span
  })
