import { create } from 'zustand'
import { subscribeWithSelector } from 'zustand/middleware'

export interface ITimerStore {
  currentMs: number
  currentSec: number
  refresh: (val: number) => void
  addTimer: (timerId: string, endTimeInSec: number, callback: () => void) => void
  removeTimer: (timerId: string) => void
  timerId: number | null
  timers: Array<{ id: string; endTimeInSec: number; callback: () => void }>
}

export const useTimerStore = create<ITimerStore>()(
  // Adding devtools will cause the Redux extension to crash
  subscribeWithSelector((set) => ({
    currentMs: 0,
    currentSec: 0,
    timerId: null,
    timers: [],

    refresh: (timestamp: number) =>
      set({
        currentMs: timestamp,
        currentSec: Math.ceil(timestamp / 1000),
      }),

    addTimer: (timerId: string, endTimeInSec: number, callback: () => void) => {
      if (useTimerStore.getState().timers.find((t) => t.id === timerId)) {
        throw new Error(`Timer with id ${timerId} already exists`)
      }
      set((state) => ({
        ...state,
        timers: [...state.timers, { id: timerId, endTimeInSec, callback }],
      }))
    },
    removeTimer: (timerId: string) =>
      set((state) => ({
        ...state,
        timers: state.timers.filter((timer) => timer.id !== timerId),
      })),
  }))
)

