import { SicBoBetNo } from '@ikigaians/house'

export const chooseDomeType = (betResult: number[]) => {
  if (new Set(betResult).size === 1) {
    return 'triple'
  }
  if (betResult.reduce((a, c) => a + c, 0) < 11) {
    return 'small'
  }
  return 'big'
}

export const getSicBoNumberColor = (numbers: number[]) => {
  if (new Set(numbers).size === 1) return 'green'

  const total = numbers.reduce((a, b) => a + b, 0)
  return total < 11 ? 'blue' : 'red'
}

