import { useTranslation } from 'react-i18next'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getSicBoNumberColor } from '@repo/ui/utils/types/betType.sicbo'

import { cn } from '@repo/utils/common/common'
import Dome from './you-win/Dome'
import ResultDices from './you-win/ResultDices'

function WinNumberResult({ winNumber }: { winNumber: number[] }) {
  const { t } = useTranslation()
  const color = getSicBoNumberColor(winNumber)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  return (
    <div className="relative">
      <div
        className={cn(
          'g-winNumberFrame',
          'my-8 flex h-[8.625rem] flex-col items-center justify-start overflow-visible p-[0.6rem_4rem_0rem_4rem]',
          color
        )}
      >
        <h1>{t('COMMON.GAME_RESULT')}</h1>
        <div className={cn(isSmallDevice && 'w-[12.5rem]')}>
          <WinNumber win={winNumber} />
        </div>
      </div>
    </div>
  )
}
export default WinNumberResult

export const WinNumber = (props: { win: number[] }) => {
  return (
    <div className="absolute bottom-[-1.7rem] left-1/2 -translate-x-1/2">
      <Dome
        betResult={props.win}
        className="scale-[0.85]"
      />
      <ResultDices
        betResult={props.win}
        className="absolute bottom-[0.5rem] left-[50%] z-[2] flex -translate-x-1/2 scale-[0.85] gap-[0.4rem]"
      />
    </div>
  )
}
