import { DealerPayload } from '@ikigaians/house'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { handleGamePauseToast } from '@repo/ui/utils/common'
import { BetState } from '@repo/utils/types/betstate'
import { create } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'
import { useConfigStore } from './useConfigStore'

export type BetModeType = 'default' | 'straight' | 'fullComplete'

export interface IBettingStore {
  isRaceTrack: boolean
  isBet: boolean
  neighborRange: number
  betPeriod: number
  betStart: number
  betEnd: number
  gameState: BetState
  betMode: BetModeType
  isOverLimit: boolean
  dealer: DealerPayload
  chipSequence: number
  resetBoard: boolean // TODO: can be removed when betpeek is no longer used
  /**
   * Flag to indicate when bets are being retrieved from activeRoundBets
   */
  isRetrieveBet: boolean
  getGameState: () => {
    gameState: BetState
    isBet: boolean
    isWheelSpin: boolean
    isSettlement: boolean
  }
  toggleRaceTrack: () => void
  stopCountdown: () => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useBettingStore`,
  store: 'BettingStore',
  serialize: {
    options: true,
  },
}

export const useBettingStore = create<IBettingStore>()(
  devtools(
    subscribeWithSelector((set, get) => ({
      isRaceTrack: false,
      isBet: false,
      neighborRange: 2,
      betPeriod: 0,
      betStart: 0,
      betEnd: 0,
      gameState: BetState.Unknown,
      roundStart: 0,
      betMode: 'default',
      isOverLimit: false,
      dealer: { name: '' },
      isAutoPlay: false,
      autoPlayRound: 0,
      chipSequence: 1,
      resetBoard: true,
      isRetrieveBet: false,
      getGameState: () => {
        const { gameState, isBet } = get()
        return {
          gameState,
          isBet,
          isWheelSpin: gameState === BetState.WheelSpin,
          isSettlement: gameState === BetState.Settlement,
        }
      },
      toggleRaceTrack: () => set(({ isRaceTrack }) => ({ isRaceTrack: !isRaceTrack })),
      stopCountdown: () => {
        const isBet = get().isBet
        if (isBet) set({ betEnd: Date.now() })
      },
    })),
    DEVTOOLS_CONFIG
  )
)

useBettingStore.subscribe(
  (state) => state.gameState,
  (gameState) => {
    const { enterBet, enterWheelSpin, enterSettlement } = useCartStore.getState()

    if (gameState === BetState.Bet) enterBet()
    if (gameState === BetState.WheelSpin) enterWheelSpin()
    if (gameState === BetState.Settlement) enterSettlement()
  }
)

useBettingStore.subscribe(
  (state) => state.isBet,
  (isBet) => {
    /*
     * FND-1244
     * Why here needs to checks isPause
     * When bet's closed => if game is paused => show toast
     */
    const { isPaused } = useConfigStore.getState()
    handleGamePauseToast(isBet, isPaused)

    if (!isBet) useLayoutStore.setState({ isOpenSelector: false })
  }
)
