import { Connect } from '@ikigaians/connect'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getBaseDomain } from '@repo/ui/utils/common'
import { SocketConnectionManager } from './connection-manager'

export let connectIns: Connect
let connectionManager: SocketConnectionManager | null = null

export const connectWS = (force = false) => {
  if (connectIns && !force) {
    console.debug('WebSocket connection already exists.')
    return
  }

  // Cleanup old connection if force reconnecting
  if (force && connectionManager) {
    connectionManager.close()
  }

  const { GAME_TABLE, IS_CLOSE_SERVER, SESSION_ID } = useConfigStore.getState()
  if (IS_CLOSE_SERVER) {
    console.debug('Server connection is disabled ')
    return
  }

  const domainUrl = getBaseDomain()
  const launch = SESSION_ID ? domainUrl + '/?sid=' + SESSION_ID : ''

  connectionManager = new SocketConnectionManager(domainUrl, launch, GAME_TABLE, SESSION_ID)
  connectIns = connectionManager.getInstance()
  connectionManager.connect()

  // Cleanup on page unload
  window.onbeforeunload = function () {
    if (connectionManager) {
      connectionManager.close()
    }
  }
}

// Re-export types and constants for backward compatibility
export * from './constants'
export * from './types'
