import { cn } from '@repo/utils/common/common'
import { AnimatePresence, motion } from 'framer-motion'
import { useCallback, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'

import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { isMobileDevice } from '@repo/utils/common/common'

// Constants for better maintainability
const MESSAGE_CHANGE_INTERVAL = 1000 // milliseconds

// shuffle algorithm for uniform randomization
const shuffleArray = (array: readonly string[]): string[] => {
  const shuffled = [...array]
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1))
    ;[shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]]
  }
  return shuffled
}

interface LoadingMessagesProps {
  loadingTextOptions?: readonly string[]
}

export default function LoadingMessages({ loadingTextOptions }: LoadingMessagesProps) {
  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const { t } = useTranslation()
  const [currentMessage, setCurrentMessage] = useState<string>('')
  const poolRef = useRef<string[]>([])

  // Simple draw from shuffled pool
  const drawOne = useCallback((): string | null => {
    return poolRef.current.pop() || null
  }, [])

  // Show next random message
  const showRandomMessage = useCallback(() => {
    let message = drawOne()

    // Refill and shuffle pool when empty, then try again
    if (!message && loadingTextOptions?.length) {
      poolRef.current = shuffleArray(loadingTextOptions)
      message = drawOne()
    }

    if (message) {
      setCurrentMessage(t(message))
    }
  }, [drawOne, loadingTextOptions, t])

  useEffect(() => {
    const shouldShowMessages = loadingTextOptions?.length && loadingStage >= LoadingStageEnum.Loading

    if (!shouldShowMessages) {
      return
    }

    // Initialize and shuffle pool using Fisher-Yates algorithm
    poolRef.current = shuffleArray(loadingTextOptions)

    // Show first message immediately
    showRandomMessage()

    // Set up interval for subsequent messages
    const interval = setInterval(showRandomMessage, MESSAGE_CHANGE_INTERVAL)

    return () => clearInterval(interval)
  }, [loadingStage, loadingTextOptions, showRandomMessage])

  // Early return for conditional rendering
  const shouldRender = loadingTextOptions?.length && loadingStage >= LoadingStageEnum.Loading

  if (!shouldRender) return null

  const fontSize = isMobileDevice ? '0.7rem' : '1rem'

  return (
    <div className={cn('loadingMessages')}>
      <AnimatePresence mode="wait">
        <motion.div
          key={currentMessage}
          className="loadingMessage"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -20 }}
          transition={{ duration: 0.4, ease: [0.25, 0, 0.25, 1] }}
          style={{ fontSize }}
        >
          {currentMessage}
        </motion.div>
      </AnimatePresence>
    </div>
  )
}
