import { Connect, GameAction, WsEvent } from '@ikigaians/connect'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { LoadingResourceEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { traceWsMessageEvent } from '@repo/utils/common/trace'
import { WsClosedEvent, WsOpenedEvent, WsReconnectedEvent, WsReconnectingEvent } from '../types'

export class ConnectionEventHandlers {
  static onOpened(_msg: WsOpenedEvent, connectIns: Connect, gameTable: string) {
    useNetworkStore.setState({ isConnected: true })
    connectIns.send(GameAction.SUBSCRIBE_TABLE, { gameCode: gameTable })

    const decrementResources = useLoadingStore.getState().decrementResources
    decrementResources(LoadingResourceEnum.ConnectToRGS)

    console.debug('connect to RGS')
    useDebugStore.getState().setMsg(`${WsEvent.WS_OPENED}`)
  }

  static onClosed(_msg: WsClosedEvent) {
    // inclining reconnecting and kicked event
    useDebugStore.getState().setMsg(`${WsEvent.WS_CLOSED}`)
  }

  static onReconnecting(msg: WsReconnectingEvent, sessionId: string) {
    traceWsMessageEvent(WsEvent.WS_RECONNECTING, msg, { sid: sessionId })
    console.debug(WsEvent.WS_RECONNECTING, msg)
    useNetworkStore.setState({ isConnected: false })
    useDebugStore.getState().setMsg(`${WsEvent.WS_RECONNECTING}`)
  }

  static onReconnected(msg: WsReconnectedEvent, connectIns: Connect, gameTable: string, sessionId: string) {
    traceWsMessageEvent(WsEvent.WS_RECONNECTED, msg, { sid: sessionId })
    console.debug(WsEvent.WS_RECONNECTED, msg)
    connectIns.syncWallet()
    useNetworkStore.setState({ isConnected: true })
    connectIns.send(GameAction.SUBSCRIBE_TABLE, { gameCode: gameTable })
    useDebugStore.getState().setMsg(`${WsEvent.WS_RECONNECTED}`)
  }
}
