import { useTranslation } from 'react-i18next'
import { useMatch } from 'react-router-dom'

import { GameType, TableRoundPayload, TableRoundStatus } from '@ikigaians/house'
import BaccaratWinNumberResult from '@repo/ui/components/baccarat/BaccaratWinNumberResult'
import WinNumberResult from '@repo/ui/components/roulette/RouletteWinNumberResult'
import SicboWinNumberResult from '@repo/ui/components/sicbo/SicboWinNumberResult'
import { Icon } from '@repo/ui/icon'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isBaccaratGameType, isRouletteGameType, isSicboGameType } from '@repo/ui/utils/common'
import { scrollWrapToTop } from '@repo/ui/utils/misc'
import { betTypeMap as baccaratBetTypeMap } from '@repo/ui/utils/types/betType.baccarat'
import { betTypeMap as rouletteBetTypeMap } from '@repo/ui/utils/types/betType.roulette'
import { betTypeMap as sicboBetTypeMap } from '@repo/ui/utils/types/betType.sicbo'

import { cn, getFormattedRoundId, isMobileDevice } from '@repo/utils/common/common'
import { currencyFormat } from '@repo/utils/common/currency'
import { playSound } from '@repo/utils/common/sound'
import { connectIns } from '@repo/utils/socket'

import GameHistoryResult from './GameHistoryResult'

const gameTypeMap: Record<string, string> = {
  [GameType.AUTO_ROULETTE]: 'AUTO ROULETTE',
  [GameType.ROULETTE]: 'ROULETTE',
  [GameType.AUTO_SIC_BO]: 'AUTO SIC BO',
  [GameType.SIC_BO]: 'SIC BO',
  [GameType.BACCARAT]: 'LIVE BACCARAT',
}

export function GameNumber(roundCreatedAt: number) {
  return `#${new Date(roundCreatedAt).toLocaleString('en-US', {
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: false,
    timeZone: 'UTC',
  })}`
}

export function GameHistoryContentHeader() {
  const { histories, currentHistoryContent: current } = useHistoryStore()
  const isHistoryContentRoute = useMatch('/history-content')

  const changePage = (page: number) => {
    const idx = histories.findIndex((item) => current && item.id === current.id)
    const target = histories.at((idx + page) % histories.length)
    useHistoryStore.setState({ currentHistoryContent: target })
    scrollWrapToTop('history_content')
    playSound(['click'])
  }

  return isHistoryContentRoute ? null : (
    <div id="GameHistoryTypeHeader">
      <div className="header relative h-8 w-[9.1875rem]">
        <Icon.RightGoldenDarkerBottom
          style={{ transform: 'rotateY(180deg)' }}
          onClick={() => changePage(-1)}
          className="absolute left-2 w-4 cursor-pointer"
        />
        <h1>{GameNumber(current?.roundCreatedAt ?? 0)}</h1>
        <Icon.RightGoldenDarkerBottom
          onClick={() => changePage(1)}
          className="absolute right-2 w-4 cursor-pointer"
        />
      </div>
    </div>
  )
}

export default function GameHistoryContent() {
  const { t } = useTranslation()
  const { currentHistoryContent: current } = useHistoryStore()
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const isHistoryContentRoute = useMatch('/history-content')

  if (!current) return null

  const { roundId, result: betResults, winMax } = current
  const { status, gameType, gameCode, roundCreatedAt, roundFinishedAt } = current
  const { bets, payouts, betTotal, winTotal, currency } = current

  const result = Object.values(betResults ?? {})[0]
  const isFinished = status === 'finished'
  const isMax = winMax && current.winTotal >= winMax

  const handleReport = () => {
    if (!current || !roundCreatedAt || !roundFinishedAt) return

    const data: TableRoundPayload = {
      roundId: roundId,
      createdAt: roundCreatedAt,
      finishedAt: roundFinishedAt,
      status: status as TableRoundStatus,
      result: betResults,
      gameCode,
      gameType: gameType,
    }
    connectIns.sendSupportChatGameHistory(data)
    useLayoutStore.setState({ menuPage: 'chat_support', desktopModal: 'support' })
    playSound(['click'])
  }

  return (
    roundCreatedAt &&
    roundFinishedAt && (
      <div
        id="GameHistoryContent"
        className={cn('flex flex-col justify-center px-4 pt-2', {
          'pt-3': isMobileDevice,
        })}
      >
        <div className="flex gap-4">
          <div className="flex flex-grow flex-col gap-2">
            <div className="g-subTitle-1">{gameTypeMap[gameType]}</div>
            <div
              className={cn('inline-flex gap-x-1', {
                'flex-col items-start': isMobileDevice && isSmallDevice,
              })}
            >
              <span className="item">{t('COMMON.GAME_RESOLVED')}:</span>
              <span className="content">
                {new Date(roundFinishedAt).toLocaleString('en-US', {
                  year: 'numeric',
                  month: '2-digit',
                  day: '2-digit',
                  hour: '2-digit',
                  minute: '2-digit',
                  second: '2-digit',
                  hour12: true,
                  timeZone: 'UTC',
                })}
              </span>
            </div>
            <div className={cn('inline-flex gap-x-1', { 'flex-col items-start': isMobileDevice && isSmallDevice })}>
              <span className="item">{t('COMMON.GAME_NUMBER')}:</span>
              <span className="content">{`#${getFormattedRoundId(roundId)}`}</span>
            </div>
          </div>

          {!isHistoryContentRoute && (
            <div className="flex flex-col items-center justify-items-center gap-[0.375rem]">
              <button
                className="h-[2.5rem] w-[2.5rem]"
                onClick={handleReport}
              >
                <div className="inner grid items-center justify-center">
                  <Icon.Support className="h-[1.25rem] w-[1.25rem]" />
                </div>
              </button>
              <div className="g-subTitle-1 text-[0.625rem] leading-3 tracking-[.0938rem]">{t('COMMON.SUPPORT')}</div>
            </div>
          )}
        </div>
        {!isFinished ? (
          <div className="g-winNumberFrame cancel my-8 flex h-[8.625rem] items-center justify-center">
            <b>{t('COMMON.GAME_CANCELED')}</b>
          </div>
        ) : (
          <>
            {isRouletteGameType(gameType) && <WinNumberResult winNumber={result} />}
            {isSicboGameType(gameType) && <SicboWinNumberResult winNumber={result} />}
            {isBaccaratGameType(gameType) && <BaccaratWinNumberResult winNumber={result} />}
          </>
        )}
        <table>
          <thead>
            <tr className="headerRow">
              <th className="text-left">{t('COMMON.BET')}</th>
              <th className="text-right">{t('COMMON.TOTAL_BET')}</th>
              <th className="w-10 pl-[1.85rem] text-left">{t('COMMON.RESULT')}</th>
            </tr>
          </thead>
          <tbody>
            {Object.keys(bets).map((k, idx) => {
              const key = k as keyof typeof bets
              const val = bets[key] ?? 0
              const payout = payouts?.[key] ?? 0
              const win = isFinished ? payout : val

              return (
                <tr
                  key={idx}
                  className="subtotalRow"
                >
                  <td className="text-left text-[#EADFD2]">
                    {isRouletteGameType(current.gameType) && rouletteBetTypeMap[key as keyof typeof rouletteBetTypeMap]}
                    {isSicboGameType(current.gameType) && sicboBetTypeMap[key as keyof typeof sicboBetTypeMap]}
                    {isBaccaratGameType(current.gameType) && baccaratBetTypeMap[key as keyof typeof baccaratBetTypeMap]}
                  </td>
                  <td className="text-right text-[#B0B0B0]">{currencyFormat(val, currency)}</td>
                  <td className="text-right">
                    <GameHistoryResult
                      status={status}
                      result={win}
                      currency={currency}
                    />
                  </td>
                </tr>
              )
            })}
            {isMax && (
              <tr className="maxWinRow">
                <td
                  colSpan={3}
                  className="text-center"
                >
                  <div className="maxWin">MAXWIN IS REACHED</div>
                </td>
              </tr>
            )}
            <tr className="divider">
              <td colSpan={3}>
                <div className="border-t border-solid border-t-[#323232]"></div>
              </td>
            </tr>
            <tr className="totalRow">
              <td className="total text-left uppercase">{t('COMMON.TOTAL')}</td>
              <td className="text-right text-[#B0B0B0]">{currencyFormat(betTotal, currency)}</td>
              <td className="text-right">
                <GameHistoryResult
                  status={status}
                  result={
                    // For cancelled games, show the bet amount as the result since no win/loss occurred
                    isFinished ? winTotal : betTotal
                  }
                  currency={currency}
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  )
}
