import { GenericBetNo } from '@ikigaians/house'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { CallBetsEnum, raceTrackGroup, raceTrackGroupMap } from '@repo/ui/utils/types/betType.roulette'
import { FullBetType } from '@repo/utils/hook/useBettingBoard'
import Decimal from 'decimal.js-light'

export const cleanExtraBets = (history: Record<string, number>) => {
  const cleanedBets = { ...history }
  const extraBets: Record<string, number> = {}
  Object.keys(cleanedBets).forEach((key) => {
    if (raceTrackGroup.includes(key as CallBetsEnum)) {
      extraBets[key] = cleanedBets[key]
      delete cleanedBets[key]
    }
  })
  return { cleanedBets, extraBets }
}
export const addExtraBets = (historyExtraBets: Record<string, number>, diffExtraBets?: Record<string, number>) => {
  const result: Record<string, number> = { ...historyExtraBets }

  const limit = getRaceTrackGroupLimit()

  if (diffExtraBets) {
    Object.keys(diffExtraBets).forEach((key) => {
      const existingAmount = new Decimal(result[key] || 0)
      const diffAmount = new Decimal(diffExtraBets[key])
      const betLimit = new Decimal(limit[key] ?? Number.MAX_SAFE_INTEGER)
      const newTotal = existingAmount.plus(diffAmount)
      result[key] = Math.min(newTotal.toNumber(), betLimit.toNumber())
    })
  }
  return result
}

export const getRaceTrackGroupLimit = () => {
  const limit = {} as Record<string, number>
  const betTypesLimit = useConfigStore.getState().betTypesLimit
  if (!Object.keys(betTypesLimit).every((v) => v.startsWith('ro.'))) return limit

  raceTrackGroup.forEach((betType) => {
    const filteredGroup = raceTrackGroupMap[betType].filter((item) => !item.includes('straight'))
    //ARO-254: Remove duplicates in the group to avoid counting the limit multiple times
    new Set(filteredGroup).forEach((item) => {
      limit[betType] = (limit[betType] ?? 0) + (betTypesLimit[`ro.${item.split('-')[0]}`]?.max ?? 0)
    })
  })
  return limit
}

/**
 * Get the distance from the edge of the chip to the mouse position,
 * so that it snaps to the exact position when user presses down the mouse
 * @param mouseX - Mouse X position
 * @param mouseY - Mouse Y position
 * @param element - Chip element
 * @returns Object containing offsetX and offsetY distances from the left and top edges of the chip
 */
export const getDraggedChipEdgeDist = (
  mouseX: number,
  mouseY: number,
  element: Element | undefined
): { offsetX: number; offsetY: number } | undefined => {
  if (!element) return
  const rect = element.getBoundingClientRect()
  const inHorizontally = mouseX >= rect.left && mouseX <= rect.right
  const inVertically = mouseY >= rect.top && mouseY <= rect.bottom
  if (!inHorizontally || !inVertically) return

  return {
    offsetX: mouseX - rect.left, // distance from left edge
    offsetY: mouseY - rect.top, // distance from top edge
  }
}

// * IDs for the board elements
export enum BoardIDEnum {
  SICBO_BOARD = 'StandardBoard',
  RACETRACK = 'RacetrackBoard',
  GRID_DESKTOP = 'D_GridBoard',
  GRID_MOBILE = 'GridBoard',
  GameBoard = 'GameBoard',
}

/**
 * Get the dragged chip element to change its opacity on the fly for the drag-and-drop effect
 * @param betType The dragged bet type
 * @param boardId The board element ID
 * @returns The dragged chip element
 */
export const getDraggedChipRef = (betType: FullBetType | null, boardId: string) => {
  if (!betType) return

  const board = document.getElementById(boardId)
  if (!board) return

  const preChip = board?.querySelector(`[data-drag-chip="${betType}"]`)
  return preChip as HTMLElement | undefined
}

/**
 *
 * @param event Mouse event to get the center position of the target element.
 * @returns The center position of the target element in page coordinates.
 */
export const getCenterPosition = (event: React.MouseEvent<Element, MouseEvent>) => {
  const target = document.elementFromPoint(event.pageX, event.pageY)

  const rect = (target as HTMLElement).getBoundingClientRect()
  const centerPosition = {
    x: rect.left + rect.width / 2,
    y: rect.top + rect.height / 2,
  }
  return centerPosition
}
export const getBetDiff = (
  prevBets: Record<GenericBetNo | CallBetsEnum, number>,
  curBets: Record<GenericBetNo | CallBetsEnum, number>
): Record<GenericBetNo | CallBetsEnum, number> => {
  const diff = {} as Record<GenericBetNo | CallBetsEnum, number>
  const curEntries = Object.entries(curBets) as [GenericBetNo | CallBetsEnum, number][]
  for (const [betType, amount] of curEntries) {
    if (!prevBets[betType] || prevBets[betType] !== amount) {
      diff[betType] = amount
    }
  }
  return diff
}
