import { LoadingResourceEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import i18n from 'i18next'
import LanguageDetector, { type DetectorOptions } from 'i18next-browser-languagedetector'
import ChainedBackend, { type ChainedBackendOptions } from 'i18next-chained-backend'
import HttpBackend from 'i18next-http-backend'
import resourcesToBackend from 'i18next-resources-to-backend'
import { initReactI18next } from 'react-i18next'
import { isEnvDev } from '../common/common'
import { FALLBACK_LANGUAGE_MAP } from './constants'
import fallbackJson from './fallback.json'

const resources = {
  en: { translation: fallbackJson },
}

// https://ikigaians.atlassian.net/wiki/spaces/CI/pages/1097465875/Tolgee+Usage+Guide
const tolgeeTranslated = 'https://i18lg.iki-utl.cc/react-live-roulette/translated/{{lng}}.json'
const tolgeeReviewed = 'https://i18lg.iki-utl.cc/react-live-roulette/reviewed/{{lng}}.json'
const isDevCIT = import.meta.env.VITE_DEPLOY_ENV === 'dev' || import.meta.env.VITE_DEPLOY_ENV === 'cit'

const httpBackendConfig = {
  loadPath: isDevCIT ? tolgeeTranslated : tolgeeReviewed,
  crossDomain: true,
}

i18n.on('loaded', (loaded) => {
  if (Object.keys(loaded).length !== 0) {
    useLoadingStore.getState().decrementResources(LoadingResourceEnum.i18n)
  }
})

i18n.on('failedLoading', (lng, ns, msg) => {
  console.warn(`Failed loading ${lng} ${ns}: ${msg}`)
  useLoadingStore.getState().decrementResources(LoadingResourceEnum.i18n)
})

i18n
  .use(LanguageDetector)
  .use(ChainedBackend)
  .use(initReactI18next)
  .init<ChainedBackendOptions & DetectorOptions>({
    fallbackLng: FALLBACK_LANGUAGE_MAP,
    load: 'currentOnly',
    debug: isEnvDev,
    interpolation: {
      escapeValue: false, // react already safes from xss
    },
    backend: {
      // HTTP -> Fallback
      // See: https://www.i18next.com/how-to/caching and https://www.i18next.com/how-to/backend-fallback
      backends: [HttpBackend, resourcesToBackend(resources)],
      backendOptions: [httpBackendConfig, {}],
    },
  })

export default i18n
