import { clsx, type ClassValue } from 'clsx'
import DOMPurify from 'isomorphic-dompurify'
import { twMerge } from 'tailwind-merge'

/**
 * Combines clsx and tailwind-merge for optimal CSS class handling
 * @param inputs - Class values to be processed by clsx and merged by tailwind-merge
 * @returns Merged and deduplicated class string
 */
export const cn = (...inputs: ClassValue[]) => {
  return twMerge(clsx(inputs))
}

const navigator = window.navigator
export const hasVibrationSupport = 'vibrate' in navigator

export const isIOS = () => {
  const ua = navigator.userAgent
  return !!/iPad/i.exec(ua) || !!/iPhone/i.exec(ua)
}

export const isSafari = () => {
  const ua = navigator.userAgent
  const iOS = !!/iP(ad|od|hone)/i.exec(ua)
  const hasSafariInUa = !!/Safari/i.exec(ua)
  const noOtherBrowsersInUa = !/Chrome|CriOS|OPiOS|mercury|FxiOS|Firefox/i.exec(ua)
  let result = false
  if (iOS) {
    //detecting Safari in IOS mobile browsers
    const webkit = !!/WebKit/i.exec(ua)
    result = webkit && hasSafariInUa && noOtherBrowsersInUa
  } else if ('safari' in window) {
    //detecting Safari in Desktop Browsers
    result = true
  } else {
    // detecting Safari in other platforms
    result = hasSafariInUa && noOtherBrowsersInUa
  }
  return result
}

/**
 * Creates a Promise that resolves after a specified number of milliseconds.
 * This is an async/await-friendly version of setTimeout, useful for creating delays within asynchronous functions.
 *
 * @param milliseconds The number of milliseconds to wait before the promise resolves.
 * @returns A Promise that resolves after the specified delay.
 * @example
 * async function greetAfterDelay() {
 *  console.log('Hello');
 *  await sleep(2000); // Waits for 2 seconds
 *  console.log('World!');
 * }
 */
export const sleep = (milliseconds: number): Promise<void> => {
  return new Promise((resolve) => setTimeout(resolve, milliseconds))
}

export const isLandscape = () => window.matchMedia('(orientation: landscape)').matches

export const isMobileDevice = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
export const isDesktop = !isMobileDevice

export const thousandSeparator = (num: number | string) => {
  const n = Number(num)
  if (isNaN(n)) return '0.00'
  return n.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

export const throttle = (cb: () => void, ms: number) => {
  let lastTime = 0
  return () => {
    const now = Date.now()
    if (now - lastTime >= ms) {
      cb()
      lastTime = now
    }
  }
}

export const toggleFullScreen = () => {
  if (!document.fullscreenElement) {
    document.documentElement.requestFullscreen().catch((err) => {
      console.error(`Error attempting to enable full-screen mode: ${err.message} (${err.name})`)
    })
  } else if (document.exitFullscreen) {
    document.exitFullscreen().catch((err) => {
      console.error(`Error attempting to exit full-screen mode: ${err.message} (${err.name})`)
    })
  }
}

// including local test
export const isEnvDev = import.meta.env.VITE_DEPLOY_ENV === 'dev'
export const isPrdEnv = import.meta.env.VITE_DEPLOY_ENV === 'prd'

export const isLocalhost = () => {
  const { hostname } = window.location
  return (
    process.env.NODE_ENV === 'development' ||
    hostname === 'localhost' ||
    hostname === '127.0.0.1' ||
    hostname === '::1' || // IPv6 localhost
    hostname.startsWith('192.168.') || // Local network
    hostname.startsWith('10.') || // Local network
    hostname.startsWith('172.') || // Local network
    hostname.endsWith('.local') || // mDNS
    hostname.endsWith('.localhost') // localhost subdomains
  )
}

export const getFormattedRoundId = (roundId: string) => roundId.split('-').at(-1)?.toUpperCase() ?? ''

/**
 * Safely parses JSON string with fallback value
 * @param jsonString - The JSON string to parse
 * @param fallback - The fallback value if parsing fails
 * @returns Parsed object or fallback value
 */
export const safeJsonParse = <T>(jsonString: string | null, fallback: T): T => {
  if (!jsonString) return fallback

  try {
    return JSON.parse(jsonString) as T
  } catch (error) {
    console.warn('Failed to parse JSON:', error)
    return fallback
  }
}

/**
 * Safely stringifies object to JSON with error handling
 * @param obj - The object to stringify
 * @param fallback - The fallback string if stringify fails
 * @returns JSON string or fallback
 */
export const safeJsonStringify = (obj: unknown, fallback = '{}'): string => {
  try {
    return JSON.stringify(obj)
  } catch (error) {
    console.warn('Failed to stringify object:', error)
    return fallback
  }
}

export const sanitizeRule = (html: string) => {
  return DOMPurify.sanitize(html, {
    ALLOWED_TAGS: ['h3', 'h4', 'p', 'ul', 'li', 'table', 'thead', 'tbody', 'tr', 'th', 'td', 'strong'],
    ALLOWED_ATTR: [],
  })
}
