import {
  BaccaratBetLimitProfile,
  BaccaratBetNo,
  BetRange,
  GameType,
  RouletteBetLimitProfile,
  RouletteBetNo,
  RouletteCallBet,
  SicBoBetLimitProfile,
  SicBoBetNo,
} from '@ikigaians/house'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { isEnvDev } from '@repo/utils/common/common'

import { playSound } from '@repo/utils/common/sound'

const rouletteGameTypes = [GameType.AUTO_ROULETTE, GameType.ROULETTE]
const sicboGameTypes = [GameType.AUTO_SIC_BO, GameType.SIC_BO]

export const isRouletteGameType = (gameType: GameType) => {
  return rouletteGameTypes.includes(gameType)
}

export const isSicboGameType = (gameType: GameType) => {
  return sicboGameTypes.includes(gameType)
}

export const isBaccaratGameType = (gameType: GameType) => {
  return gameType === GameType.BACCARAT
}

// * This function mainly used to get the bet keys for the game type
export const getGameBetKeys = (gameType: GameType): string[] => {
  if (isRouletteGameType(gameType)) return Object.values({ ...RouletteBetNo, ...RouletteCallBet })
  else if (isSicboGameType(gameType)) return Object.values(SicBoBetNo)
  else if (isBaccaratGameType(gameType)) return Object.values(BaccaratBetNo)
  else return []
}

export const getStackingBetSound = (stackingMap: Record<string, number>, latestPlaceBet: Record<string, number>) => {
  let stack = 0
  Object.keys(latestPlaceBet).forEach((betSpot) => {
    stack = stackingMap[betSpot] > stack ? stackingMap[betSpot] : stack
  })
  const stackingSoundMap: { [key: string]: string } = {
    '1': 'bet',
    '2': 'stackingBet1',
    '3': 'stackingBet2',
    '4': 'stackingBet3',
  }
  if (Number(stack) < 5) return stackingSoundMap[stack]
  return 'stackingBet4'
}

export const getStackingMap = (history: Record<string, number>[]) => {
  const stackMap: Record<string, number> = {}
  let prevHistory: Record<string, number> = {}

  history.forEach((entry) => {
    for (const spot in entry) {
      if (!prevHistory[spot] || prevHistory[spot] !== entry[spot]) stackMap[spot] = (stackMap[spot] || 0) + 1
    }
    Object.keys(stackMap).forEach((spot) => {
      if (!entry[spot]) {
        stackMap[spot] = 0
      }
    })
    prevHistory = entry
  })

  return stackMap
}

export const getBetSound = (history: Record<string, number>[], latestPlaceBet: Record<string, number>) => {
  if (!history || !latestPlaceBet) return
  const stackingMap = getStackingMap(history)

  if (latestPlaceBet && Object.keys(latestPlaceBet).length >= 3) {
    playSound(['multipleBet'])
    return
  }
  const sound = getStackingBetSound(stackingMap, latestPlaceBet)
  playSound([sound])
}

export const isInIframe = () => {
  try {
    return window.self !== window.top
  } catch {
    return true
  }
}

