export const handleGamePauseToast = (isBet: boolean, isPaused: boolean) => {
  if (!isBet && isPaused) useToastStore.getState().addToast('info', 'COMMON.GAME_PAUSED', { persistent: true, id: 'game-paused' })
  if (!isPaused) useToastStore.getState().dismissToast('game-paused')
}

export const getBaseDomain = () => {
  const WS_SERVER = useConfigStore.getState().WS_SERVER
  if (isEnvDev) return WS_SERVER

  if (!WS_SERVER) {
    throw new Error('WS_SERVER is not configured.')
  }

  const serverParts = WS_SERVER.split('.')
  if (serverParts.length < 3) {
    throw new Error('WS_SERVER format is incorrect. Expected format: domain.subdomain.ext')
  }

  const [secondLevelDomain, defaultSubDomain, domainExt] = serverParts

  const subdomain = getBrowserSubdomain() || defaultSubDomain

  return `${secondLevelDomain}.${subdomain}.${domainExt}`
}

const getBrowserSubdomain = (): string | undefined => {
  const hostnameParts = window.location.hostname.split('.')
  const hasNoSubdomain = hostnameParts.length < 2
  return hasNoSubdomain ? undefined : hostnameParts[1] // return the subdomain part
}

/**
 * Calculates chip skew angle for 3D perspective effects
 * @param index - Current index in the array/grid
 * @param length - Total length of array or column count for grid
 * @param skewMultiplier - Multiplier for the skew effect
 * @param options - Optional configuration
 * @param options.useSign - Use only sign (-1, 0, 1) instead of full offset
 * @param options.useModulo - Use modulo operation where multiple rows occur in one array loop
 * @returns The calculated skew angle in degrees
 */
export const calculateChipSkew = (
  index: number,
  length: number,
  skewMultiplier: number,
  options?: {
    useSign?: boolean
    useModulo?: boolean
  }
): number => {
  const median = (length - 1) / 2
  const position = options?.useModulo ? index % length : index
  const offset = position - median
  return options?.useSign ? Math.sign(offset) * skewMultiplier : offset * skewMultiplier
}

/**
 * Extracts the left percentage value from Tailwind class string
 * @param s - Class string containing left-[x%] pattern
 * @returns The extracted percentage value or 50 as default
 */
export const extractLeft = (s: string): number => {
  return parseFloat(s.match(/left-\[([0-9.]+)%\]/)?.[1] ?? '50')
}
