/* eslint-disable @typescript-eslint/no-explicit-any */
import { Howl, Howler } from 'howler'

export const sounds = new Map<string, Howl>()
export type soundResource = Record<string, { url: string }>

const UISounds = new Set(
  [
    'click',
    'lastfive',
    'chiptrayOpen',
    'disabledAction',
    'double',
    'errorToast',
    'multipleBet',
    'bet',
    'choosingChip',
    ...Array.from({ length: 8 }, (_, i) => `slider${i + 1}`),
    ...Array.from({ length: 4 }, (_, i) => `stackingBet${i + 1}`),
    'normalToast',
    'undo',
    'rouletteBGM',
    'winEffect',
  ].map((name) => `sound_${name}`)
)

const voiceOverSound = new Set<string>([
  'sound_lastBets',
  'sound_noMoreBets',
  'sound_placeYourBets',
  'sound_youWin',
  ...Array.from({ length: 37 }, (_, i) => `sound_${i}`),
  ...Array.from({ length: 6 }, (_, i) => `sound_D${i + 1}`),
  ...Array.from({ length: 6 }, (_, i) => `sound_TR${i + 1}`),
])

export const getVolume = (key: string) => {
  if (key === 'sound_rouletteBGM' || key === 'sound_sicboBGM') return 0.25
  if (UISounds.has(key)) return 1.8
  if (voiceOverSound.has(key)) return 0.15
  return 1
}

// * Sound should only init after user interaction
export const initSoundResource = (soundResource: soundResource) => {
  let bgmRestarting = false

  sounds.clear()
  Object.entries(soundResource).forEach(([key, { url }]) => {
    if (key === 'sound_rouletteBGM' || key === 'sound_sicboBGM') {
      const sound = new Howl({
        src: url,
        autoplay: true,
        volume: 0.25,
        html5: false,
        onend: () => {
          // prevent multiple trigger onend event and having noise when replaying bgm
          if (bgmRestarting) return
          bgmRestarting = true

          setTimeout(() => {
            sound.play()
            bgmRestarting = false
          }, 50) // delay to let internal state settle
        },
      })
      sounds.set(key, sound)
    } else {
      sounds.set(
        key,
        new Howl({
          src: url,
          volume: getVolume(key),
          autoplay: false,
          html5: false,
        })
      )
    }
  })
}

export function playSound(list: string[]): () => void {
  let dispose = false
  let sound: Howl | undefined = undefined
  let id: number | undefined = undefined

  const next = (name: string) => {
    if (dispose) return Promise.resolve()
    return new Promise<void>((resolve) => {
      if (!sounds.has(name)) {
        return resolve()
      }
      sound = sounds.get(name)
      id = sound?.play()
      sound?.once(
        'end',
        () => {
          resolve()
        },
        id
      )
    })
  }

  let promiser = Promise.resolve()

  list.forEach((item) => {
    const soundName = `sound_${item}`
    promiser = promiser.then(() => next(soundName))
  })

  return () => {
    dispose = true
    sound?.stop(id)
  }
}

export function setVolume(volume: number) {
  if (volume !== 0) {
    ;(Howler as any)._muted = false
  }
  Howler.volume(volume)
}

let retryCount = 0
export function suspendAudio() {
  if (Howler.ctx) {
    Howler.ctx
      .suspend()
      .then(() => {
        setTimeout(() => {
          Howler.ctx.resume()
        }, 100)
        retryCount = 0
      })
      .catch((err) => {
        console.warn('AudioContext suspend failed:', err.name + ':', err.message)
        if (retryCount < 5) {
          suspendAudio()
          retryCount++
        }
      })
  }
}

export function setMute(mute: boolean) {
  Howler.mute(mute)
}

export function stopAudio() {
  Howler.stop()
}

window.addEventListener('unhandledrejection', (event) => {
  if (!event.reason || typeof event.reason !== 'object') return
  const name = event.reason.name
  const message = event.reason.message
  if (name === 'InvalidStateError' && message === 'Failed to start the audio device') {
    console.log('audio error', name, message)
    setTimeout(() => {
      suspendAudio()
    }, 500)
  }
})
