import { GameResult, GameType, getGameRuleByType } from '@ikigaians/house'
import { useConfigStore } from '@repo/ui/store/useConfigStore'

// Game type is optional, will use current game config if not provided
export const getCheckResultValidFn = (gameType?: GameType) => {
  const currentGameType = gameType ?? useConfigStore.getState().GAME_TYPE
  const gameRule = getGameRuleByType(currentGameType)
  if (!gameRule) return () => false
  else return (result: GameResult) => gameRule.validateResult(result) === undefined
}
