import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isDesktop, isMobileDevice } from '@repo/utils/common/common'
import { RefObject } from 'react'

export interface MainBoardParams {
  factor: number
  height: number
  width: number
  left: number
  top: number
  round: number
  dpr: number
  ratio: number
}

/**
 * Gets the MainBoard element from the DOM and stores it in the provided ref
 * @param mainBoardRef Reference to store the MainBoard element
 */
export const getMainBoard = (mainBoardRef: RefObject<HTMLDivElement | null>) => {
  const mainBoardEle = document.getElementById('MainBoard')
  if (!mainBoardEle) return
  mainBoardRef.current = mainBoardEle as HTMLDivElement
}

export const DIST_TO_LEFT_EDGE = 8

/**
 * Gets the parameters for the MainBoard element
 * @param mainBoardRef Reference to the MainBoard element
 * @returns MainBoardParams or null if the element is not found
 */
export const getMainBoardParams = (mainBoardRef: RefObject<HTMLDivElement | null>): MainBoardParams | null => {
  const mainBoardEle = mainBoardRef.current

  if (!mainBoardEle) return null

  const boundingRect = mainBoardEle.getBoundingClientRect()

  const isCompact = useConfigStore.getState().IS_SMALL_DEVICE && isMobileDevice
  const dpr = 1
  const ratio = useLayoutStore.getState().rootEmFontSize
  const factor = ratio / 16

  const left = isCompact || isDesktop ? DIST_TO_LEFT_EDGE * factor + boundingRect.left : boundingRect.left
  const top = boundingRect.top
  const width = mainBoardEle.offsetWidth * dpr
  const height = mainBoardEle.offsetHeight * dpr
  const round = (isCompact ? 24 : 32) * dpr

  return { left, top, width, height, round, dpr, ratio, factor }
}
