import { useCallback, useEffect, useRef } from 'react'

import { GlowFilter as Glow, KawaseBlurFilter as Kawase } from 'pixi-filters'
import { Container, Graphics, Ticker } from 'pixi.js'

import { getMainBoard, getMainBoardParams, MainBoardParams } from '@repo/ui/pixi-react/utils/mainBoardUtils'
import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useStageStore } from '@repo/ui/store/useStageStore'

import {
  calcDegreeOffsetDist,
  createLineSpec,
  createMainBoardMask,
  createPath,
  createPoints,
  createSegmentGraphics,
  drawSegment,
  getFactor,
  LineGraphics,
  LineSpec,
  Path,
  RenderCache,
} from './utils'

import {
  FADE_IN_DELAY_MS,
  FADE_IN_DURATION_MS,
  GLOW_FILTER_CONFIG,
  KAWASE_BLUR_FILTER_CONFIG,
  OPACITY_ANIMATION_DURATION_MS,
  ROTATION_DURATION_MS,
  SEGMENTS_SPECS,
} from './constants'

/**
 * Loading Light Effect Hook
 *
 * Handles PIXI.js rendering for animated loading light lines around the MainBoard border.
 * Uses utility functions for calculations and focuses on PIXI-specific rendering logic.
 */
export default function useLoadingLightEffect() {
  const core = useStageStore((s) => s.core)
  const loadingStage = useLoadingStore((s) => s.loadingStage)

  const rafRef = useRef<number | null>(null)
  const mainBoardRef = useRef<HTMLDivElement | null>(null)
  const wrapperContainerRef = useRef<Container | null>(null)
  const graphicsRef = useRef<LineGraphics[]>([])
  const animTimeRef = useRef<number>(0)
  const currDegreeRef = useRef<number>(0)
  const currOpacityRef = useRef<number>(0)

  const renderCacheRef = useRef<RenderCache>({
    params: null,
    path: [],
    totalPerimeter: 0,
    glow: null,
    kawase: [],
  })

  const updateAnim = useCallback((ticker: Ticker) => {
    animTimeRef.current += ticker.deltaMS

    const { totalPerimeter, path } = renderCacheRef.current
    if (!path.length || !totalPerimeter) return

    const rotationProgress = (animTimeRef.current % ROTATION_DURATION_MS) / ROTATION_DURATION_MS
    currDegreeRef.current = rotationProgress * 360
    const baseOffset = rotationProgress * totalPerimeter

    graphicsRef.current.forEach((graphics) => {
      const { baseDegree } = graphics.userData
      const animatedDegreeOffset = calcDegreeOffsetDist(baseDegree, totalPerimeter)
      drawSegment(graphics, path, animatedDegreeOffset + baseOffset, totalPerimeter)
    })
  }, [])

  const createFilter = useCallback((lineSpec: LineSpec, lineIndex: number, isLastLine: boolean) => {
    const { alpha, blur, color } = lineSpec

    const cache = renderCacheRef.current

    if (isLastLine) {
      if (!cache.glow) cache.glow = new Glow({ ...GLOW_FILTER_CONFIG, color, alpha })
      return [cache.glow]
    }

    if (!cache.kawase[lineIndex]) {
      cache.kawase[lineIndex] = new Kawase({ strength: blur, ...KAWASE_BLUR_FILTER_CONFIG })
    }

    return [cache.kawase[lineIndex]]
  }, [])

  const cleanUpGraphics = useCallback(() => {
    const container = wrapperContainerRef.current
    if (!container) return

    container.destroy({ children: true, texture: true })
    container.parent?.removeChild(container)
    wrapperContainerRef.current = null
    graphicsRef.current = []
    core?.ticker?.remove?.(updateAnim)
  }, [core, updateAnim])

  const renderLines = useCallback(
    (linesSpec: LineSpec[], degreeOffset: number, baseDegree: number, container: Container, totalPerimeter: number, path: Path[]) => {
      const offsetDist = calcDegreeOffsetDist(degreeOffset, totalPerimeter)

      linesSpec.forEach((lineSpec, lineIndex) => {
        const lineGraphics: Graphics[] = []

        SEGMENTS_SPECS.forEach((segmentSpec) => {
          const graphics = createSegmentGraphics(lineSpec, segmentSpec, offsetDist, totalPerimeter, path)
          graphics.userData.baseDegree = baseDegree
          lineGraphics.push(graphics)
          graphicsRef.current.push(graphics)
        })

        const isLastLine = lineIndex === linesSpec.length - 1
        const lineContainer = new Container({
          children: lineGraphics,
          filters: createFilter(lineSpec, lineIndex, isLastLine),
        })
        container.addChild(lineContainer)
      })
    },
    [createFilter]
  )

  const createWrapperContainer = useCallback(
    (params: MainBoardParams) => {
      const container = new Container()
      container.x = params.left
      container.y = params.top
      container.alpha = currOpacityRef.current
      wrapperContainerRef.current = container

      const mainBoardMask = createMainBoardMask(params)
      container.addChild(mainBoardMask)
      container.mask = mainBoardMask
      core?.stage.addChild(container)

      return container
    },
    [core]
  )

  const initLoadingLight = useCallback(() => {
    cleanUpGraphics()
    getMainBoard(mainBoardRef)
    const params = getMainBoardParams(mainBoardRef)

    if (!params || !core?.stage || loadingStage >= LoadingStageEnum.PostLoaded4) return

    const points = createPoints(params.width, params.height, params.round * getFactor())
    const { path, totalPerimeter } = createPath(points)
    const linesSpec = createLineSpec(totalPerimeter)

    renderCacheRef.current = { params, path, totalPerimeter, glow: null, kawase: [] }

    const container = createWrapperContainer(params)
    const currDegree = currDegreeRef.current

    // Render two sets of lines 180 apart for symmetrical effect
    renderLines(linesSpec, currDegree, 0, container, totalPerimeter, path)
    renderLines(linesSpec, currDegree + 180, 180, container, totalPerimeter, path)

    core.ticker.add(updateAnim)
  }, [cleanUpGraphics, core, loadingStage, renderLines, updateAnim, createWrapperContainer])

  const finalCleanUp = useCallback(() => {
    cleanUpGraphics()

    if (rafRef.current) {
      cancelAnimationFrame(rafRef.current)
      rafRef.current = null
    }

    const { glow, kawase } = renderCacheRef.current

    glow?.destroy(true)
    kawase.forEach((filter) => filter.destroy(true))
    renderCacheRef.current = { params: null, path: [], totalPerimeter: 0, glow: null, kawase: [] }
  }, [cleanUpGraphics])

  useEffect(() => {
    if (!core) return

    initLoadingLight()

    core.renderer?.on('resize', initLoadingLight)
    return () => {
      core.renderer?.off('resize', initLoadingLight)
    }
  }, [core, initLoadingLight])

  const animateOpacity = useCallback((targetOpacity: number, duration: number = OPACITY_ANIMATION_DURATION_MS) => {
    if (rafRef.current) {
      cancelAnimationFrame(rafRef.current)
      rafRef.current = null
    }

    const container = wrapperContainerRef.current
    if (!container) return

    const startOpacity = currOpacityRef.current
    const startTime = Date.now()
    const change = targetOpacity - startOpacity

    const step = () => {
      if (!wrapperContainerRef.current) return
      const now = Date.now()
      const elapsed = now - startTime
      const progress = Math.min(elapsed / duration, 1)

      const newOpacity = startOpacity + change * progress

      currOpacityRef.current = newOpacity
      wrapperContainerRef.current.alpha = newOpacity

      if (progress < 1) {
        rafRef.current = requestAnimationFrame(step)
      } else {
        currOpacityRef.current = targetOpacity
        wrapperContainerRef.current.alpha = targetOpacity
        rafRef.current = null
      }
    }

    rafRef.current = requestAnimationFrame(step)
  }, [])

  useEffect(() => {
    switch (loadingStage) {
      case LoadingStageEnum.Idle: {
        // Fade-in: opacity to 1 (initial opacity is 0)
        setTimeout(() => animateOpacity(1, FADE_IN_DURATION_MS), FADE_IN_DELAY_MS)
        break
      }

      case LoadingStageEnum.Loaded: {
        // Fade-out: opacity to 0 over 1 second
        animateOpacity(0)
        break
      }

      case LoadingStageEnum.PostLoaded4: {
        finalCleanUp()
        break
      }
      default:
        break
    }
  }, [loadingStage, animateOpacity, finalCleanUp])
}
