import { cn } from '@repo/utils/common/common'

import front1 from '@repo/ui/assets/sicbo/youWin/dices/front-1.webp'
import front2 from '@repo/ui/assets/sicbo/youWin/dices/front-2.webp'
import front3 from '@repo/ui/assets/sicbo/youWin/dices/front-3.webp'
import front4 from '@repo/ui/assets/sicbo/youWin/dices/front-4.webp'
import front5 from '@repo/ui/assets/sicbo/youWin/dices/front-5.webp'
import front6 from '@repo/ui/assets/sicbo/youWin/dices/front-6.webp'
import tilt1 from '@repo/ui/assets/sicbo/youWin/dices/tilt-1.webp'
import tilt2 from '@repo/ui/assets/sicbo/youWin/dices/tilt-2.webp'
import tilt3 from '@repo/ui/assets/sicbo/youWin/dices/tilt-3.webp'
import tilt4 from '@repo/ui/assets/sicbo/youWin/dices/tilt-4.webp'
import tilt5 from '@repo/ui/assets/sicbo/youWin/dices/tilt-5.webp'
import tilt6 from '@repo/ui/assets/sicbo/youWin/dices/tilt-6.webp'
import Image from '@repo/ui/components/common/Image'

const diceMap = {
  'front-1': front1,
  'front-2': front2,
  'front-3': front3,
  'front-4': front4,
  'front-5': front5,
  'front-6': front6,
  'tilt-1': tilt1,
  'tilt-2': tilt2,
  'tilt-3': tilt3,
  'tilt-4': tilt4,
  'tilt-5': tilt5,
  'tilt-6': tilt6,
}

const Dice = ({ type, value, className }: { type: string; value: number; className?: string }) => {
  const src = diceMap[`${type}-${value}` as keyof typeof diceMap]
  if (!src) {
    console.warn(`Missing dice image for ${type}-${value}`)
    return null
  }
  return (
    <div className={cn('dice', className)}>
      <Image
        src={src}
        alt={`Dice ${type}-${value}`}
        className="h-full w-full"
      />
    </div>
  )
}

interface Props {
  betResult: number[]
  className?: string
}

const ResultDices = ({ betResult, className }: Props) => {
  return (
    <div className={className}>
      <Dice
        type="tilt"
        value={betResult[0]}
        className="h-[2.5rem] w-[2.5625rem] rotate-[-10deg]"
      />
      <Dice
        type="front"
        value={betResult[1]}
        className="mt-[-0.36rem] h-[2.5rem] w-[2.25rem]"
      />
      <Dice
        type="tilt"
        value={betResult[2]}
        className="h-[2.5rem] w-[2.5625rem] rotate-[10deg] scale-x-[-1]"
      />
    </div>
  )
}

export default ResultDices
