import { GameType, getPayout, Payload, PayoutResult } from '@ikigaians/house'
import i18n from 'i18next'
import { create, StoreApi, UseBoundStore } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'

import { getBetDetail, useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { sleep, thousandSeparator } from '@repo/utils/common/common'
import { getCheckResultValidFn } from '@repo/utils/common/gameResult'

import { getWinNumberStore } from './useWinNumberStore'

export type AnimationStateType = null | 'animationStart' | 'winAnimationStart' | 'winChipsMoving'
export type DealResultData<T, DealDetail = void> = {
  roundId: string
  curr: T
  diff: T
  timecode?: number
  currDetail?: DealDetail
}
export type WinResultData<T> = {
  roundId: string
  result: T
  betDetail: { [k: string]: number } | null
  payout: Record<string, number>
  userWin: number
  isMaxWin: boolean
  isTimeUp: boolean
  isTableFinished: boolean
  timecode?: number
}
export type GetDealDetailFNType<T, DealDetail = void> = (
  currResult: T,
  prevResult?: T
) => {
  isDone: boolean
  diff: T
  detail: DealDetail
}
export type RoundInfo = {
  roundId: string
  gameType: GameType
  gameCode: string | undefined
}

// * T: roulette=string, sicBo=number[], baccarat=string[]
interface IWinAnimStore<T, DealDetail = void> {
  animState: AnimationStateType
  animTimeCode: number | null
  pendingTimecodes: number[]

  // * Current deal result for games has multiple deal events
  // * Also used to draw deal animation
  dealResults: { [roundId: string]: DealResultData<T, DealDetail>[] } // Store the deal results has not been animated yet for timecode callback
  dealResult: { curr: T; diff: T; currDetail?: DealDetail } | null // Current animating deal result data
  // Default deal function applied for Roulette and SicBo, only 1 deal event is expected
  // For card games, the deal function will be overridden in the preInit.ts because it has multiple deal events
  onTableDeal: (data: Payload, timecode?: number) => void
  getDealDetail?: GetDealDetailFNType<T, DealDetail> // Optional function to get deal detail, used in Baccarat

  // * Current animating win result data
  winResults: { [roundId: string]: WinResultData<T> } // Store the win results has not been animated yet for timecode callback
  winResult: WinResultData<T> | null // Current animating win result data
  onTableFinished: (data: Payload, timecode?: number) => void

  // * On bet failed handler
  betResultErrorRoundId: string | null
  onBetResultError: (roundId: string) => void

  // * For AVPlayer callback
  onTimeUp: (timecode: number) => void

  // * Create animations
  animateChipRemoval: () => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useWinAnimStore`,
  store: 'WinAnimStore',
  serialize: { options: true },
}

function createWinAnimStore<T, DealDetail = void>() {
  const useWinNumberStore = getWinNumberStore<T>()

  // Helper function to create WinResultData from payload
  const createWinResultData = (payload: Payload, timecode?: number, isTableFinished = false): WinResultData<T> | null => {
    const { table, activeRound } = payload
    if (!table || !activeRound) return null

    const { gameType, gameCode } = table
    const { roundId, result: roundResult } = activeRound
    if (!roundId || !roundResult || !gameType) return null

    // * Collect required win data
    const result = Object.values(roundResult)[0] as T

    // * Validate results structure
    const checkResultValid = getCheckResultValidFn(gameType)
    const isValid = checkResultValid(roundResult)
    if (!isValid) {
      console.error('Invalid result structure:', { gameType, gameCode, result })
      return null
    }

    const roundInfo: RoundInfo = { roundId, gameType, gameCode }
    console.debug('roundInfo', roundInfo, 'roundResult', roundResult)

    // * Win result data
    const betDetail = getBetDetail()
    const { payoutDetail = {}, amount = 0 } = betDetail ? getPayout(gameType, roundResult, betDetail) : ({} as PayoutResult)
    const tableMaxPayout = useConfigStore.getState().tableMaxPayout
    const isMaxWin = amount > tableMaxPayout

    return {
      roundId,
      result,
      betDetail,
      payout: payoutDetail,
      userWin: isMaxWin ? tableMaxPayout : amount,
      isMaxWin,
      // TODO: Since player.setMaxLatency is working, we can use the table.finish as win animation trigger and remove this logic, leaving it for now just in case
      isTimeUp: true,
      // isTimeUp: timecode ? false : true, // If timecode is provided, it will be set to false for timeup callback
      isTableFinished,
      timecode,
    }
  }

  const updateStores = (roundId: string, winResult: WinResultData<T>) => {
    const { result, userWin } = winResult
    useWinNumberStore.getState().add(result, roundId)
    const tableMaxPayout = useConfigStore.getState().tableMaxPayout
    if (userWin) useWinNumberStore.setState({ lastWinCache: Math.min(tableMaxPayout, userWin) })
  }

  const runWinResultAnim = async (
    set: StoreApi<IWinAnimStore<T, DealDetail>>['setState'],
    get: StoreApi<IWinAnimStore<T, DealDetail>>['getState'],
    winResult: WinResultData<T>
  ) => {
    const ANIMATION_START_DELAY = 333
    const WIN_ANIMATION_DELAY = 2250 - 333
    const CHIPS_MOVING_DELAY = 1250

    set({ animState: 'animationStart', winResult: winResult })

    await sleep(ANIMATION_START_DELAY)
    set({ animState: 'winAnimationStart' })

    await sleep(WIN_ANIMATION_DELAY)
    set({ animState: 'winChipsMoving' })

    await sleep(CHIPS_MOVING_DELAY)
    // Reset anim state and win result after animation
    set({ animState: null, winResult: null, dealResult: null })
    useConfigStore.setState({ tableFinished: true })

    // * Remove the roundId from winResults
    const roundId = winResult.roundId
    const { winResults, pendingTimecodes } = get()
    const newWinResults = removeRoundFromResults(winResults, roundId)
    set({ winResults: newWinResults })

    // * Check if there are pending timecodes, run the next animation if any
    const timecode = pendingTimecodes.shift()
    if (timecode) handleTimeUp(set, get, timecode)
  }

  const runCancelAnim = async (set: StoreApi<IWinAnimStore<T, DealDetail>>['setState']) => {
    // For round that are not finished, all chips will disappear
    set({ animState: 'winChipsMoving' })

    await sleep(600)
    set({ animState: null, winResult: null, dealResult: null })

    // Clear board history to prevent chips from showing on the board again
    useCartStore.setState({ boardHistory: [] })
  }

  const handleTimeUp = (
    set: StoreApi<IWinAnimStore<T, DealDetail>>['setState'],
    get: StoreApi<IWinAnimStore<T, DealDetail>>['getState'],
    timecode: number
  ) => {
    // * If deal results has timecode, run the deal animation
    const { animState, pendingTimecodes, dealResults, winResults } = get()
    const dealResult = Object.values(dealResults)
      .flat()
      .find((v) => v.timecode === timecode)
    if (dealResult) set({ dealResult })

    // * If the win results has timecode, update or run the win animation
    const winResult = Object.entries(winResults).find(([, v]) => v.timecode === timecode)
    if (!winResult) return

    // * If animState is not null, it means we are already animating something, wait for it to finish
    if (animState) {
      pendingTimecodes.push(timecode)
      set({ pendingTimecodes })
      return
    }

    const [roundId, winResultData] = winResult
    const { isTableFinished } = winResultData
    if (isTableFinished) {
      // * Run the win animation if the table has finished
      updateStores(roundId, winResultData)
      runWinResultAnim(set, get, winResultData)
    } else {
      // * Update the isTimeUp=true if table has not finished yet and wait for table to finish
      set({ winResults: { ...get().winResults, [roundId]: { ...winResultData, isTimeUp: true } } })
    }
  }

  const subscribe = subscribeWithSelector<IWinAnimStore<T, DealDetail>>((set, get) => ({
    animState: null,
    animTimeCode: null,
    pendingTimecodes: [],

    dealResults: {},
    dealResult: null,
    onTableDeal: (payload, timecode) => {
      const { table, activeRound } = payload
      if (!table || !activeRound) return

      const { roundId } = activeRound
      if (!roundId) return

      const { getDealDetail, dealResults, betResultErrorRoundId } = get()

      // * Return if bet result error for this round
      if (betResultErrorRoundId === roundId) return

      // * Create win result data using the helper function
      const winResult = createWinResultData(payload, timecode, false)
      if (!winResult) return

      const setWinResult = () =>
        set((s) => ({
          animTimeCode: timecode,
          winResults: { ...s.winResults, [roundId]: winResult },
        }))

      // * If getDealDetail function is provided, check if the deal is done
      if (!getDealDetail) {
        // * If no deal detail is provided, just set the win result and wait for table.finished
        setWinResult()
        return
      }

      const { result } = winResult
      const prevResult = dealResults[roundId]?.at(-1)?.curr
      const { isDone, diff, detail } = getDealDetail(result, prevResult)
      const dealResult: DealResultData<T, DealDetail> = {
        roundId,
        curr: result,
        diff,
        timecode,
        currDetail: detail,
      }
      console.debug('dealResult', dealResult, 'isDone', isDone)

      // TODO: Since player.setMaxLatency is working, we can use the table.finish as win animation trigger and remove this logic, leaving it for now just in case
      set({ dealResult })

      // if (timecode) {
      //   // * If timecode is provided, store the deal result and wait for the timecode to be triggered
      //   set((s) => ({
      //     animTimeCode: timecode,
      //     dealResults: {
      //       ...s.dealResults,
      //       [roundId]: [...(s.dealResults[roundId] ?? []), dealResult],
      //     },
      //   }))
      // } else {
      //   // * If no timecode, just run the deal animation immediately
      //   set({ dealResult })
      // }

      // * Only add the win result if the deal is done
      if (isDone) setWinResult()
    },

    winResults: {},
    winResult: null,
    onTableFinished: (payload, finishedTimecode) => {
      const { roundId, status } = payload?.activeRound ?? {}
      const { dealResults, winResults, betResultErrorRoundId, getDealDetail } = get()
      const storedDealResult = dealResults[roundId || '']
      let storedWinResult = winResults[roundId || '']

      // * Return if bet result error for this round
      if (betResultErrorRoundId === roundId) {
        set({ betResultErrorRoundId: null })
        return
      }

      // * If no storedWinResult and we have a valid finished round, create one from payload
      if (!roundId || status !== 'finished') {
        // * No roundId or round is not finished, just run cancel animation if any
        runCancelAnim(set)
        return
      }
      // * Create WinResultData from payload when not found in store (created by onTableDeal)
      // * The case can happen when user reload the page after the round is already done dealing
      if (!storedWinResult) {
        const createdWinResult = createWinResultData(payload, finishedTimecode, true)
        if (!createdWinResult) {
          runCancelAnim(set)
          return
        }
        storedWinResult = createdWinResult
        // * Store the created result for consistency
        set({ winResults: { ...winResults, [roundId]: storedWinResult } })
      }
      // * Same for dealResult, we need to put back the dealResult if not set yet
      // * The case can happen when user reload the page after the round is already done dealing
      const { userWin, timecode, isTimeUp } = storedWinResult
      if (!storedDealResult && getDealDetail) {
        const { result } = storedWinResult
        const { diff, detail } = getDealDetail(result)
        const dealResult: DealResultData<T, DealDetail> = {
          roundId,
          curr: result,
          diff,
          timecode,
          currDetail: detail,
        }
        set({ dealResult })
      }
      if (isTimeUp) {
        // * Max payout toast
        const tableMaxPayout = useConfigStore.getState().tableMaxPayout
        const currencySymbol = useCartStore.getState().currencySymbol
        if (userWin >= tableMaxPayout) {
          const toastText = i18n.t('COMMON.MAX_WIN_REACHED', {
            currencySymbol,
            amount: thousandSeparator(tableMaxPayout),
          })
          useToastStore.getState().addToast('info', toastText)
        }
        // * Draw win animation if it's already time up
        updateStores(roundId, storedWinResult)
        runWinResultAnim(set, get, storedWinResult)
        return
      } else if (timecode) {
        // * Update isTableFinished to true if we need to wait for time up
        set({ winResults: { ...winResults, [roundId]: { ...storedWinResult, isTableFinished: true } } })
      }
    },

    betResultErrorRoundId: null,
    onBetResultError: (roundId: string) => {
      // * Remove the roundId from both dealResults and winResults
      const { dealResults, winResults } = get()
      const newDealResults = removeRoundFromResults(dealResults, roundId)
      const newWinResults = removeRoundFromResults(winResults, roundId)
      set({
        betResultErrorRoundId: roundId,
        dealResults: newDealResults,
        winResults: newWinResults,
      })

      // * Run cancel animation if the error bet round is the current animating round
      runCancelAnim(set)
    },

    onTimeUp: (timecode) => handleTimeUp(set, get, timecode),

    animateChipRemoval: async () => {
      set({ animState: 'animationStart' })
      await sleep(333)
      set({ animState: null, winResult: null, dealResult: null })
    },
  }))

  return create(devtools(subscribe, DEVTOOLS_CONFIG))
}

export const useWinAnimStore = createWinAnimStore<unknown, void>()

// Helper function to remove entries by roundId
const removeRoundFromResults = <T>(results: Record<string, T>, roundId: string): Record<string, T> => {
  return Object.entries(results).reduce((acc, [k, v]) => (k !== roundId ? { ...acc, [k]: v } : acc), {} as Record<string, T>)
}

export const getWinAnimStore = <T, DealDetail = void>() => useWinAnimStore as UseBoundStore<StoreApi<IWinAnimStore<T, DealDetail>>>
