import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isMobileDevice, throttle } from './common'

const DESKTOP_RATIO = 1.7777
// const BASE_WIDTH = isMobileDevice ? 360 : 1600
const DESKTOP_REM_RATIO = 100 // 1600 / 100 = 16px
const MOBILE_REM_RATIO = 22.5 // 360 / 22.5 = 16px
const MOBILE_NARROW_REM_RATIO = 18.95 // (360 - 56.8) / 18.95 = 16px, 56.8 is the width of the right sidebar

const SM_DEVICE_HEIGHT_BREAKPOINT = 649
const SM_DEVICE_WIDTH_RATIO = 0.845
const SM_DEVICE_BOARD_HEIGHT_OFFSET = 13 // px

let timer: number
let cacheW = 0
let cacheH = 0

const shouldSkipResizeHandling = () => {
  const skipPath = ['/history-content']
  const currentPath = window.location?.pathname
  if (!currentPath) return false
  return skipPath.some((path) => currentPath.endsWith(path))
}

// update max size when resize
const handleResize = () => {
  if (shouldSkipResizeHandling()) return

  const ratio = window.innerWidth / window.innerHeight
  const [maxRatio, minRatio] = isMobileDevice ? [9.5 / 16, 400 / 800, 9.5 / 16] : [DESKTOP_RATIO, DESKTOP_RATIO]
  let w = ratio < maxRatio ? window.innerWidth : maxRatio * window.innerHeight
  let h = ratio < minRatio ? window.innerWidth / minRatio : window.innerHeight

  const isSmallDevice = isMobileDevice && window.innerHeight < SM_DEVICE_HEIGHT_BREAKPOINT

  // set w and h for small device
  if (isMobileDevice && isSmallDevice) {
    w = window.innerWidth * SM_DEVICE_WIDTH_RATIO
    h = window.innerHeight - SM_DEVICE_BOARD_HEIGHT_OFFSET
  }

  useConfigStore.setState({ IS_SMALL_DEVICE: isSmallDevice })

  document.documentElement.style.setProperty(isMobileDevice ? '--mobile-narrow' : '--desktop-narrow', `${w}px`)
  document.documentElement.style.setProperty(isMobileDevice ? '--mobile-wide' : '--desktop-wide', `${h}px`)

  // if layout size changed, update root font size for scaling
  if (w !== cacheW || h !== cacheH) {
    cacheW = w
    cacheH = h
    const remRatio = isMobileDevice ? (isSmallDevice ? MOBILE_NARROW_REM_RATIO : MOBILE_REM_RATIO) : DESKTOP_REM_RATIO
    const rootFontSize = w / remRatio
    document.documentElement.style.fontSize = `${rootFontSize.toFixed(2)}px`
    useLayoutStore.setState({ rootEmFontSize: Number(rootFontSize.toFixed(2)) })
  }
}

handleResize()
const resizeCallback = throttle(() => {
  handleResize()

  // Resize once more after 100ms to prevent incorrect value (FND-435)
  window.clearTimeout(timer)
  timer = window.setTimeout(handleResize, 100)
}, 100)

window.addEventListener('resize', resizeCallback)
