import { Assets } from 'pixi.js'

import { videoManager } from '@repo/ui/pixi-react/utils/videoManager'
import { useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { isEnvDev } from '@repo/utils/common/common'
import { ResourceDetails, ResourceType } from '@repo/utils/types/misc'

const CACHE_NAME = 'react-live-resources'

const { decrementResources } = useLoadingStore.getState()

// Check storage quota and return usage details
export async function checkStorageQuota(): Promise<{
  usage: number
  quota: number
  ratio: number
} | null> {
  if (!('storage' in navigator) || !('estimate' in navigator.storage)) {
    return null
  }

  try {
    const estimate = await navigator.storage.estimate()
    if (!estimate.quota || !estimate.usage) {
      return null
    }

    const usage = estimate.usage
    const quota = estimate.quota
    const ratio = usage / quota

    if (ratio > 0.9) {
      console.warn(
        `Storage quota warning: ${Math.round(ratio * 100)}% used (${Math.round(usage / 1024 / 1024)}MB / ${Math.round(quota / 1024 / 1024)}MB)`
      )
    }

    return { usage, quota, ratio }
  } catch (error) {
    console.warn('Failed to check storage quota:', error)
    return null
  }
}

// Check if cache should be pruned based on environment and version
export function shouldPruneCache(): boolean {
  // 1. Check if dev environment
  if (isEnvDev) {
    return true
  }

  // 2. Check VITE_VERSION from localStorage
  const storedVersion = localStorage.getItem('VITE_VERSION')
  const currentVersion = import.meta.env.VITE_VERSION

  // 2.1 If version doesn't exist in localStorage, store it and return true
  if (!storedVersion) {
    if (currentVersion) {
      localStorage.setItem('VITE_VERSION', currentVersion)
    }
    return true
  }

  // 2.2 If version exists but doesn't match, update localStorage and return true
  if (storedVersion !== currentVersion) {
    if (currentVersion) {
      localStorage.setItem('VITE_VERSION', currentVersion)
    }
    return true
  } else {
    // 2.3 If version exists and matches, return false
    return false
  }
}

// Prune cache on initial page load
export async function pruneCache(checkLocalStorage = true): Promise<void> {
  if (checkLocalStorage && !shouldPruneCache()) {
    return
  }

  try {
    const cacheNames = await caches.keys()
    await Promise.all(
      cacheNames.map(async (cacheName) => {
        if (cacheName.startsWith(CACHE_NAME)) {
          await caches.delete(cacheName)
        }
      })
    )
  } catch (error) {
    console.error('Failed to prune cache:', error)
  }
}

// Simplified preload function that fetches and caches resources
// The service worker will handle serving them from cache and concurrent fetch deduplication automatically
export async function loadResource(type: ResourceDetails['type'], url: string): Promise<void> {
  try {
    // Fetch the resource (this will go through service worker if registered)
    // The service worker now handles concurrent fetch deduplication
    const response = await fetch(url)
    if (!response.ok) {
      throw new Error(`Failed to fetch ${type}: ${url}`)
    }

    // Handle special cases that need additional processing
    if (type === ResourceType.Video) {
      await videoManager.preloadVideo(url)
    } else if (type === ResourceType.PixiJson) {
      await Assets.load(url)
    }

    decrementResources(url)
  } catch (error) {
    console.error(`Failed to preload ${type}: ${url}`, error)

    // Clean up video cache on error
    if (type === ResourceType.Video) {
      videoManager.clearCache(url)
    }

    decrementResources(url)
  }
}
