import { BetHistoryPayload, BetHistorySubtotalPayload } from '@ikigaians/house'
import { create } from 'zustand'
import { devtools, subscribeWithSelector } from 'zustand/middleware'

import { getCheckResultValidFn } from '@repo/utils/common/gameResult'
import { connectIns } from '@repo/utils/socket'

export type DayGroupedHistory = Record<string, number[]> // Value is array of history id

type HistoryType = {
  next?: number
  histories: BetHistoryPayload[]
  groupedHistoryId: DayGroupedHistory
  daySubtotals: Record<string, BetHistorySubtotalPayload>
  currentHistoryContent: BetHistoryPayload | null
  addHistories: (val: BetHistoryPayload[]) => void
  addDaySubtotal: (val: BetHistorySubtotalPayload[]) => void
}

export const getDateGroup = (date: number) => {
  return new Date(date).toLocaleDateString('en-gb', {
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
  })
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useHistoryStore`,
  store: 'HistoryStore',
  serialize: {
    options: true,
  },
}

export const useHistoryStore = create<HistoryType>()(
  devtools(
    subscribeWithSelector((set) => ({
      histories: [],
      groupedHistoryId: {},
      daySubtotals: {},
      currentHistoryContent: null,
      addHistories: (val: BetHistoryPayload[]) => {
        set(
          (state) => {
            const sorted = state.histories
              .filter((oldHistory) => !val.some((newHistory) => newHistory.id === oldHistory.id))
              .concat(val)
              // Check timestamps exist
              .filter((h) => h.roundCreatedAt && h.roundFinishedAt)
              // Filter out invalid results
              .filter((history) => {
                const { status, result, gameType } = history
                // Table cancelled will not have result
                if (status !== 'finished') return true
                // Check if result is valid
                if (!result) return false
                const checkResultValid = getCheckResultValidFn(gameType)
                return checkResultValid(result)
              })
              .sort((a, b) => {
                const aCreatedAt = a.roundCreatedAt ?? 0
                const bCreatedAt = b.roundCreatedAt ?? 0
                return bCreatedAt - aCreatedAt
              })
            const daySubTotalKeys = Object.keys(state.daySubtotals)
            const groupedHistory = groupHistoryByDate(sorted, daySubTotalKeys)

            const newState = { ...state, histories: sorted, groupedHistoryId: groupedHistory }
            return newState
          },
          undefined,
          'addHistories'
        )
      },
      addDaySubtotal: (val: BetHistorySubtotalPayload[]) => {
        set(
          (state) => {
            const daySubtotals = val.reduce(
              (acc, value) => {
                const { day } = value
                const dateStr = getDateGroup(day)
                acc[dateStr] = value
                return acc
              },
              {} as Record<string, BetHistorySubtotalPayload>
            )
            const newState = {
              ...state,
              daySubtotals: { ...state.daySubtotals, ...daySubtotals },
            }
            return newState
          },
          undefined,
          'addDaySubtotal'
        )
      },
    })),
    DEVTOOLS_CONFIG
  )
)

export function groupHistoryByDate(history: BetHistoryPayload[], _daySubtotalKeys: string[]): DayGroupedHistory {
  const groupedHistory = history.reduce((acc, value) => {
    const { roundCreatedAt } = value
    if (!roundCreatedAt) return acc

    const dateStr = getDateGroup(roundCreatedAt)
    const prevGroup = acc[dateStr] ?? []

    prevGroup.push(value.id)
    acc[dateStr] = prevGroup

    return acc
  }, {} as DayGroupedHistory)

  const days = Object.keys(groupedHistory)
  if (days.length) getHistoryDaySubtotal(days)

  return groupedHistory
}

export function getHistoryDaySubtotal(daysStr: string[]) {
  if (!daysStr.length) return

  function convertToUnixUTCTimestamp(dateString: string): number {
    const [day, month, year] = dateString.split('/').map(Number)
    const date = new Date(Date.UTC(year, month - 1, day)) // month is 0-indexed
    return date.getTime()
  }

  const days = daysStr.map(convertToUnixUTCTimestamp)
  connectIns?.subtotalBetHistory(days)
}
