import { DesktopModalType } from '@repo/ui/store/useLayoutStore'

interface ScrollOptions {
  modalType?: DesktopModalType
  elementId?: string
  // The unit is pixels (px), representing the distance from the top of the scroll container to the top of the element.
  offset?: number
}

export function scrollWrap(options: ScrollOptions = {}) {
  const { modalType, elementId, offset = 0 } = options

  const findScrollWrap = (): HTMLDivElement | null => {
    let scrollWrapElement: HTMLDivElement | null = null

    if (modalType) {
      const modalElement = document.querySelector(`[data-modal-type="${modalType}"]`)
      scrollWrapElement = (modalElement?.querySelector('.scrollWrap') as HTMLDivElement) || null
    } else {
      scrollWrapElement = document.querySelector('.scrollWrap') as HTMLDivElement
    }

    return scrollWrapElement
  }

  const scroll = (): boolean => {
    const scrollWrap = findScrollWrap()
    if (!scrollWrap) return false

    // If elementId is provided, scroll to that element
    if (elementId) {
      const section = document.getElementById(elementId)

      if (!section) return false

      // Check if elements have actual dimensions
      if (section.offsetHeight === 0 || scrollWrap.offsetHeight === 0) return false

      scrollWrap.scrollTop = section.offsetTop - scrollWrap.offsetTop - offset
    } else {
      // Otherwise just scroll to top
      scrollWrap.scrollTo(0, 0)
    }
    // console.log('scroll', modalType, elementId, scrollWrap)
    return true
  }

  return requestAnimationFrame(scroll)
}

export function scrollWrapToTop(dataModalType?: DesktopModalType) {
  return scrollWrap({ modalType: dataModalType })
}

export async function imageFormatSupport(): Promise<{ webp: boolean }> {
  let webpSupport = false

  try {
    const webpBlop = await fetch('data:image/webp;base64,UklGRhoAAABXRUJQVlA4TA0AAAAvAAAAEAcQERGIiP4HAA==').then((r) => r.blob())
    webpSupport = await createImageBitmap(webpBlop)
      .then(() => true)
      .catch(() => false)
  } catch (error) {
    console.error('webp not supported:', error)
    webpSupport = false
  }

  return {
    webp: webpSupport,
  }
}
