/**
 * Duration of a single animation frame in seconds (30 FPS).
 * Use this for all keyframe/animation timing calculations to keep frame rates consistent.
 */
export const ONE_FRAME_TIME_SEC = 1 / 30

/**
 * Animation delay offset (in seconds) for countdown transitions.
 * Compensates for interpolation timing to ensure countdown numbers
 * reach their final centered position precisely.
 * Without this delay, numbers would arrive at center position prematurely
 * due to easing/interpolation curves.
 */
export const COUNT_DOWN_DELAY_SEC = 0.25

/**
 * After bet ends, no more bets text show up,
 * it will take 2.5 seconds to complete the animation.
 */
export const NO_MORE_BETS_ANIMATION_DURATION = 2500
