import { BrowserRouter, Routes as ReactRoutes, Route } from 'react-router-dom'

import '@repo/utils/locales/index'
import HistoryContentRoute from './history-content/HistoryContentRoute'

// Function to determine the base path from the current URL
const APP_NAMES = ['roulette', 'sicbo', 'baccarat']
function getBasename(): string {
  const currentPath = window.location?.pathname
  const segments = currentPath.split('/').filter(Boolean)

  // Check if the first segment is one of our known app names
  if (segments.length > 0 && APP_NAMES.includes(segments[0])) {
    return `/${segments[0]}`
  }

  return '/'
}

interface RoutesProps {
  Root: React.FC
}
export default function Routes({ Root }: RoutesProps) {
  const basename = getBasename()

  return (
    <BrowserRouter basename={basename}>
      <ReactRoutes>
        <Route
          path="/history-content"
          element={<HistoryContentRoute />}
        />
        <Route
          path="/*"
          element={<Root />}
        />
      </ReactRoutes>
    </BrowserRouter>
  )
}
