import { useStageStore } from '@repo/ui/store/useStageStore'
import type { AnimatedSprite, Application, Container, DestroyOptions, Graphics, Text } from 'pixi.js'
import { Sprite } from 'pixi.js'

// Define the GameObject type that can be any PIXI.js display object
export type GameObject = Sprite | Text | AnimatedSprite | Container | Graphics

// Define the stage type
export type StageType = {
  core?: {
    stage?: Application['stage']
  }
}

// Default destroy options for PIXI.js objects
export const DESTROY_OPTIONS: DestroyOptions = {
  children: true,
  textureSource: true,
}

/**
 * Utility function to kill (destroy and remove) PIXI.js game objects
 * @param refs - Array of refs to game objects or arrays of game objects
 * @param stage - Optional stage to remove objects from. If not provided, will use the stage from useStageStore
 */
export const killSprites = (refs: React.RefObject<GameObject | GameObject[] | null>[], stage?: Application | null): void => {
  const core = stage || useStageStore.getState().core

  const removeObject = (obj: GameObject): void => {
    obj?.destroy(DESTROY_OPTIONS)
    if (core?.stage) {
      core.stage.removeChild(obj)
    }
  }

  refs.forEach((ref) => {
    if (Array.isArray(ref.current)) {
      ref.current.forEach(removeObject)
      ref.current = []
    } else if (ref.current) {
      removeObject(ref.current)
      ref.current = null
    }
  })
}

/**
 * Interface for 9-slice sprite configuration
 */
export interface NineSliceConfig {
  left: number
  top: number
  width: number
  height: number
  dpr: number
  ratio: number
  color: string
  factor: number
  alpha?: number
  isCompact?: boolean
  isNormal?: boolean
}
