import { cn, isDesktop, isMobileDevice } from '@repo/utils/common/common'
import React, { useCallback, useEffect, useRef, useState } from 'react'

import useLoadingDissolveEffect from '@repo/ui/pixi-react/effects/loadingDissolve/useLoadingDissolveEffect'

import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'

import './LoadingLogo.scss'

interface LoadingLogoProps {
  topIcon: React.ReactNode
  gameIcon: React.ReactNode
  bottomIcon: React.ReactNode
  gameIconDissolve?: string
  loadingOverlayRef: React.RefObject<HTMLDivElement | null>
  gameId: 'roulette' | 'sicbo' | 'baccarat'
}

const DissolveEffect = ({
  gameIconDissolve,
  gameIconRef,
  shouldRender,
  gameId,
}: {
  gameIconDissolve: string
  gameIconRef: React.RefObject<HTMLDivElement | null>
  shouldRender: boolean
  gameId: 'roulette' | 'sicbo' | 'baccarat'
}) => {
  useLoadingDissolveEffect({ gameIconDissolve, gameIconRef, shouldRender, gameId })

  return null
}

const PPA_OFFSETS = {
  baccarat: {
    desktop: { x: -6, y: -4 },
    mobile: { x: 0, y: -21 },
    mobileCompact: { x: 0, y: -20 },
  },
  sicbo: {
    desktop: { x: -7, y: -2 },
    mobile: { x: 0, y: -20 },
    mobileCompact: { x: 0, y: -18.9 },
  },
} as const

const DEFAULT_OFFSET = { x: 0, y: 0 }

function LoadingLogo({ topIcon, gameIcon, bottomIcon, gameIconDissolve, loadingOverlayRef, gameId }: LoadingLogoProps) {
  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const isLoadingFinished = useLoadingStore((s) => s.isLoadingFinished)
  const shouldFadeOut = loadingStage >= LoadingStageEnum.PostLoaded3
  const shouldRender = !isLoadingFinished
  const shouldTranslate = loadingStage >= LoadingStageEnum.PostLoaded1
  const gameIconRef = useRef<HTMLDivElement>(null)
  const wrapperRef = useRef<HTMLDivElement>(null)

  const [translateX, setTranslateX] = useState(0)
  const [translateY, setTranslateY] = useState(0)

  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  // Dynamically center the logo vertically
  const center = useCallback(() => {
    const factor = useLayoutStore.getState().rootEmFontSize

    // Currently only Baccarat has this animation
    if (shouldTranslate && isMobileDevice && gameId === 'baccarat') {
      const overlayEle = loadingOverlayRef.current
      const boundaryEle = document.querySelector('[data-loading-logo-boundary]')
      const boundary = parseFloat(boundaryEle?.getAttribute('data-loading-logo-boundary') || '0') * factor

      const { height } = overlayEle?.getBoundingClientRect() || { height: 0 }

      const center = height / 2
      const remain = height - boundary
      const remainCenter = remain / 2

      setTranslateX(0)
      setTranslateY(-(center - remainCenter))
      return
    }

    const deviceKey = isDesktop ? 'desktop' : isSmallDevice ? 'mobileCompact' : 'mobile'
    const offsets = PPA_OFFSETS[gameId as keyof typeof PPA_OFFSETS]?.[deviceKey]
    if (offsets) {
      setTranslateX(offsets.x)
      setTranslateY(offsets.y)
    } else {
      setTranslateX(DEFAULT_OFFSET.x)
      setTranslateY(DEFAULT_OFFSET.y)
    }
  }, [gameId, isSmallDevice, loadingOverlayRef, shouldTranslate])

  useEffect(() => {
    if (!shouldRender) return

    center()

    const overlayEle = loadingOverlayRef.current
    const observer = new ResizeObserver(center)

    if (overlayEle) observer.observe(overlayEle)
    else observer.disconnect()

    return () => {
      observer.disconnect()
    }
  }, [shouldTranslate, shouldRender, loadingOverlayRef, gameId, isSmallDevice, center])

  return (
    <>
      {shouldRender && gameIconDissolve && (
        <DissolveEffect
          gameIconDissolve={gameIconDissolve}
          gameIconRef={gameIconRef}
          shouldRender={shouldRender}
          gameId={gameId}
        />
      )}
      {shouldRender && (
        <div
          key="loadingLogo"
          ref={wrapperRef}
          className={cn('loadingLogo origin-center', `${gameId}`, {
            shouldTranslate,
            shouldFadeOut,
          })}
          style={{ transform: `translate(${translateX}px, ${translateY}px)` }}
        >
          <div className="topIcon">{topIcon}</div>

          <div
            className="gameIcon"
            ref={gameIconRef}
          >
            {gameIcon}
          </div>

          <div className="bottomIcon">{bottomIcon}</div>
        </div>
      )}
    </>
  )
}

export default LoadingLogo
