import { MessageHistoryPayload } from '@ikigaians/house'
import { create } from 'zustand'
import { devtools, type DevtoolsOptions } from 'zustand/middleware'

export enum RoomType {
  Live,
  Support,
}

interface ChatStoreState {
  isBanned: boolean
  isTableClosed: boolean
  queueLive: MessageHistoryPayload[]
  queueSupport: MessageHistoryPayload[]
  supportChatUnread: number
}

interface ChatStoreActions {
  addBubble: (val: MessageHistoryPayload[], roomType: RoomType) => void
}

export interface IChatGameResult {
  winNumber: number
  date: number
  gameNo: string
}

const DEVTOOLS_CONFIG: DevtoolsOptions = {
  name: `${document.title} - useChatStore`,
  store: 'ChatStore',
  serialize: {
    options: true,
  },
}

export const useChatStore = create<ChatStoreState & ChatStoreActions>()(
  devtools(
    (set) => ({
      isBanned: false,
      isTableClosed: false,
      queueLive: [],
      queueSupport: [],
      supportChatUnread: 0,
      addBubble: (val, type) => {
        set(
          (state) => {
            const queueKey = type === RoomType.Live ? 'queueLive' : 'queueSupport'

            // Prevent duplicates when there is only one message in the whole history. (FND-711)
            const isSingleIdentical = state[queueKey].length === 1 && val.length === 1 && val[0].sender?.userId === 'support-system'

            if (isSingleIdentical) return { [queueKey]: state[queueKey] }

            // If there's only one message and is not from the support system default message, just add the message
            if (val.length === 1) {
              return { [queueKey]: state[queueKey].concat(val) }
            }

            // Prevent duplicate messages received from RGS
            // If the last message received from RGS is the same as the last message in the store, do not add the new message
            const lastReceiveMessage = val[val.length - 1]
            const lastCurrentMessage = state[queueKey][state[queueKey].length - 1]
            if (
              val[val.length - 1] !== undefined &&
              state[queueKey][state[queueKey].length - 1] !== undefined &&
              'createdAt' in lastReceiveMessage.content &&
              'createdAt' in lastCurrentMessage.content
            ) {
              if (lastCurrentMessage.content.createdAt === lastReceiveMessage.content.createdAt) {
                return { [queueKey]: state[queueKey] }
              }
            }

            return { [queueKey]: val }
          },
          undefined,
          'addBubble'
        )
      },
    }),
    DEVTOOLS_CONFIG
  )
)
