import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { getBetSound } from '@repo/ui/utils/common'
import { playSound } from '@repo/utils/common/sound'
import { useTranslation } from 'react-i18next'
import CenterModal from './CenterModal'

export function resumeAutoPlay() {
  const autoplayRoundArr = [5, 10, 25, 100]

  const boardSnapshot = useAutoPlayStore.getState().boardSnapshot
  const boardHistory = useCartStore.getState().boardHistory
  const roundLastChosen = useAutoPlayStore.getState().roundLastChosen
  useAutoPlayStore.setState({
    roundRemaining: autoplayRoundArr[roundLastChosen as number],
    roundSelected: roundLastChosen,
    isAutoPlay: true,
    confirmModalType: null,
    autoPlayResumed: true,
  })
  useCartStore.setState({ boardHistory: [boardSnapshot as Record<string, number>] })
  getBetSound(boardHistory, boardSnapshot as Record<string, number>)
}

export default function AutoPlayFinishedModal() {
  const confirmModalType = useAutoPlayStore((s) => s.confirmModalType)
  const { t } = useTranslation()
  const handleContinue = () => {
    playSound(['click'])
    const gameState = useBettingStore.getState().gameState

    // Store the state and return if is in bet not allowed stage
    if (gameState !== 1) {
      useAutoPlayStore.setState({ autoPlayResumed: false, confirmModalType: null })
      return
    }

    resumeAutoPlay()
  }

  const handleStop = () => {
    playSound(['click'])
    useAutoPlayStore.setState({ confirmModalType: null })
  }
  return (
    <CenterModal
      open={confirmModalType === 'AutoPlayFinished'}
      icon={'autoplayFinished'}
      title={t('COMMON.AUTOPLAY_FINISHED')}
      titleClassName="pl-[0.2rem]"
      text={t('COMMON.AUTOPLAY_PROMPT4')}
      confirmText={t('COMMON.CONTINUE')}
      onConfirm={handleContinue}
      cancelText={t('COMMON.STOP')}
      onCancel={handleStop}
    />
  )
}
