import { BetHistoryPayload } from '@ikigaians/house'
import { changeLanguage } from 'i18next'
import { useEffect, useState } from 'react'

import GameHistoryContent, { GameHistoryContentHeader } from '@repo/ui/components/menu/common/history/GameHistoryContent'
import { useHistoryStore } from '@repo/ui/store/useHistoryStore'
import { cn } from '@repo/utils/common/common'
import { type LanguageCode } from '@repo/utils/locales/constants'

import './HistoryContentRoute.scss'

interface PostMessageData {
  betHistoryPayload: BetHistoryPayload
  lang: LanguageCode
}

// Helper function to validate message data structure
function isValidPostMessageData(data: unknown): data is PostMessageData {
  if (!data || typeof data !== 'object') return false

  const messageData = data as Record<string, unknown>

  // Check if betHistoryPayload exists and has basic required structure
  const hasValidPayload = Boolean(
    messageData.betHistoryPayload && typeof messageData.betHistoryPayload === 'object' && messageData.betHistoryPayload !== null
  )

  // Check if lang is a valid string (optional validation could check against LanguageCode values)
  const hasValidLang = !messageData.lang || typeof messageData.lang === 'string'

  return hasValidPayload && hasValidLang
}

export default function HistoryContentRoute() {
  const [hasReceivedMessage, setHasReceivedMessage] = useState(false)

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      // Data structure validation - accept messages from any origin but validate the data
      if (!isValidPostMessageData(event.data)) {
        console.warn('Received message with invalid data structure:', event.data)
        return
      }

      const { betHistoryPayload, lang } = event.data

      if (betHistoryPayload) {
        useHistoryStore.setState({ currentHistoryContent: betHistoryPayload })
        setHasReceivedMessage(true)
      }

      if (lang) {
        changeLanguage(lang)
      }
    }

    window.addEventListener('message', handleMessage)

    return () => {
      window.removeEventListener('message', handleMessage)
      useHistoryStore.setState({ currentHistoryContent: null })
    }
  }, [])

  return (
    hasReceivedMessage && (
      <div className={cn('history-content-route', 'w-[20.5rem]')}>
        <GameHistoryContentHeader />
        <GameHistoryContent />
      </div>
    )
  )
}
