import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { LoadingResourceEnum, LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { cn, isDesktop, isMobileDevice } from '@repo/utils/common/common'
import { Application } from 'pixi.js'
import { memo, useCallback, useEffect, useRef } from 'react'
import { useStageStore } from '../../store/useStageStore'
import { DIST_TO_LEFT_EDGE } from '../utils/mainBoardUtils'
interface StageProps {
  className?: string
}

/*
 * This component is used to create a centralized stage for the pixi app,
 * the canvas element will be resized to the wrapper div.
 *
 * Each PIXI's application instance creates its own WebGL context, textures, and render loops.
 * WebGL has a limit on the number of contexts a browser can handle,
 * and multiple instances will lead to higher memory usage and performance issues.
 * So here only uses one, and use "useStageStore" to store the core instance.
 */
function Stage({ className }: StageProps) {
  const decrementResources = useLoadingStore((s) => s.decrementResources)
  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const core = useStageStore((s) => s.core)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const wrapperRef = useRef<HTMLDivElement>(null)
  const isInitializingRef = useRef(false)

  const resizeTimeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null)
  const animationFrameRef = useRef<number | null>(null)

  const { isIdleTimeout, isExpired, isMultiSession } = useNetworkStore((state) => ({
    isIdleTimeout: state.isIdleTimeout,
    isExpired: state.isExpired,
    isMultiSession: state.isMultiSession,
  }))

  const resize = useCallback(() => {
    /*
     * Need to do manual resizing, since "resizeTo" is drastically unreliable when changing window size rapidly,
     * border lines will fluctuate.
     */
    if (!wrapperRef.current || !canvasRef.current || !core || !core.renderer) return

    const root = document.getElementById('root')
    const { width, height } = root?.getBoundingClientRect() ?? { width: 0, height: 0 }
    const isCompact = useConfigStore.getState().IS_SMALL_DEVICE && isMobileDevice
    const ratio = useLayoutStore.getState().rootEmFontSize
    const factor = ratio / 16

    const offset = isCompact || isDesktop ? DIST_TO_LEFT_EDGE * factor : 0

    core.renderer.resolution = Math.max(Math.min(window.devicePixelRatio, 2), 1)
    core.renderer.resize(width + offset, height)
    canvasRef.current.style.width = `${width + offset}px`
    canvasRef.current.style.height = `${height}px`
    wrapperRef.current.style.width = `${width + offset}px`
    wrapperRef.current.style.height = `${height}px`
    /*
     * Canvas's left edge cannot align with MainBoard's left edge in:
     * 1. compact mode => mainboard's left edge is not most left edge of the screen.
     * 2. desktop mode => traffic light's left outward gradient needs to be visible
     */
    wrapperRef.current.style.left = `${offset * -1}px`
    core.render()
  }, [core])

  // Avoid resize triggering too frequently
  const debouncedResize = useCallback(() => {
    // During loading phase, canvas must be resized eagerly
    const delay = loadingStage >= LoadingStageEnum.PostLoaded4 ? 16 : 0

    if (resizeTimeoutRef.current) {
      clearTimeout(resizeTimeoutRef.current)
    }
    if (animationFrameRef.current) {
      cancelAnimationFrame(animationFrameRef.current)
    }

    // using a more aggressive debounce delay 16ms for ~60fps,
    // for smoother resize handling during rapid viewport changes
    resizeTimeoutRef.current = setTimeout(() => {
      animationFrameRef.current = requestAnimationFrame(resize)
    }, delay)
  }, [resize, loadingStage])

  // Init core
  const init = useCallback(async () => {
    if (!canvasRef.current) return
    // If core is already initialized, or is initializing, skip initialization
    if (useStageStore.getState().core || isInitializingRef.current) return

    isInitializingRef.current = true
    const app = new Application()
    await app.init({
      width: 0,
      height: 0,
      backgroundAlpha: 0,
      antialias: false,
      powerPreference: 'low-power',
      resolution: Math.max(Math.min(window.devicePixelRatio, 2), 1),
      // autoStart: false,
      canvas: canvasRef.current,
    })

    useStageStore.setState({ core: app })
    useLayoutStore.setState({ isPixiLoading: false })
    useLoadingStore.setState({ loadingStage: LoadingStageEnum.Idle })

    console.debug('Pixi core finished initialization.')

    isInitializingRef.current = false
    decrementResources(LoadingResourceEnum.PixiCore)
    return app
  }, [decrementResources])

  // Init core and ticker when preload completes
  useEffect(() => {
    init()

    return () => {
      const currentCore = useStageStore.getState().core
      if (currentCore) {
        currentCore.destroy()
        useStageStore.setState({ core: null })
      }
    }
  }, [init])

  // Stop/Start ticker based on network state
  useEffect(() => {
    const currentCore = useStageStore.getState().core
    if (!currentCore) return

    if (isIdleTimeout || isExpired || isMultiSession) {
      if (currentCore.ticker.started) {
        // Check 'started' to avoid unnecessary method calls for performance optimization and ensure idempotency
        currentCore.ticker.stop()
      }
    } else {
      if (!currentCore.ticker.started) {
        // Check 'started' to avoid unnecessary method calls for performance optimization and ensure idempotency
        currentCore.ticker.start()
      }
    }
  }, [isIdleTimeout, isExpired, isMultiSession])

  // Resizing
  useEffect(() => {
    if (!wrapperRef.current || !core) return
    const root = document.getElementById('root')

    // Due to LoadingScreen, there might be multiple #MainBoard elements in the DOM,
    // we only want to observe the first one to avoid multiple resize observers.
    const allMainBoards = document.querySelectorAll('#MainBoard')
    if (allMainBoards.length !== 1) return

    const resizeObserver = new ResizeObserver(debouncedResize)
    resizeObserver.observe(root as HTMLElement)
    resizeObserver.observe(allMainBoards[0] as HTMLElement)

    return () => {
      resizeObserver.disconnect()

      if (resizeTimeoutRef.current) {
        clearTimeout(resizeTimeoutRef.current)
      }
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current)
      }
    }
  }, [core, debouncedResize, loadingStage, resize])

  return (
    <div
      ref={wrapperRef}
      className={cn(className)}
    >
      <canvas
        ref={canvasRef}
        className="relative"
      ></canvas>
    </div>
  )
}

export default memo(Stage)
