import { useCartStore } from '@repo/ui/store/useCartStore'
import Decimal from 'decimal.js-light'

export const convertLabel = (val: number) => {
  if (val < 1000) {
    return new Decimal(val).toDecimalPlaces(2, Decimal.ROUND_DOWN).toString()
  }

  let num = new Decimal(val)
  let unit = ''

  if (num.gte(1_000_000_000)) {
    num = num.div(1_000_000_000)
    unit = 'B'
  } else if (num.gte(1_000_000)) {
    num = num.div(1_000_000)
    unit = 'M'
  } else {
    num = num.div(1000)
    unit = 'K'
  }

  if (num.dp() > 1) {
    num = num.toDecimalPlaces(1, Decimal.ROUND_DOWN)
    unit += '+'
  }

  return num.toString() + unit
}

export const calculateBetDelta = (
  currBets: Record<string, number>,
  prevBets: Record<string, number>,
  gameBetKeys: string[]
): Record<string, number> => {
  const allKeys = [...Object.keys(prevBets), ...Object.keys(currBets)]
  const filteredKeys = allKeys.filter((k) => gameBetKeys.includes(k)) // filter keys that is legal for the game
  const uniqueKeys = [...new Set(filteredKeys)] // use Set to collect unique keys

  const delta = uniqueKeys.reduce(
    (acc, key) => {
      const prevVal = new Decimal(prevBets[key] ?? 0)
      const currVal = new Decimal(currBets[key] ?? 0)
      const diff = currVal.minus(prevVal)

      if (!diff.isZero()) {
        acc[key] = diff.toNumber()
      }
      return acc
    },
    {} as Record<string, number>
  )

  return delta
}

export const calculateCountDelta = (prevBets: Record<string, number>, currBets: Record<string, number>) => {
  const currBetKeys = Object.keys(currBets)
  const prevBetKeys = Object.keys(prevBets)

  if (currBetKeys.length > prevBetKeys.length) return 1
  else if (currBetKeys.length < prevBetKeys.length) return -1
  else {
    const keysChanged = currBetKeys.some((key) => !prevBetKeys.includes(key)) || prevBetKeys.some((key) => !currBetKeys.includes(key))
    return keysChanged ? 1 : 0
  }
}

export function isLowBalance(val: Record<string, number>): boolean {
  const walletBalance = useCartStore.getState().walletBalance
  const totalBet = useCartStore.getState().getTotalBet([val])
  return totalBet > walletBalance
}
