import { AnimatePresence, MotionConfigProps, motion } from 'framer-motion'

import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'

import { useRef } from 'react'
import LoadingBackground from './LoadingBackground'
import LoadingFooter from './LoadingFooter'
import LoadingLogo from './LoadingLogo'
import LoadingMessages from './LoadingMessages'

interface LoadingOverlayProps {
  loadingOverlayAnimation: MotionConfigProps
  boundedStyle: React.CSSProperties
  desktopBG: string
  mobileBG: string
  topIcon: React.ReactNode
  gameIcon: React.ReactNode
  bottomIcon: React.ReactNode
  gameId: 'roulette' | 'sicbo' | 'baccarat'
  gameIconDissolve?: string
  loadingTextOptions?: readonly string[]
}

export default function LoadingOverlay(props: LoadingOverlayProps) {
  const {
    loadingOverlayAnimation,
    boundedStyle,
    desktopBG,
    mobileBG,
    topIcon,
    gameIcon,
    bottomIcon,
    gameIconDissolve,
    loadingTextOptions,
    gameId,
  } = props
  const loadingOverlayRef = useRef<HTMLDivElement>(null)
  const loadingStage = useLoadingStore((s) => s.loadingStage)
  const shouldShowContent = loadingStage >= LoadingStageEnum.Idle && loadingStage < LoadingStageEnum.PostLoaded1

  return (
    <>
      <div
        className="loadingOverlay bg-black"
        style={{ ...boundedStyle }}
        ref={loadingOverlayRef}
      >
        <LoadingBackground
          desktopBG={desktopBG}
          mobileBG={mobileBG}
          gameId={gameId}
        />
        <LoadingLogo
          topIcon={topIcon}
          gameIcon={gameIcon}
          bottomIcon={bottomIcon}
          gameId={gameId}
          gameIconDissolve={gameIconDissolve}
          loadingOverlayRef={loadingOverlayRef}
        />
      </div>

      <motion.div
        className="loadingOverlay"
        {...loadingOverlayAnimation}
      >
        <AnimatePresence>
          {shouldShowContent && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: shouldShowContent ? 1 : 0 }}
              transition={shouldShowContent ? { duration: 0.7, ease: 'linear' } : { duration: 2 }}
              className="loadingContent"
              exit={{ opacity: 0 }}
            >
              <LoadingMessages loadingTextOptions={loadingTextOptions} />
              <LoadingFooter />
            </motion.div>
          )}
        </AnimatePresence>
      </motion.div>
    </>
  )
}
