import { cn } from '@repo/utils/common/common'

import big from '@repo/ui/assets/sicbo/youWin/big-dome.webp'
import small from '@repo/ui/assets/sicbo/youWin/small-dome.webp'
import triple from '@repo/ui/assets/sicbo/youWin/triple-dome.webp'
import Image from '@repo/ui/components/common/Image'
import { chooseDomeType } from '@repo/ui/utils/types/betType.sicbo'

// * Note: MUST import the style here for shared component "SicboWinNumberResult"
import './YouWin.scss'

interface Props {
  className?: string
  betResult: number[]
}

const Dome = ({ betResult, className }: Props) => {
  const type = chooseDomeType(betResult)
  const total = betResult.reduce((a, b) => a + b, 0)
  return (
    <div
      id="Dome"
      className={cn('relative h-[9.3125rem] w-[8.6875rem]', className)}
    >
      <Image
        src={type === 'small' ? small : type === 'big' ? big : triple}
        alt={type === 'small' ? 'Small Dome' : type === 'big' ? 'Big Dome' : 'Triple Dome'}
        className="w-full"
      />
      <span className="totalText absolute left-[50%] top-[2.565rem] -translate-x-1/2">Total</span>
      <span className="sumText absolute left-[50%] top-[3.5rem] -translate-x-1/2">{total}</span>
    </div>
  )
}

export default Dome
