import { Connect, GameEvent } from '@ikigaians/connect'
import { JurisdictionPayloadHelper, TableRoundStatus } from '@ikigaians/house'
import { NO_MORE_BETS_ANIMATION_DURATION } from '@repo/ui/constants/animation'
import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { LoadingResourceEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { useWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import { isEnvDev } from '@repo/utils/common/common'
import { BetState } from '@repo/utils/types/betstate'
import { t } from 'i18next'
import { v4 as uuidv4 } from 'uuid'
import { AUTOPLAY_PLAYER_COUNT_DIFF } from '../constants'
import { updateBetPeriod, updateBetState, updateBettingFromPayload, updateChipTray, updateRoundInfo } from '../state-updaters'
import {
  RoundInfo,
  TableBetStopEvent,
  TableDealEvent,
  TableFinishEvent,
  TableMaintenanceEvent,
  TablePauseEvent,
  TableResumeEvent,
  TableStartEvent,
  TableStateEvent,
  TableWinnersEvent,
} from '../types'

export class TableEventHandlers {
  static onTableState(msg: TableStateEvent, connectIns: Connect, getHistory: () => void) {
    const ev = msg
    console.debug(GameEvent.TABLE_STATE, ev)
    updateBetState(BetState.Bet)
    connectIns.syncWallet()

    const decrementResources = useLoadingStore.getState().decrementResources
    decrementResources(LoadingResourceEnum.SyncWallet)

    const { data } = ev
    if (!data) return

    const { table, jurisdiction, betSettings, activeRoundBets, player, activeRound } = data
    const { betPeriod, gameType, link, streams, pause, maintenance } = table ?? {}
    const { status } = activeRound ?? {}

    useBettingStore.setState({ betPeriod: betPeriod ?? 0 })

    getHistory()
    updateChipTray(data)
    updateBettingFromPayload(data)

    useConfigStore.setState({ isPaused: !!pause })
    useWinNumberStore.getState().initPrevRounds(data)
    updateRoundInfo(data)

    const { GAME_TABLE } = useConfigStore.getState()
    connectIns.listGameChatHistory(GAME_TABLE)
    connectIns.listSupportChatHistory()

    if (activeRoundBets && Array.isArray(activeRoundBets) && activeRoundBets.length > 0) {
      useBettingStore.setState({ isRetrieveBet: true })
      useCartStore.setState({ boardHistory: activeRoundBets })
    }

    // Jurisdiction
    const jurisdictionPayloadHelper = new JurisdictionPayloadHelper(jurisdiction)
    const AUTOPLAY_ALLOWED = jurisdictionPayloadHelper.getAutoplayAllowed()
    const FULLSCREEN_ALLOWED = jurisdictionPayloadHelper.getFullscreenAllowed()
    const SHOW_LOBBY_ALLOWED = jurisdictionPayloadHelper.getShowLobby()
    const SHOW_ACTIVE_SESSION_ALLOWED = jurisdictionPayloadHelper.getShowActiveSessionCounter()
    const SHOW_SUPPORT_CHAT = jurisdictionPayloadHelper.getShowSupportChat()
    const BET_OPTIONS = jurisdictionPayloadHelper.getBetOptions()
    const JURISDICTION = {
      AUTOPLAY_ALLOWED,
      FULLSCREEN_ALLOWED,
      SHOW_LOBBY_ALLOWED,
      SHOW_ACTIVE_SESSION_ALLOWED,
      SHOW_SUPPORT_CHAT,
      BET_OPTIONS,
    }
    // Enable all UI in development mode
    if (!isEnvDev) useConfigStore.setState({ JURISDICTION })

    const GAME_TYPE = gameType
    if (GAME_TYPE) useConfigStore.setState({ GAME_TYPE })

    const screenName = player?.nickname
    if (screenName) useSettingStore.setState({ screenName })

    // Links for returning to the lobby
    const LOBBY_URL = link?.lobby
    if (LOBBY_URL) useConfigStore.setState({ LOBBY_URL })

    // Streams
    if (streams) {
      useConfigStore.setState({ streams })
    } else {
      useLayoutStore.setState({ isCanStream: false })
    }

    // Bet Settings
    if (betSettings) {
      useConfigStore.setState({
        betTypesLimit: betSettings.betTypesLimit,
        tableLimit: betSettings.tableLimit,
        tableMaxPayout: betSettings.tableMaxPayout ?? 0,
      })
    }

    // Maintenance
    if (maintenance) {
      const { startTime, endTime = Infinity } = maintenance
      const now = Date.now()
      if (now >= startTime && now < endTime) {
        useNetworkStore.setState({ isMaintenance: true })
        connectIns.close()
      }
      useConfigStore.setState({ maintenanceStartTime: startTime, maintenanceEndTime: endTime })
    }

    // Bet stopped
    if (status === TableRoundStatus.BET_STOPPED) {
      /**
       * Minus 2.5 seconds here because we need to skip no more bets disappearing animation,
       * which, at the time of writing, takes 2.5 seconds to complete
       */
      useBettingStore.setState({ betEnd: Date.now() - NO_MORE_BETS_ANIMATION_DURATION })
    }

    const debugEntries: [string, unknown][] = [
      ['streams', streams],
      ['jurisdiction', JURISDICTION],
      ['lobby url', LOBBY_URL],
    ]

    debugEntries.forEach(([label, payload]) => useDebugStore.getState().setMsg(`${label}: ${JSON.stringify(payload, null, '\t')}`))
  }

  static onTableStart(msg: TableStartEvent, connectIns: Connect, getBetDetail: () => { [k: string]: number } | null) {
    const ev = msg
    console.debug(GameEvent.TABLE_START, ev)
    useBettingStore.setState({ resetBoard: true })
    updateBetState(BetState.Bet)
    connectIns.syncWallet()

    const { data } = ev
    if (!data) return

    const { betPeriod } = data.table ?? {}
    updateBetPeriod(betPeriod ?? 0)

    updateBettingFromPayload(data)
    const roundInfo = updateRoundInfo(data)

    // Bet peek for auto play
    this.handleAutoPlayBetPeek(roundInfo, connectIns, getBetDetail)

    const { decrementAutoPlayRound, isAutoPlay } = useAutoPlayStore.getState()
    if (isAutoPlay) {
      decrementAutoPlayRound()
      const boardHistory = useCartStore.getState().boardHistory
      const totalBet = useCartStore.getState().getTotalBet(boardHistory)
      useCartStore.setState({ totalBet })
    }

    // Reset bet stats on new round
    useCartStore.setState({ betStats: { amount: {}, count: {} } })
  }

  static onTableBetStop(msg: TableBetStopEvent) {
    const ev = msg
    console.debug(GameEvent.TABLE_BET_STOP, ev)
    updateBetState(BetState.WheelSpin)

    const { isBet } = useBettingStore.getState()
    // Only during the bet period, set betEnd to current time to stop betting
    if (isBet) useBettingStore.setState({ betEnd: Date.now() })
  }

  static onTableDeal(msg: TableDealEvent) {
    const ev = msg
    console.debug(GameEvent.TABLE_DEAL, ev)
    updateBetState(BetState.Settlement)

    const { data, timecode } = ev
    if (!data) return
    useWinAnimStore.getState().onTableDeal(data, timecode)
  }

  static onTableWinners(msg: TableWinnersEvent) {
    const ev = msg
    console.debug(GameEvent.TABLE_WINNERS, ev)
    const { data } = ev
    if (!data) return
    const winnerList = ev.data?.winners ?? []
    useWinNumberStore.setState({ winnerList: winnerList })
  }

  static onTableFinish(msg: TableFinishEvent, connectIns: Connect) {
    const ev = msg
    console.debug(GameEvent.TABLE_FINISH, ev)
    const { data, timecode } = ev
    useCartStore.setState({ totalBet: 0 })
    if (!data) return

    if (data.activeRound.status === TableRoundStatus.CANCELLED) {
      useToastStore.getState().addToast('info', t('COMMON.ROUND_CANCELLED'))
      useBettingStore.setState({ gameState: BetState.Settlement })
      useWinAnimStore.getState().animateChipRemoval()
      useConfigStore.setState({ tableFinished: true })

      if (useAutoPlayStore.getState().isAutoPlay) {
        useAutoPlayStore.setState({ roundRemaining: useAutoPlayStore.getState().roundRemaining + 1 })
      }

      if (useBettingStore.getState().isBet) {
        useBettingStore.getState().stopCountdown()
      }
      connectIns.syncWallet()
    } else {
      useWinAnimStore.getState().onTableFinished(data, timecode)
    }
  }

  static onTablePause(msg: TablePauseEvent) {
    const ev = msg
    console.debug(GameEvent.TABLE_PAUSE, ev)
    useConfigStore.setState({ isPaused: true })
  }

  static onTableResume(msg: TableResumeEvent) {
    const ev = msg
    console.debug(GameEvent.TABLE_RESUME, ev)
    useConfigStore.setState({ isPaused: false })
  }

  static onTableMaintenance(msg: TableMaintenanceEvent) {
    const ev = msg
    const { data } = ev
    if (!data) return

    const {
      table: { maintenance },
    } = data
    if (!maintenance) return

    const { startTime, endTime = Infinity } = maintenance
    useConfigStore.setState({ maintenanceStartTime: startTime, maintenanceEndTime: endTime })
    console.debug(GameEvent.TABLE_MAINTENANCE, ev)
  }

  private static handleAutoPlayBetPeek(roundInfo: RoundInfo, connectIns: Connect, getBetDetail: () => { [k: string]: number } | null) {
    const boardHistory = useCartStore.getState().boardHistory
    const lastHistory = boardHistory[boardHistory.length - 1]
    if (!lastHistory) return

    const betDetail = getBetDetail()
    if (!betDetail) return

    const { roundId, gameType, gameCode } = roundInfo
    const uuid = uuidv4()
    connectIns.betPeek(roundId, gameType, gameCode, betDetail, AUTOPLAY_PLAYER_COUNT_DIFF, uuid)
  }
}
