import { GameEvent } from '@ikigaians/connect'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useChatStore } from '@repo/ui/store/useChatStore'
import { PlayerStateEvent, WalletStateEvent } from '../types'

export class PlayerEventHandlers {
  static onPlayerState(msg: PlayerStateEvent) {
    const ev = msg
    console.debug(GameEvent.PLAYER_STATE, ev)
    const unreadMessages = ev.data?.player?.unread?.support ?? 0
    useChatStore.setState({ supportChatUnread: unreadMessages })
  }

  static onWalletState(msg: WalletStateEvent, getHistory: () => void) {
    const ev = msg
    console.debug(GameEvent.WALLET_STATE, ev)
    getHistory() // ensure history is updated after balance sync

    const minChipAmount = useCartStore.getState().chipList[0]
    const balance = ev.data?.wallet?.balance ?? 0
    const currencySymbol = ev.data?.wallet?.currencySymbol ?? useCartStore.getState().currencySymbol

    useCartStore.setState((s) => ({
      ...s,
      walletBalance: balance,
      remainedBalance: balance,
      currencySymbol,
      lowBalanceNotifyCount: balance < minChipAmount.amount ? s.lowBalanceNotifyCount + 1 : 1,
    }))
  }
}
