import { isMobileDevice } from '@repo/utils/common/common'
import { PreloadResource, ResourceType } from '@repo/utils/types/misc'

// pixi
import winNumberSpotlightJson from '@repo/ui/assets/pixi/winNumberSpotlight/winNumberSpotlight.json?url'
import winNumberSpotlight from '@repo/ui/assets/pixi/winNumberSpotlight/winNumberSpotlight.webp'
import winParticleDesktopJson from '@repo/ui/assets/pixi/winParticle/winParticleDesktop.json?url'
import winParticleDesktop from '@repo/ui/assets/pixi/winParticle/winParticleDesktop.webp'
import winParticleMobileJson from '@repo/ui/assets/pixi/winParticle/winParticleMobile.json?url'
import winParticleMobile from '@repo/ui/assets/pixi/winParticle/winParticleMobile.webp'

const pixiResources: PreloadResource = {
  winNumberSpotlightJson: { type: ResourceType.PixiJson, url: winNumberSpotlightJson },
  winNumberSpotlight: { type: ResourceType.Image, url: winNumberSpotlight },
  winParticleJson: { type: ResourceType.PixiJson, url: isMobileDevice ? winParticleMobileJson : winParticleDesktopJson },
  winParticle: { type: ResourceType.Image, url: isMobileDevice ? winParticleMobile : winParticleDesktop },
}

export default pixiResources
