import { GameEvent } from '@ikigaians/connect'
import { RoomType, useChatStore } from '@repo/ui/store/useChatStore'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { useToastStore } from '@repo/ui/store/useToastStore'
import { SYSTEM_MESSAGE_KEY_MAP } from '@repo/utils/common/system-msg'
import { t } from 'i18next'
import { GameMessageEvent, SupportMessageEvent, SystemMessageEvent } from '../types'

export class MessageEventHandlers {
  static onGameMessage(msg: GameMessageEvent) {
    const ev = msg
    const msgHistory = ev.data?.msgHistory
    console.debug(GameEvent.GAME_MESSAGE, msgHistory)
    if (msgHistory) {
      useChatStore.getState().addBubble(msgHistory, RoomType.Live)
    }
  }

  static onSupportMessage(msg: SupportMessageEvent) {
    const ev = msg
    const msgHistory = ev.data?.msgHistory
    console.debug(GameEvent.SUPPORT_MESSAGE, msgHistory)
    if (msgHistory) {
      useChatStore.getState().addBubble(msgHistory, RoomType.Support)
    }
  }

  static onSystemMessage(msg: SystemMessageEvent) {
    const ev = msg
    console.debug(GameEvent.SYSTEM_MESSAGE, ev)

    const msgId = msg.data?.sysMsg?.msgId

    if (!msgId) {
      console.error('SYSTEM_MESSAGE missing required fields')
      return
    }

    const messageMap = SYSTEM_MESSAGE_KEY_MAP[msg.data.sysMsg.msgId as keyof typeof SYSTEM_MESSAGE_KEY_MAP]
    if (messageMap) {
      const { localeKey, type, callback } = messageMap
      useToastStore.getState().addToast(type, t(localeKey))
      callback?.()
    } else {
      useDebugStore.getState().setMsg(`type: unknown, system msgId: ${msgId}`)
    }
  }
}
