import React from 'react'

type SupportedImageType = 'image/webp' | 'image/avif' | 'image/jpeg' | 'image/png'

type Props = {
  src: string
  ref?: React.RefCallback<HTMLImageElement>
  fallback?: string
  alt?: string
  className?: string
  width?: number
  height?: number
  style?: React.CSSProperties
  onClick?: () => void
  onMouseEnter?: () => void
  onMouseLeave?: () => void
}

const Image = ({ src, fallback, ref, alt, className, width, height, style, onClick, onMouseEnter, onMouseLeave }: Props) => {
  return (
    <picture>
      <source
        srcSet={src}
        type={getType(src)}
      />
      <img
        src={fallback}
        className={className}
        ref={ref}
        style={style}
        width={width}
        height={height}
        alt={alt}
        onClick={onClick}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        // onLoad={(e) => {
        //   console.log('Loaded image source:', (e.target as HTMLImageElement).currentSrc)
        // }}
      />
    </picture>
  )
}

const getType = (url: string): SupportedImageType => {
  const extension = url?.split('.').pop()?.toLowerCase() || ''
  switch (extension) {
    case 'webp':
      return 'image/webp'
    case 'avif':
      return 'image/avif'
    case 'jpg':
    case 'jpeg':
      return 'image/jpeg'
    case 'png':
      return 'image/png'
    default:
      return 'image/webp'
  }
}

export default Image
