import { Payload, TableRoundPayload } from '@ikigaians/house'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { BetState } from '@repo/utils/types/betstate'
import { BET_SAFETY_MARGIN_MS } from './constants'
import { RoundInfo, createEmptyRoundInfo } from './types'

// Helper functions
const getActiveRound = (payload: Payload): TableRoundPayload | undefined => {
  return payload.activeRound
}

// Round state updates
export const updateRoundInfo = (payload: Payload): RoundInfo => {
  const table = payload.table
  if (!table || !payload || !table.gameType) {
    return createEmptyRoundInfo()
  }

  const round = getActiveRound(payload)
  const roundInfo: RoundInfo = {
    gameCode: table.gameCode ?? '',
    gameType: table.gameType,
    roundId: round?.roundId ?? '',
    betResult: false,
    dealer: table.dealer ?? { name: '' },
  }

  useConfigStore.setState({ roundInfo })
  useDebugStore.getState().setMsg(`roundInfo:\n${JSON.stringify(roundInfo, null, 2)}`)

  return roundInfo
}

// Betting state updates
export const updateBettingFromPayload = (payload: Payload) => {
  const round = getActiveRound(payload)
  if (!round) return

  const current = Date.now()
  const { remainBetPeriod, betStopTime } = round

  const deviceBetStopTime = Date.now() + (remainBetPeriod ?? 0)
  const timeDiffWithRgs = (betStopTime ?? 0) - deviceBetStopTime
  console.debug(`Time difference with RGS: ${timeDiffWithRgs}ms`)

  // Reduce remainBetPeriod by safety margin to prevent "Bet Not Allowed" error
  const betEnd = remainBetPeriod ? remainBetPeriod + Date.now() - BET_SAFETY_MARGIN_MS : 0
  const betStart = betEnd - useBettingStore.getState().betPeriod * 1000

  const isBet = current < betEnd
  const dealer = payload.table?.dealer ?? { name: '' }

  useBettingStore.setState({ betStart, betEnd, isBet, dealer })
}

export const updateBetState = (state: BetState) => {
  useBettingStore.setState({ gameState: state })
}

export const updateBetPeriod = (betPeriod: number) => {
  if (typeof betPeriod === 'number' && betPeriod > 0) {
    useBettingStore.setState({ betPeriod })
  }
}

// Cart/Chip tray updates
export const updateChipTray = (payload: Payload) => {
  const { chipTray } = payload
  if (!chipTray || chipTray.length === 0) return

  const sortedChips = [...chipTray].sort((a, b) => a.index - b.index)
  const chipList = sortedChips.map(({ amount, onTable }) => ({ amount, onTable: !!onTable }))
  const foundIdx = sortedChips.findIndex((chip) => chip.defaultSelected)
  const selectedChipIdx = foundIdx !== -1 ? foundIdx : Math.min(3, chipList.length - 1)

  useCartStore.setState({
    chipList,
    selectedChipIdx,
    selectedChipVal: chipList[selectedChipIdx].amount,
  })
}
