import { isMobileDevice } from '@repo/utils/common/common'
import { create } from 'zustand'
import { createJSONStorage, devtools, type DevtoolsOptions, persist } from 'zustand/middleware'

export const volumeKeys = ['masterVolume', 'studioVolume', 'gameVolume', 'dealerVolume'] as const
export type VolumeKeys = (typeof volumeKeys)[number]
type prevVolumeKeys = 'prevMasterVolume' | 'prevStudioVolume' | 'prevGameVolume' | 'prevDealerVolume'

// 0: Low(lo), 1: Medium(me), 2: High(hi), 3: HD(hd), 'AUTO'
const videoQualities = [0, 1, 2, 3, 'AUTO'] as const
export type VideoQuality = (typeof videoQualities)[number]

interface SettingStoreState {
  allVolume: number
  masterVolume: number
  prevMasterVolume: number
  studioVolume: number
  prevStudioVolume: number
  gameVolume: number
  prevGameVolume: number
  dealerVolume: number
  prevDealerVolume: number
  otherMessagesEnable: boolean
  screenName: string
  videoQuality: VideoQuality
  soundEnable: boolean

  // Game specifics
  showAllSideBets: boolean
  bettingStatistics: boolean
  scorecardsMode: 'immersive' | 'classic'
}

interface SettingStoreActions {
  // Game specifics
  toggleShowAllSideBets: () => void
  toggleBettingStatistics: () => void
  selectScorecardsMode: (mode: SettingStoreState['scorecardsMode']) => void

  toggleOtherMessages: () => void
  volumeChange: (volumeKey: VolumeKeys, val: number) => void
  toggleAllVolume: () => void
  toggleVolume: (volumeKey: VolumeKeys) => void
}

const DEVTOOLS_CONFIG: DevtoolsOptions = {
  name: `${document.title} - useSettingStore`,
  store: 'SettingStore',
  serialize: {
    options: true,
  },
}

export const useSettingStore = create<SettingStoreState & SettingStoreActions>()(
  devtools(
    persist(
      (set) => ({
        allVolume: 1,
        masterVolume: 1,
        prevMasterVolume: 1,
        studioVolume: 1,
        prevStudioVolume: 1,
        gameVolume: 1,
        prevGameVolume: 1,
        dealerVolume: 1,
        prevDealerVolume: 1,
        otherMessagesEnable: true,
        screenName: '',
        videoQuality: 'AUTO',
        soundEnable: true,

        // Game specifics
        showAllSideBets: true,
        toggleShowAllSideBets: () => set((state) => ({ showAllSideBets: !state.showAllSideBets })),
        bettingStatistics: true,
        toggleBettingStatistics: () => set((state) => ({ bettingStatistics: !state.bettingStatistics })),
        scorecardsMode: 'classic',
        selectScorecardsMode: (mode) => set(() => ({ scorecardsMode: mode })),

        toggleOtherMessages: () => set((state) => ({ otherMessagesEnable: !state.otherMessagesEnable })),
        volumeChange: (volumeKey: VolumeKeys, val: number) => {
          const prevKey = getPrevVolumeKey(volumeKey)
          const newVolume = {
            [volumeKey]: val,
            [prevKey]: val,
          }
          set(() => newVolume)
        },
        toggleAllVolume: () => {
          set((state) => {
            const newState: Partial<SettingStoreState> = {}
            const checkAllPrev = state.prevGameVolume === 0 && state.prevDealerVolume === 0 && state.prevStudioVolume === 0
            volumeKeys.forEach((key) => {
              const prevKey = getPrevVolumeKey(key)
              if (state.masterVolume !== 0) {
                newState[key] = 0
              } else if (state.prevMasterVolume === 0) {
                newState[key] = checkAllPrev || key === 'masterVolume' ? 0.2 : state[prevKey]
              } else {
                newState[key] = state[prevKey]
              }

              if (state.prevMasterVolume === 0 && checkAllPrev) {
                newState[prevKey] = 0.2
              }
            })
            return newState
          })
        },
        toggleVolume: (volumeKey: VolumeKeys) => {
          set((state) => {
            const newState: Partial<SettingStoreState> = {}
            const prevKey = getPrevVolumeKey(volumeKey)
            newState[volumeKey] = state[volumeKey] === 0 ? (state[prevKey] === 0 ? 0.2 : state[prevKey]) : 0

            if (state[volumeKey] === 0 && state[prevKey] === 0) {
              newState[prevKey] = 0.2
            }
            return newState
          })
        },
      }),
      {
        name: `useSettingStore-${isMobileDevice ? 'mobile' : 'desktop'}`,
        storage: createJSONStorage(() => localStorage),
        onRehydrateStorage: () => (state) => {
          if (!state) return
          // Validate video quality
          if (!videoQualities.includes(state.videoQuality)) state.videoQuality = 'AUTO'
        },
        // Increment this number whenever there is a "breaking" change to the state structure.
        version: 2,
        migrate: (persistedState, version) => {
          if (version < 2) {
            // When Zustand receives "undefined", it will discard the old persisted state and use the new initial state defined in the create function instead.
            return undefined
          }
          return persistedState
        },
      }
    ),
    DEVTOOLS_CONFIG
  )
)

export const getPrevVolumeKey = (volumeKey: VolumeKeys) => {
  if (!volumeKeys.includes(volumeKey)) throw new Error(`Invalid volume key: ${volumeKey}`)

  return `prev${volumeKey.charAt(0).toUpperCase() + volumeKey.slice(1)}` as prevVolumeKeys
}
