import { playSound } from '@repo/utils/common/sound'
import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

const TOAST_DURATION = 5000
const MAX_NORMAL_TOAST_COUNT = 5

export type toastType = 'info' | 'error' | 'success' | 'none'
export type Toast = {
  id: string
  type: toastType
  msg: string
  persistent?: boolean
}
export type ToastOptions = {
  persistent?: boolean
  id?: string
}
type ToastStore = {
  toastQueue: Toast[]
  toastTimeouts: Map<string, ReturnType<typeof setTimeout>>

  /**
   * Msg needs to be the key of the message in the locales file for dynamic i18n.
   * Or pure string if i18n is not needed
   */
  addToast: (type: toastType, msg: string, options?: ToastOptions) => void
  dismissToast: (id: string) => void
}

const DEVTOOLS_CONFIG = {
  name: `${document.title} - useToastStore`,
  store: 'ToastStore',
  serialize: {
    options: true,
  },
}

export const useToastStore = create<ToastStore>()(
  devtools(
    (set) => ({
      toastQueue: [],
      toastTimeouts: new Map(),

      addToast: (type, msg, options) => {
        const id = options?.id ?? `toast-${Date.now()}-${Math.random().toString(36).substring(2, 9)}`
        const persistent = options?.persistent ?? false
        const newToast: Toast = { id, type, msg, persistent }

        if (type === 'error') {
          playSound(['errorToast'])
        } else {
          playSound(['normalToast'])
        }

        set((state) => {
          const all = [...state.toastQueue, newToast]
          const persistentToasts: Toast[] = []
          const normalToasts: Toast[] = []

          for (const toast of all) {
            if (toast.persistent) persistentToasts.push(toast)
            else normalToasts.push(toast)
          }

          const updated = [...persistentToasts, ...normalToasts.slice(-MAX_NORMAL_TOAST_COUNT)]

          if (persistent) return { toastQueue: updated }

          const timeoutId = setTimeout(() => useToastStore.getState().dismissToast(id), TOAST_DURATION)

          const newTimeouts = new Map(state.toastTimeouts)
          newTimeouts.set(id, timeoutId)

          return {
            toastQueue: updated,
            toastTimeouts: newTimeouts,
          }
        })
      },

      dismissToast: (id) => {
        set((state) => {
          const timeoutId = state.toastTimeouts.get(id)

          const newTimeouts = new Map(state.toastTimeouts)

          if (timeoutId) {
            clearTimeout(timeoutId)
            newTimeouts.delete(id)
          }

          const newQueue = state.toastQueue.filter((toast) => toast.id !== id)

          return {
            toastQueue: newQueue,
            toastTimeouts: newTimeouts,
          }
        })
      },
    }),
    DEVTOOLS_CONFIG
  )
)
